import React, { Component } from 'react'
import { Button, message } from 'antd';
import './index.less'
import BreadcrumbLink from '../../../components/bread'
import Table from './table-list';
import MenuModal from './menu-modal';

class MenuManage extends Component {
    apis = window.apis;
    constructor(props) {
        super(props)
        this.state = {
            breadcrumbData: [
                { name: '系统管理' },
                { name: '菜单管理' }
            ],
            loading: false,   // 列表加载中
            dataList: [],     // 部门列表数据
            type: 0,          // 弹框类型 0:新增，1:修改
            menuDetail: {},    // 点击选中的role的行内容
            setMenuModalShow: false,    // 新增，修改职位弹框的显隐 
        }
    }
    componentDidMount() {
        this.queryTableList();
    }
    // 数据循环
    changeData = (data) => {
        data.map(item => {
            if (item.list && item.list.length) {
                const val= item['list'];
                delete item['list'];
                item['children']=val;
                this.changeData(item.children);
            }
            return {...item}
        })
        return data
    }
    // 查询试卷列表
    queryTableList = () =>{
        this.setState({
            loading: true,
        },() => {
            this.apis.listQMSMenus({parentId: 0},(res) => {
                if(res.code === '200') {
                    const list = this.changeData(res.data);
                    this.setState({
                        dataList: list,
                        loading: false,
                    })
                }else{
                    this.setState({
                        loading: false,
                    })
                }
            })
        })
    }
    //  删除
    handleDelete = (bizId) => {
        this.apis.deleteMenu({bizId},(res) => {
            if(res.code === '200') {
                message.success('删除成功！');
                this.queryTableList();
            }
        })
    }
    // 编辑弹框
    handleEdit = (item) => {
        this.setState({
            setMenuModalShow:true,
            type: 1,
            menuDetail: item
        })
    }
    // 新增弹框
    handleMenuModal = () => {
        this.setState({
            setMenuModalShow:true,
            type: 0,
            menuDetail: {}
        })
    }
    // 关闭弹框
    handleCancel = () => {
        this.setState({
            setMenuModalShow:false,
        },() => {
            this.refs.MenuModal.resetFields();
        })
    }
    // 弹框确定事件
    handleaddsetMeal = (values) => {
        const params = {
            ...values
        }
        delete params['type'];
        if (values.type === 0) { // 新增
            this.apis.addMenu(params,(res) => {
                if(res.code === '200') {
                    message.success('新增成功！');
                    this.handleCancel();
                    this.queryTableList();
                }
            })
        }else{ // 修改
            params['bizId'] = this.state.menuDetail.bizId;
            this.apis.updateMenu(params,(res) => {
                if(res.code === '200') {
                    message.success('修改成功！');
                    this.handleCancel();
                    this.queryTableList();
                }
            })
        }
    }
    render() {
        const { breadcrumbData, loading, dataList, setMenuModalShow, type, menuDetail } = this.state;
        return (
            <div className="menuManage">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="btn">
                        <Button type="primary" onClick={this.handleMenuModal}>新增</Button>
                    </div>
                    <div className="table-list">
                        <Table 
                            loading={loading} 
                            data={dataList}
                            childrenColumnName='list'
                            handleDelete={this.handleDelete}
                            handleEdit={this.handleEdit}
                        />
                    </div>
                </div>
                <MenuModal
                    ref="MenuModal"
                    setMenuModalShow={setMenuModalShow}
                    type={type}
                    menuDetail={menuDetail}
                    menuData={dataList}
                    handleCancel={this.handleCancel}
                    handleaddsetMeal={this.handleaddsetMeal}
                />
            </div>
        )
    }
}
export default MenuManage