import React, { Component } from 'react'
import { Form, Input, Modal, Select, TreeSelect, Tree } from 'antd'
import './index.less'
import Api from '../../../api/index';
const { Option } = Select;
const { TextArea } = Input;
const { SHOW_PARENT } = TreeSelect;
const { TreeNode } = Tree

const App = Form.create({ name: 'form_in_modal' })(
    class extends Component {
        constructor(props) {
            super(props)
            this.state = {
                expandedKeys: ['0-0-0', '0-0-1'],
                autoExpandParent: true,
                selectedKeys: [],
            }
        }
        componentDidMount() {
            this.listQMSDept()//部门
        }

        //部门
        listQMSDept = () => {
            Api.listQMSDept({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listQMSDeptData: res.data
                    })
                }
            })
        }
        onExpand = (expandedKeys) => {
            console.log('onExpand', expandedKeys);
            this.setState({
                expandedKeys,
                autoExpandParent: false,
            });
        };

        // onCheck = (checkedKeys) => {
        //     console.log('onCheck', checkedKeys);
        //     this.setState({ 
        //         checkedKeys,
        //         deptIdListData: checkedKeys,
        //      });
        // };

        renderTreeNodes = (listQMSDeptData) =>
            listQMSDeptData ? listQMSDeptData.map(item => {
                if (item.list) {
                    return (
                        <TreeNode title={item.deptName} key={item.bizId} dataRef={item}>
                            {this.renderTreeNodes(item.list)}
                        </TreeNode>
                    );
                }
                return <TreeNode key={item.bizId} {...item} />;
            }) : null;

        //部门授权 点击确定
        // departmentalNo = () => {
        //     Api.addFileInDeptRole({
        //         fileId: this.props.depatId,
        //         deptIdList: deptIdListData,
        //     }, (res) => {
        //         this.setState({
        //             departmentalVisible: false,
        //         })
        //     })
        // }
        render() {
            const {
                visible, onCancel, onCreate, form, depatId, onCheck, onSelect,
                checkedKeys
            } = this.props;
            const {
                listQMSDeptData
            } = this.state;
            const { getFieldDecorator } = form;
            const formItemLayout = {
                labelCol: { span: 6 },
                wrapperCol: { span: 18 },
            };

            // const tProps = {
            //     treeData,
            //     value: this.state.value,
            //     onChange: this.onChange,
            //     treeCheckable: true,
            //     showCheckedStrategy: SHOW_PARENT,
            //     searchPlaceholder: 'Please select',
            //     style: {
            //         width: 300,
            //     },
            // };
            return (
                <Modal
                    style={{ width: '500px' }}
                    visible={visible}
                    title={'部门授权'}
                    okText="确定"
                    cancelText='取消'
                    onCancel={onCancel}
                    onOk={onCreate}
                    maskClosable={false}
                >
                    <Form style={{ width: "450px", overflowX: "auto", height: '450px', overflowY: "auto" }}>
                        <Tree
                            checkable
                            onExpand={this.onExpand}
                            expandedKeys={this.state.expandedKeys}
                            autoExpandParent={this.state.autoExpandParent}
                            onCheck={onCheck}
                            checkedKeys={this.props.checkedKeys}
                            onSelect={onSelect}
                            selectedKeys={this.state.selectedKeys}
                        >
                            {this.renderTreeNodes(listQMSDeptData)}
                        </Tree>
                    </Form>
                </Modal>
            );
        }
    }
);
export default App