import React,{Component} from 'react';
import './index.less'
import { Modal, Form, Input, Checkbox, TreeSelect, Select, Row, Col, Upload, Icon, message } from "antd";
const FormItem = Form.Item;
const { Option } = Select;
const { TreeNode } = TreeSelect;

function beforeUpload(file) {
    const isJpgOrPng = (file.type === 'image/jpeg' && file.name.indexOf('.jpg') > -1 ) || file.type === 'image/png';
    if (!isJpgOrPng) {
        message.error('请上传jpg和png格式的图片!');
    }
    const isLt2M = file.size / 1024 / 1024 < 2;
    if (!isLt2M) {
        message.error('请上传小于2MB的图片!');
    }
    return isJpgOrPng && isLt2M;
}
  
class setMenuModal extends Component{
    constructor(props) {
        super(props)
        this.state={
            loading:false,
            imageUrl: ''
        }
        this.deptData = [];
    }
    componentWillReceiveProps(props) {
        if(props.menuDetail.img && props.menuDetail.img !== this.props.menuDetail.img) {
            this.setState({
                imageUrl: props.menuDetail.img
            })
        }
    }
    // 点击确定
    handleaddsetMeal = (e) => {
        e.stopPropagation();
        const { form, type } = this.props;
        form.validateFields((err, values) => {
            if (!err) {
                values.img = this.state.imageUrl;
                values.type = type;
                this.props.handleaddsetMeal(values)
            }
        })
    }
    // 点击取消
    handleCancel = () => {
        this.props.handleCancel();
    }
    // 渲染树结构
    renderTreeNodes = (data) => {
        const treeItem = data.map(item => {
            if (item.children && item.children.length) {
                return <TreeNode value={item.bizId} title={item.fullName} key={item.bizId} dataRef={item}>
                    {this.renderTreeNodes(item.children)}
                </TreeNode>
            }
            return <TreeNode value={item.bizId} title={item.fullName} key={item.bizId} />;
        })
        return treeItem
    }
    handleChange = info => {
        if (info.file.status === 'uploading') {
            this.setState({ loading: true });
            return;
        }
        if (info.file.status === 'done') {
            const data = info.file.response.data[0];
            const img = Object.keys(data).map(item => {
                return data[item]
            })
            this.setState({
                imageUrl: img[0],
                loading: false
            })
        }else if (info.file.status === 'error') {
            message.error(`${info.file.name} 上传失败`);
        }
    };
    handleCancelIcon = (e) => {
        e.stopPropagation();
        this.setState({
            imageUrl: ''
        })
    }
    render(){
        const { setMenuModalShow, form, type, menuDetail, menuData } = this.props;
        const { getFieldDecorator } = form;
        const uploadButton = (
            <div>
                <Icon type={this.state.loading ? 'loading' : 'plus'} />
                <div className="ant-upload-text">Upload</div>
            </div>
        );
        const priceWrap = (
            <div className="priceWrap">
                <img src={this.state.imageUrl} alt="avatar" style={{ width: '100%' }} />
                <Icon className="close" onClick={this.handleCancelIcon.bind(this)} type="close-circle" />
            </div>
        )
        return(
            <Modal
                className="setMenuModal"
                title={type === 0 ? '新增菜单' : '修改菜单'}
                visible={setMenuModalShow}
                width={500}
                onCancel={this.handleCancel}
                onOk={this.handleaddsetMeal}
            >
                <div className="setRole-box">
                    <Form labelCol={{ span: 5 }} wrapperCol={{ span: 17 }}>
                        <FormItem 
                            label="模块名称">
                            {getFieldDecorator('fullName', {
                                initialValue: menuDetail.fullName,
                                rules: [
                                    { required: true, message: '请输入模块名称' },
                                ],
                            })(
                                <Input placeholder="请输入" />
                            )}
                        </FormItem>
                        <FormItem 
                            label="模块编码">
                            {getFieldDecorator('code', {
                                initialValue: menuDetail.code,
                                rules: [
                                    { required: true, message: '请输入模块编码' },
                                ],
                            })(
                                <Input placeholder="请输入" />
                            )}
                        </FormItem>
                        <FormItem 
                            label="上级模块">
                            {getFieldDecorator('parentId', {
                                initialValue: menuDetail.parentId,
                                rules: [
                                    { required: true, message: '请选择上级模块' },
                                ],
                            })(
                                <TreeSelect placeholder="请选择" allowClear treeDefaultExpandAll onChange={this.onChange}>
                                    <TreeNode value="0" title="父级" />
                                    {this.renderTreeNodes(menuData)}
                                </TreeSelect>
                            )}
                        </FormItem>
                        <FormItem 
                            label="菜单图标"
                            style={{marginBottom: 0}}>
                            {getFieldDecorator('img', {
                                initialValue: menuDetail.img
                            })(
                                <Upload
                                    name="file"
                                    listType="picture-card"
                                    className="avatar-uploader"
                                    showUploadList={false}
                                    action="/file/uploadFiles"
                                    multiple={false}
                                    beforeUpload={beforeUpload}
                                    onChange={this.handleChange}
                                >
                                    {this.state.imageUrl ? priceWrap : uploadButton}
                                </Upload>
                            )}
                        </FormItem>
                        <FormItem label="连接目标">
                            {getFieldDecorator('target', {
                                initialValue: menuDetail.target ? menuDetail.target : 'click'
                            })(
                                <Select placeholder="请选择">
                                    <Option value="click">click</Option>
                                    <Option value="lucy">iframe</Option>
                                    <Option value="Open">Open</Option>
                                    <Option value="href">href</Option>
                                </Select>
                            )}
                        </FormItem>
                        <FormItem label="Web连接地址">
                            {getFieldDecorator('navigateUrl', {
                                initialValue: menuDetail.navigateUrl
                            })(
                                <Input placeholder="请输入" />
                            )}
                        </FormItem>
                        <FormItem label="选项" style={{marginBottom: 0}}>
                            <Row gutter={24}>
                                <Col span={6}>
                                    <FormItem>
                                        {getFieldDecorator('enabled', {
                                            valuePropName: 'checked',
                                            initialValue: menuDetail['enabled'] ? true : false
                                        })(
                                            <Checkbox>有效</Checkbox>
                                        )}
                                    </FormItem>
                                </Col>
                                <Col span={6}>
                                    <FormItem>
                                        {getFieldDecorator('isUnfold', {
                                            valuePropName: 'checked',
                                            initialValue: menuDetail['isUnfold'] ? true : false
                                        })(
                                            <Checkbox>折叠</Checkbox>
                                        )}
                                    </FormItem>
                                </Col>
                                <Col span={6}>
                                    <FormItem>
                                        {getFieldDecorator('allowEdit', {
                                            valuePropName: 'checked',
                                            initialValue: menuDetail['allowEdit'] ? true : false
                                        })(
                                            <Checkbox>编辑</Checkbox>
                                        )}
                                    </FormItem>
                                </Col>
                                <Col span={6}>
                                    <FormItem>
                                        {getFieldDecorator('allowDelete', {
                                            valuePropName: 'checked',
                                            initialValue: menuDetail['allowDelete'] ? true : false
                                        })(
                                            <Checkbox>删除</Checkbox>
                                        )}
                                    </FormItem>
                                </Col>
                            </Row>
                        </FormItem>
                        <FormItem 
                            label="排序">
                            {getFieldDecorator('sortCode', {
                                initialValue: menuDetail.sortCode,
                                rules: [
                                    { required: true, message: '请输入排序' },
                                    { pattern: /^\d{1,10}$/, message: '请输入一至十位数字' }
                                ],
                            })(
                                <Input placeholder="请输入" />
                            )}
                        </FormItem>
                    </Form>
                </div>
            </Modal>
        )
    }
}
export default Form.create()(setMenuModal)