import React, { Component } from 'react'
import { Button, Popconfirm, message, Input, Form, Row, Col, DatePicker, Table } from 'antd';
import './index.less'
import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread';
// import SearchForm from "./search-form"; // 搜索条件
// import Table from "./table-list"; // 搜索条件
const FormItem = Form.Item
const { RangePicker } = DatePicker

class onBanner extends Component {
    constructor(props) {
        // apis = window.apis;
        super(props)
        this.state = {
            breadcrumbData: [
                { name: '考试管理' },
                { name: '试卷管理' }
            ],
            dataList: [],     // 试卷列表数据
            loading: false,   // 列表是否正在加载中
            total: 0,         // 角色总数
            dataList: [],     // 列表data
            pageSize: 10,     // 分页Size
            page: 1,          // 分页页码 
            columns: [],
        }
    }
    componentDidMount() {
        this.setState({
            columns: [
                {
                    title: '试卷名称',
                    dataIndex: 'examName',
                    key: 'examName',
                    render: (text, record, index) => {
                        if (record.examName) {
                            return (
                                <div className='ellipsis'>{record.examName}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '总分',
                    dataIndex: 'totalPoints',
                    key: 'totalPoints',
                    render: (text, record, index) => {
                        if (record.totalPoints) {
                            return (
                                <div className='ellipsis'>{record.totalPoints}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },

                }, {
                    title: '编制者',
                    dataIndex: 'createUserName',
                    key: 'createUserName',
                    render: (text, record, index) => {
                        if (record.createUserName) {
                            return (
                                <div className='ellipsis'>{record.createUserName}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },

                }, {
                    title: '编制日期',
                    dataIndex: 'createDate',
                    key: 'createDate',
                    render: (text, record, index) => {
                        if (record.createDate) {
                            return (
                                <div className='ellipsis'>{record.createDate}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },

                }, {
                    title: '状态',
                    dataIndex: 'isUse',
                    key: 'isUse',
                    render: (text, record, index) => {
                        if (record.isUse === false) {
                            return (
                                <div className='ellipsis'>未使用</div>
                            )
                        } if (record.isUse === true) {
                            return (
                                <div className='ellipsis'>已使用</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '操作',
                    dataIndex: 'operation',
                    width: 200,
                    render: (text, record) =>
                        <div>
                            <Popconfirm title="确定要删除该数据吗" onConfirm={this.handleDelete.bind(record)}>
                                <a className="operation">删除</a>
                            </Popconfirm>
                            <a className="operation">编辑</a>
                            <a onClick={this.handlePreview.bind(this, record)} className="operation">预览</a>
                        </div>

                },
            ]

        })
        this.queryTableList();
    }
    //预览
    handlePreview = (record) => {
        this.props.history.push(`/testManagement/testPaper/preview?examId=${record.examId}`)
    }
    //查询
    handleSearchPeople = () => {
        this.setState({
            page: 1,
        }, () => {
            this.props.form.validateFieldsAndScroll((err, values) => {
                if (true) {
                    const { pageSize, page } = this.state;
                    this.setState({
                        deviceLoading: true
                    })
                    Api.listExamByQuery({
                        pageSize,
                        pageNo: page,
                        examName: values.examName == undefined ? values.examName : values.examName.trim(),
                    }, (res) => {
                        if (res.code === '200') {
                            this.setState({
                                page,
                                dataList: res.data.list,
                                total: res.data.total,
                            })
                        }
                    })
                }
            });
        })
    }
    //点击 重置
    handleReset = () => {
        this.props.form.resetFields();
        this.queryTableList()
    }
    // 初始化 试卷列表
    queryTableList() {
        const { pageSize, page } = this.state
        const data = {
            pageSize,
            pageNo: page
        }
        Api.listExamByQuery(data, (res) => {
            if (res.code === "200") {
                this.setState({
                    dataList: res.data.list,
                    total: res.data.total,
                },
                    message.success(res.message)
                )
            } else {
                message.waring(res.message)
            }
        })
    }

    // 切换页码
    handleChangePage = (pageNo, pageSize) => {
        this.queryTableList();
        console.log(pageNo, pageSize, '----pageNo, pageSize---')
    }
    // 删除试卷
    handleDelete = (record) => {
        Api.deleteExam({
            examId: record.examId
        }, (res) => {
            if (res.code === "200") {
                message.success(res.message)
            } else {
                message.waring("删除失败")
            }
        })
    }
    // 新增试卷
    handleAddPaper = () => {
        this.props.history.push('/testManagement/testPaper/addPaper');

    }
    render() {
        const { getFieldDecorator } = this.props.form;
        const { breadcrumbData, dataList, loading, pageNo, pageSize, total, columns } = this.state;
        const formItemLayout = {
            labelCol: { span: 8 },
            wrapperCol: { span: 16 },
        };

        return (
            <div className="textPaper">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    {/* 搜索条件 */}
                    <div className="p-search">
                        <Form style={{ position: 'relative', top: -3 }}>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="试卷名称"
                                    >
                                        {getFieldDecorator('fileNo', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="编制时间"
                                    >
                                        {getFieldDecorator('applyDeptId', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Button onClick={this.handleReset} style={{ float: 'right' }}>重置</Button>
                                    <Button type="primary" htmlType="submit" onClick={this.handleSearchPeople} style={{ marginRight: 20, float: 'right' }}>查询</Button>
                                </Col>
                            </Row>
                        </Form>
                    </div>
                    <div className="addBtn">
                        <Button type="primary" onClick={this.handleAddPaper}>新增</Button>
                    </div>
                    <div className="table-list">
                        <Table
                            columns={columns}
                            dataSource={dataList}
                            key="bizId"
                        ></Table>
                    </div>

                </div>
            </div>
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner