import React, { Component } from 'react'
import './index.less'
import { Modal, Input, Form, Row, Col, Checkbox,Select, Transfer } from 'antd'


const FormItem = Form.Item;
const { TextArea } = Input;


class detailModal extends Component {
    constructor(props) {
        super(props)
        this.state = {
            rightData: [],
            leftData: [],
            selectedKeys: [],
        }
    }
    componentDidMount() {

    }
    handleOk = (e) => {
        e.preventDefault();
        const { form, type } = this.props;
        form.validateFields((err, values) => {
            if (!err) {
                values.type = type;
                console.log(values)
                this.props.handleaddsetMeal(values)
            }
        })
    }
    // 点击取消
    handleCancel = () => {
        this.props.handleCancel();
    }
    render() {
        const { type, showModal, QMDetail } = this.props;
        const { getFieldDecorator } = this.props.form;
        return (
            <Modal
                className="categoryModal"
                title={type === 0 ? '字典明细-添加' : '字典明细-修改'}
                visible={showModal}
                width={520}
                onOk={this.handleOk}
                onCancel={this.handleCancel}
            >
                <Form labelCol={{ span: 5 }} wrapperCol={{ span: 16 }}>
                    <FormItem label="代码">
                        {getFieldDecorator('itemCode', {
                            initialValue: QMDetail.itemCode,
                            rules: [
                                { required: true, message: '请输入代码' },
                            ],
                        })(
                            <Input placeholder="请输入" />
                        )}
                    </FormItem>
                    <FormItem label="名称">
                        {getFieldDecorator('itemName', {
                            initialValue: QMDetail.itemName,
                            rules: [
                                { required: true, message: '请输入名称' },
                            ],
                        })(
                            <Input placeholder="请输入名称" />
                        )}
                    </FormItem>
                    <FormItem label="选项" style={{marginBottom: 0}}>
                        <Row gutter={24}>
                            <Col span={6}>
                                <FormItem>
                                    {getFieldDecorator('enabled', {
                                        valuePropName: 'checked',
                                        initialValue: QMDetail['enabled'] ? true : false
                                    })(
                                        <Checkbox>有效</Checkbox>
                                    )}
                                </FormItem>
                            </Col>
                            <Col span={9}>
                                <FormItem>
                                    {getFieldDecorator('allowEdit', {
                                        valuePropName: 'checked',
                                        initialValue: QMDetail['allowEdit'] ? true : false
                                    })(
                                        <Checkbox>允许编辑</Checkbox>
                                    )}
                                </FormItem>
                            </Col>
                            <Col span={9}>
                                <FormItem>
                                    {getFieldDecorator('allowDelete', {
                                        valuePropName: 'checked',
                                        initialValue: QMDetail['allowDelete'] ? true : false
                                    })(
                                        <Checkbox>允许删除</Checkbox>
                                    )}
                                </FormItem>
                            </Col>
                        </Row>
                    </FormItem>
                    <FormItem label="描述">
                        {getFieldDecorator('description', {
                            initialValue: QMDetail.description,
                        })(
                            <TextArea placeholder="请输入" />
                        )}
                    </FormItem>
                </Form>
            </Modal>
        )
    }
}
export default Form.create()(detailModal)