import React, { Component } from 'react'
import { Form, Input, Table, Row, Col, Button, DatePicker } from 'antd';
import {
    HashRouter as Router, Route, Switch, Link, withRouter,
} from 'react-router-dom';
import './index.less'
import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread'
import FromReportForms from '../../../components/echart';
import api from '../../../api';

const Search = Input.Search;
const { RangePicker } = DatePicker;
class onBanner extends Component {
    constructor(props) {
        super(props)
        this.state = {
            planData: [],
            pageSize: 10,
            page: 1,
            selectTime: [],
            breadcrumbData: [
                { name: '培训管理' },
                { name: '培训及时率计算' }
            ],
        }
    }
    componentDidMount() {
        this.setState({
            checkColumns: [{
                title: '部门名称',
                key: 'deptName',
                width: '200px',
                render: (text, record, index) => {
                    if (record.deptName) {
                        return (
                            <div className='ellipsis'>{record.deptName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '参加人数',
                key: 'trainTotal',
                render: (text, record, index) => {
                    if (record.trainTotal) {
                        return (
                            <div className='ellipsis'>{record.trainTotal}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '及时人数',
                key: 'timelyTotal',
                render: (text, record, index) => {
                    if (record.timelyTotal) {
                        return (
                            <div className='ellipsis'>{record.timelyTotal}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '及时率',
                key: 'responsibleUserName',
                render: (text, record, index) => {
                    if (record.responsibleUserName) {
                        return (
                            <div className='ellipsis'>{record.timelyTotal}/{record.trainTotal}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }
            ]
        }, () => {
            this.listTrainTimely()
        }
        )
    }
    //初始化 列表 数据
    listTrainTimely() {
        const { pageSize, page } = this.state
        api.listTrainTimely({
            pageSize,
            pageNo: page,
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    total: res.data.total,
                    planData: res.data.list
                })
            }
        })
    }
    //查询
    handleSearchPeople = () => {
        this.setState({
            page: 1,
        }, () => {
            this.props.form.validateFieldsAndScroll((err, values) => {
                if (true) {
                    const { pageSize, page, selectTime } = this.state;
                    this.setState({
                        deviceLoading: true
                    })
                    let startTime = selectTime.length > 0 ? selectTime[0] : null;
                    let endTime = selectTime.length > 0 ? selectTime[1] : null;
                    Api.listTrainTimely({
                        pageSize,
                        pageNo: page,
                        beginTime: startTime,
                        finishTime: endTime,

                    }, (res) => {
                        if (res.code === '200') {
                            this.setState({
                                page,
                                total: res.data.total,
                                planData: res.data.list
                            })
                        }
                    })
                }
            });
        })
    }
    //点击 重置
    handleReset = () => {
        this.props.form.resetFields();
        this.listTrainTimely()
    }
    //选择时间后的回调
    onTimeChange = (e, dateString) => {
        this.setState({
            selectTime: dateString
        })
    }
    render() {
        const formItemLayout = {
            labelCol: { span: 8 },
            wrapperCol: { span: 16 },
        };
        const { getFieldDecorator } = this.props.form;
        const {
            breadcrumbData,
            checkColumns,
            planData
        } = this.state;
        console.log(planData, 6 / 2, '222222222')
        return (
            <div className="feedBcak">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="p-search">
                        <Form style={{ position: 'relative', top: -3, }}>
                            <Row>
                                <Col span={6} style={{ position: 'relative', }}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="创建日期"
                                    >
                                        {
                                            getFieldDecorator('startTimeBegin', {
                                            })(
                                                <RangePicker
                                                    format="YYYY-MM-DD"
                                                    onChange={this.onTimeChange}
                                                />
                                            )}
                                    </Form.Item>
                                </Col>
                                <Col span={6} style={{ marginTop: '3px', position: 'relative', right: '20px' }}>
                                    <Button onClick={this.handleReset} style={{ float: 'right' }}>重置</Button>
                                    <Button type="primary" htmlType="submit" onClick={this.handleSearchPeople} style={{ marginRight: 20, float: 'right' }}>查询</Button>
                                </Col>
                            </Row>
                        </Form>
                    </div>
                    <div style={{ marginTop: '30px' }}>
                        <FromReportForms
                            planData={planData}
                        ></FromReportForms>
                    </div>
                    <div>
                        <Table
                            style={{ marginTop: '20px' }}
                            columns={checkColumns}
                            dataSource={planData}
                            // pagination={pagination}
                            key="bizId"
                        ></Table>
                    </div>
                </div>
            </div>
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner