import React, { Component } from 'react'
import { Form, Input, Modal, Select, Tabs, Table, Row, Col, Button } from 'antd'
import './index.less'
import Api from '../../../api/index';
const { Option } = Select;
const { TextArea } = Input;
const { TabPane } = Tabs

const App = Form.create({ name: 'form_in_modal' })(
    class extends Component {
        constructor(props) {
            super(props)
            this.state = {
                selectedRowKeys: [],
                selectedRowKeysOn: [],
                checkColumns: [],
            }
        }
        componentDidMount() {
        }
        render() {
            const {
                visible, onCancel, onCreate, form, labelList, isEdit, reportForm
            } = this.props;
            const {
            } = this.state;
            const { getFieldDecorator } = form;
            const formItemLayout = {
                labelCol: { span: 6 },
                wrapperCol: { span: 18 },
            };

            console.log(reportForm, "111111111")
            const reportFormData = reportForm && Number(reportForm.length) > 0 ? reportForm.listFile.map(res => {
                return (
                    <Row style={{ position: 'relative', left: "10px" }}>
                        <Col span={6}>{res.fileNo}</Col>
                        <Col span={6}>{res.fileName}</Col>
                        <Col span={6}>{res.fileTypeIdName}</Col>
                    </Row>
                )
            }) : <Row style={{ position: 'relative', left: "10px" }}>
                    <Col style={{ position: "relative", left: "80px"}} span={6}>-</Col>
                    <Col style={{ position: "relative", left: "80px"}} span={6}>-</Col>
                    <Col style={{ position: "relative", left: "80px"}} span={6}>-</Col>
                </Row>

            return (
                <Modal
                    visible={visible}
                    title={"培训信息-报表"}
                    okText="确定"
                    cancelText='取消'
                    onCancel={onCancel}
                    onOk={onCreate}
                    maskClosable={false}
                >
                    <Form>
                        <div style={{ marginLeft: '15px' }}><span>培训类别:</span><span style={{ marginLeft: "10px" }}>{reportForm.trainTypeName}</span></div>
                        <div style={{ marginLeft: '15px', marginTop: '15px' }}><span>培训名称:</span><span style={{ marginLeft: "10px" }}>{reportForm.trainName}</span></div>
                        <div style={{ marginLeft: '15px', marginTop: '15px' }}><span>培训内容:</span><span style={{ marginLeft: "10px" }}>{reportForm.trainDescription}</span></div>
                        <div style={{ marginLeft: '15px', marginTop: '15px' }}><span>开始日期:</span><span style={{ marginLeft: "10px" }}>{reportForm.startDate}</span></div>
                        <div style={{ marginLeft: '15px', marginTop: '15px' }}><span>结束日期:</span><span style={{ marginLeft: "10px" }}>{reportForm.endDate}</span></div>
                        <div style={{ marginLeft: '15px', marginTop: '15px' }}>
                            <span style={{ float: 'left' }}>使用文件:</span>
                            <Row style={{ position: 'relative', left: "10px" }}>
                                <Col span={6}>文件编号</Col>
                                <Col span={6}>文件名称</Col>
                                <Col span={6}>文件类型</Col>
                            </Row>
                            {reportFormData}
                        </div>
                        <div style={{ marginLeft: '15px', marginTop: '15px' }}><span>试卷名称:</span><span style={{ marginLeft: "10px" }}>{reportForm.examName}</span></div>
                        <div style={{ marginLeft: '15px', marginTop: '15px' }}><span>培训人员:</span><span style={{ marginLeft: "10px" }}>{reportForm.trainUserNames}</span></div>
                    </Form>
                </Modal>
            );
        }
    }
);
export default App