/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { Component } from 'react';
import { Table, Popconfirm, Checkbox } from 'antd';

class tableList extends Component {
    constructor(props) {
        super(props)
        this.columns = [
            {
                title: '登录账号',
                dataIndex: 'account'
            },{
                title: '姓名',
                dataIndex: 'dspName'
            },{
                title: '默认部门',
                dataIndex: 'deptName'
            },{
                title: '默认角色',
                dataIndex: 'roleName'
            },{
                title: '启用',
                dataIndex: 'allowEnable',
                render: (text, record) => 
                <Checkbox checked={text} onChange={this.onChange}></Checkbox>,
            },{
                title: '手机',
                dataIndex: 'mobile'
            },{
                title: 'EMAIL',
                dataIndex: 'email'
            },{
                title: '微信号',
                dataIndex: 'wx'
            },{
                title: 'QQ',
                dataIndex: 'qq'
            },{
                title: '登陆次数',
                dataIndex: 'logOnCount'
            },{
                title: '最后登陆时间',
                dataIndex: 'lastVisit'
            },{
                title: '操作',
                dataIndex: 'bizId',
                width: 400,
                render: (text, record) =>
                    <div>
                        <a className="operation" onClick={this.handleEdit.bind(this,record)}>编辑</a>
                        <Popconfirm title="确定要删除该数据吗" onConfirm={this.handleDelete.bind(this,text)}>
                            <a className="operation">删除</a>
                        </Popconfirm>
                        <a className="operation" onClick={this.handleClickRole.bind(this,record)}>角色授权</a>
                        <a className="operation">职位授权</a>
                        <a className="operation" onClick={this.handleDept.bind(this,record)}>部门设置</a>
                        <a className="operation" onClick={this.handleClickPassword.bind(this,record)}>重置密码</a>
                    </div>
                }]
    }
    // 点击修改
    handleEdit = (record) => {
        this.props.handleEdit(record)
    }
    // 点击角色授权
    handleClickRole = (record) => {
        this.props.handleClickRole(record)
    }
    handleChangePage = (pageNo, pageSize) => {
        this.props.handleChangePage(pageNo, pageSize);
    }
    // 删除
    handleDelete = (code) => {
        this.props.handleDelete(code);
    }
    // 重置密码
    handleClickPassword = (record) => {
        this.props.handleClickPassword(record)
    }
    onShowSizeChange = (pageNo, pageSize) => {
        this.props.handleChangePage(1, pageSize);
    }
    // 部门设置
    handleDept = (record) => {
        this.props.handleDept(record);
    }
    render() {
        return (
            <Table
                rowKey={(record) => { return record.bizId }}
                loading={this.props.loading}
                columns={this.columns} 
                dataSource={this.props.data}
                scroll={{x:2200}}
                pagination={{
                    hideOnSinglePage: true,
                    showSizeChanger: true,
                    showQuickJumper: true,
                    current: this.props.pageNo,
                    total: this.props.total,
                    pageSize: this.props.pageSize,
                    onChange: this.handleChangePage,
                    onShowSizeChange: this.onShowSizeChange,
                }}
          />
        )
    }
}
export default tableList