import React, { Component } from 'react'
import { Tree } from 'antd';
import './index.less'
const { TreeNode } = Tree;

class leftTree extends Component {
    onSelect = (selectedKeys) => {
        console.log(selectedKeys,'----')
        this.props.handleTreeUpdate(selectedKeys)
    }
    render() {
      const { tableNameList, dataBaseName, selectedKeys  } = this.props;
        return (
            <Tree
                showIcon
                defaultExpandAll
                defaultSelectedKeys={selectedKeys}
                onSelect={this.onSelect}
            >
                <TreeNode icon={<img src={require('../../../assets/images/SQL.png')} alt="SQL" />} title={dataBaseName} key={dataBaseName}>
                    {
                        tableNameList.map((item) => {
                            return <TreeNode
                                        icon={<img src={require('../../../assets/images/table.png')} alt="SQL" />}
                                        title={item}
                                        key={item}
                                    />
                        })
                    }
                </TreeNode>
            </Tree>
        )
    }
}
export default leftTree