import React, { Component } from 'react'
import { Form, Input, Modal, Select, Table, Tabs, Row, Col, message, Upload, Transfer } from 'antd'
import './index.less'
import Api from '../../../api/index';
const { Option } = Select;
const { TextArea } = Input;
const { TabPane } = Tabs

const App = Form.create({ name: 'form_in_modal' })(
    class extends Component {
        constructor(props) {
            super(props)
            this.state = {
                fileList: [],
                rightData: [],
                leftData: [],
                selectedKeys: [],
            }
        }
        componentDidMount() {
        }

        callback = (key) => {

        }
        // 文件上传
        handleChange = (info) => {
            //把fileList拿出来
            let { fileList } = info;
            const status = info.file.status;
            if (status) {
                if (status === 'done') {
                    fileList = fileList.slice(-1);
                    if (fileList[0].response.code === '200') {
                        this.setState({
                            current: 1
                        }, () => {
                            this.setState({
                                leadingVisible: true,
                                dataFileList: fileList[0].response.data
                            })
                            this.getManagementTable();
                        })
                        // message.success(fileList[0].response.message);
                    } else if (fileList[0].response.code === '500') {
                        this.setState({
                            current: 1
                        }, () => {
                            this.setState({
                                leadingVisible: true,
                                dataFileList: fileList[0].response.data
                            })
                            this.getManagementTable();
                        })
                        // message.error(fileList[0].response.message)
                    } else if (fileList[0].response.code === '40001') {
                        message.warning(fileList[0].response.message);
                    }
                } else if (status === 'error') {
                    message.error(`${info.file.name} 上传失败`);
                }
                //重新设置state
                this.setState({ fileList });
            }
        }
        //导入
        beforeUpload = (file) => {
            let nameArr = file.name.split('.');
            let name = nameArr[nameArr.length - 1];
            // 校验 格式
            if (['xlsx', 'xls'].indexOf(name) === -1) {
                // message.error('只支持 .xlsx 文件');
                return;
            }
            const isLt = file.size / 1024 / 1024 < 100;
            if (!isLt) {
                message.error('文件过大');
            }
            return isLt;
        }
        handleChange = (nextTargetKeys, direction, moveKeys) => {
            const { rightDataName } = this.props;
            let _obj = {};
            _obj[rightDataName] = nextTargetKeys
            this.props.setData(_obj);

            // console.log('targetKeys: ', nextTargetKeys);
            // console.log('direction: ', direction);
            // console.log('moveKeys: ', moveKeys);
        };
        handleSelectChange = (sourceSelectedKeys, targetSelectedKeys) => {
            this.setState({ selectedKeys: [...sourceSelectedKeys, ...targetSelectedKeys] });
        };
        render() {
            const {
                visible, onCancel, onCreate, form
            } = this.props;
            const {
                leftData,
                rightData,
                selectedKeys
            } = this.state;
            const { getFieldDecorator } = form;
            const formItemLayout = {
                labelCol: { span: 6 },
                wrapperCol: { span: 18 },
            };
            const uploadProps = {
                // action: `${baseUrl}/item/importCheckItem`,
                action: `/item/importCheckItem`,
                onChange: this.handleChange,
                multiple: false,
                showUploadList: false,
                beforeUpload: this.beforeUpload,
                headers: {
                    menuUrl: window.location.pathname
                }
            };

            return (
                <Modal
                    visible={visible}
                    title={'流程记录'}
                    okText="确定"
                    cancelText='取消'
                    onCancel={onCancel}
                    onOk={onCreate}
                    maskClosable={false}
                >
                    <Form>
                        <Tabs defaultActiveKey="1" onChange={this.callback}>
                            <TabPane tab="基本信息" key="1">
                                <Form>
                                    <Row>
                                        <Col span={8}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label="文件编号"
                                            >
                                                {getFieldDecorator('planName', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                        <Col span={8}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label="申请日期"
                                            >
                                                {getFieldDecorator('startTime', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col span={8}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label="文件名称"
                                            >
                                                {getFieldDecorator('planName', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                        <Col span={8}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label="页码"
                                            >
                                                {getFieldDecorator('planName', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                        <Col span={8}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label="编写依据"
                                            >
                                                {getFieldDecorator('startTime', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col span={8}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label="文件类别"
                                            >
                                                {getFieldDecorator('planName', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                        <Col span={8}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label="申请部门"
                                            >
                                                {getFieldDecorator('planName', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col span={8}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label="版本号"
                                            >
                                                {getFieldDecorator('planName', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                        <Col span={8}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label="申请人"
                                            >
                                                {getFieldDecorator('planName', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                        <Col span={8}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label="是否年审"
                                            >
                                                {getFieldDecorator('startTime', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                        <Col span={8}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label="年审周期"
                                            >
                                                {getFieldDecorator('startTime', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col span={8}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label="申请类别"
                                            >
                                                {getFieldDecorator('planName', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                        <Col span={8}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label="申请原因"
                                            >
                                                {getFieldDecorator('planName', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                        <Col span={8}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label="年审部门"
                                            >
                                                {getFieldDecorator('startTime', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                    </Row>
                                    <Row>
                                        <Col span={8}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label="审核人"
                                            >
                                                {getFieldDecorator('planName', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                        <Col span={8}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label="审批人"
                                            >
                                                {getFieldDecorator('planName', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                    </Row>
                                </Form>
                            </TabPane>
                            <TabPane tab="文件内容" key="2">

                            </TabPane>
                            <TabPane tab="附件" key="3">
                                <div className="addSkip">
                                    <Upload {...uploadProps} fileList={this.state.fileList}>
                                        {<a style={{ marginRight: 20 }}>
                                            上传</a>}
                                    </Upload>
                                </div>
                            </TabPane>
                            <TabPane tab="流程" key="4">
                                <div style={{ paddingLeft: 33 }}>
                                    <Transfer
                                        ref={(transfer) => { this.transfer = transfer }}
                                        dataSource={leftData}
                                        // titles={[`未关联${title}`, `已关联${title}`]}
                                        targetKeys={rightData}
                                        selectedKeys={selectedKeys}
                                        onChange={this.handleChange}
                                        onSelectChange={this.handleSelectChange}
                                        render={item => item.title}
                                        // disabled={disabled}
                                        listStyle={{
                                            width: 270,
                                            height: 320,
                                        }}
                                        loading={true}
                                        showSearch
                                        filterOption={this.filterOption}
                                    />
                                </div>
                            </TabPane>
                        </Tabs>
                    </Form>
                </Modal>
            );
        }
    }
);
export default App