import React,{Component} from 'react';
import './index.less'
import { Modal, Form, Checkbox, Select, Alert } from "antd";
const FormItem = Form.Item;
const { Option } = Select;

class SetHost extends Component{
    // 点击确定
    handleaddsetMeal = (e) => {
        e.preventDefault();
        const { form } = this.props;
        form.validateFields((err, values) => {
            if (!err) {
                this.props.handleaddsetHost(values)
            }
        })
    }
    // 点击取消
    handleCancel = () => {
        this.props.handleHostCancel();
    }
    render(){
        const { setHostVisible, form } = this.props;
        const { getFieldDecorator } = form;
        const time = new Array(24).fill('').map((item,index) => {
            if (index < 10) {
                return `0${index}:00:00`
            }else{
                return `${index}:00:00`
            }
        })
        console.log(time);
        return(
            <Modal
                className="SetHost"
                title='定时自动备份数据库'
                visible={setHostVisible}
                width={420}
                onCancel={this.handleCancel}
                onOk={this.handleaddsetMeal}
            >
                <div className="password-box">
                    <Alert message="温馨提示：是每天自动定时备份数据库设置时间" type="warning" showIcon />
                    <Form labelCol={{ span: 5 }} wrapperCol={{ span: 17 }}>
                        <FormItem 
                            label="执行时间"
                            style={{marginTop:20}}
                        >
                            {getFieldDecorator('excDate', {
                                initialValue: 14,
                            })(
                                <Select placeholder="请选择">
                                    {
                                        time.map((item,index) => {
                                            return <Option value={index} key={index} >{item}</Option>
                                        })
                                    }
                                    
                                </Select>
                            )}
                        </FormItem>
                        <FormItem label="执行状态">
                            {getFieldDecorator('effective', {
                                valuePropName: 'checked',
                                initialValue: true
                            })(
                                <Checkbox>有效</Checkbox>
                            )}
                        </FormItem>
                    </Form>
                </div>
            </Modal>
        )
    }
}
export default Form.create()(SetHost)