import React, { Component } from 'react'
import { Form, Input, Modal, Select, Tabs, Table, Row, Col, Button, message } from 'antd'
import './index.less'
import Api from '../../../api/index';
const { Option } = Select;
const { TextArea } = Input;
const { TabPane } = Tabs

const App = Form.create({ name: 'form_in_modal' })(
    class extends Component {
        constructor(props) {
            super(props)
            this.state = {
                selectedRowKeys: [],
                selectedRowKeysOn: [],
                checkColumns: [],
                planDataFiles: [],//待选文件
                planDataFilesed: [],//已选文件
                dataFiles: {
                    pageSize: 10,
                    page: 1,
                },
                dataFilesed: {
                    pageSize: 10,
                    page: 1,
                },
            }
        }
        componentDidMount() {
            this.setState({
                columns: [{
                    title: '文件编号',
                    key: 'fileNo',
                    width: '200px',
                    render: (text, record, index) => {
                        if (record.fileNo) {
                            return (
                                <div className='ellipsis'>{record.fileNo}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '文件名称',
                    key: 'fileName',
                    render: (text, record, index) => {
                        if (record.fileName) {
                            return (
                                <div className='ellipsis'>{record.fileName}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '文件类别',
                    key: 'fileTypeIdName',
                    render: (text, record, index) => {
                        if (record.fileTypeIdName) {
                            return (
                                <div className='ellipsis'>{record.fileTypeIdName}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }
                ]
            }, () => {
                // this.setInitMenuOn()
            }
            )
        }


        setInitMenuOn() {
            const { dataOn } = this.state
            this.setState({
                planDataOn: dataOn.list
            }
            )
        }
        callback = (key) => {

        }
        onSelectChange = (selectedRowKeys, selectedRows) => {
            const filesId = selectedRows.map(v => {
                return v.bizId
            })
            this.setState({
                selectedRowKeys,
                filesId,
            });
        }

        onSelectChangeOn = (selectedRowKeys, selectedRows) => {
            const filesIded = selectedRows.map(v => {
                return v.bizId
            })
            this.setState({
                selectedRowKeys,
                filesIded,
            });
        }
        //添加
        addTrainFile = () => {
            const { filesId } = this.state
            Api.addTrainFile({
                trainId: this.props.filesTrainId,
                fileIdList: filesId
            }, (res) => {
                if (res.code = "200") {
                    message.success(res.message)
                }
                // this.props.filesFunction
                // this.props.filesFunctioned
            })
        }
        //删除
        deleteTrainFile = () => {
            const { filesIded } = this.state
            Api.deleteTrainFile({
                fileIdList: filesIded,
            }, (res) => {
                if (res.code = "200") {
                    message.success(res.message)
                }
                // this.props.filesFunction
                // this.props.filesFunctioned
            })
        }
        render() {
            const {
                visible, onCancel, onCreate, form, labelList, isEdit,
                filesTrainId, planDataFiles, planDataFilesed,
                recordId, filesFunction, filesFunctioned
            } = this.props;
            const {
                selectedRowKeys,
                // selectedRowKeysOn,
                planData,
                columns,
                filesId,
                filesIded
            } = this.state;
            const { getFieldDecorator } = form;
            const formItemLayout = {
                labelCol: { span: 6 },
                wrapperCol: { span: 18 },
            };

            const rowSelection = {
                selectedRowKeys,
                onChange: this.onSelectChange,
            };

            const rowSelectionOn = {
                selectedRowKeys,
                onChange: this.onSelectChangeOn,
            };

            const checkColumns = [
                {
                    title: '文件编号',
                    key: 'fileNo',
                    width: '200px',
                    render: (text, record, index) => {
                        if (record.fileNo) {
                            return (
                                <div className='ellipsis'>{record.fileNo}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '文件名称',
                    key: 'fileName',
                    render: (text, record, index) => {
                        if (record.fileName) {
                            return (
                                <div className='ellipsis'>{record.fileName}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '文件类别',
                    key: 'fileTypeIdName',
                    render: (text, record, index) => {
                        if (record.fileTypeIdName) {
                            return (
                                <div className='ellipsis'>{record.fileTypeIdName}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    }
                }
            ]
            return (
                <Modal
                    visible={visible}
                    title={"文件配置"}
                    okText="确定"
                    cancelText='取消'
                    onCancel={onCancel}
                    onOk={onCreate}
                    maskClosable={false}
                >
                    <Form>
                        <Tabs defaultActiveKey="1" onChange={this.callback}>
                            <TabPane tab="待选文件" key="1">
                                <Button onClick={this.addTrainFile}>添加</Button>
                                <Form>
                                    <Row>
                                        <Col span={6}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label=""
                                            >
                                                {getFieldDecorator('bianxiebumen', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                        <Col span={6}>
                                            <Form.Item
                                                {...formItemLayout}
                                                label=""
                                            >
                                                {getFieldDecorator('bianxieren', {
                                                })(
                                                    <Input placeholder="请输入" autoComplete='off' />
                                                )}
                                            </Form.Item>
                                        </Col>
                                        <Col span={6}>
                                            <Button>搜索</Button>
                                        </Col>
                                    </Row>
                                </Form>

                                <Table
                                    rowSelection={rowSelection}
                                    columns={columns}
                                    dataSource={planDataFiles}
                                />
                            </TabPane>
                            <TabPane tab="已选文件" key="2">
                                <Button onClick={this.deleteTrainFile}>删除</Button>
                                <Table
                                    rowSelection={rowSelectionOn}
                                    columns={checkColumns}
                                    dataSource={planDataFilesed}
                                />
                            </TabPane>
                        </Tabs>
                    </Form>
                </Modal>
            );
        }
    }
);
export default App