import React,{Component} from 'react';
import './index.less'
import { Modal, Tree} from "antd";
const { TreeNode } = Tree;

class Authorization extends Component{
    constructor(props){
        super(props);
        this.state = {
            autoExpandParent: true,
            menuCodes: [],
            checkedChildrenCode: [],
        }
    }
    componentWillReceiveProps(props) {
        if (props !== this.props) {
            console.log(props.checkedMenuCode,'----------');
            this.setState({
                menuCodes: props.checkedMenuCode
            })
        }
    }
    // 点击确定
    handleaddsetMeal = (e) => {
        e.preventDefault();
        const { menuCodes } = this.state;
        
        this.setState({
            checkedChildrenCode: menuCodes
        },() => {
            const needData = this.checkedChildren(this.props.menuData);
            this.props.handleaddsetMeal(needData)
        })
    }
    // 循环拿到选中的子节点
    checkedChildren = (data) => {
        const checkedChildrenCode = this.state.checkedChildrenCode
        data.forEach(item => {
            if (item.list && item.list.length){ // 判断是否有下一级
                if(checkedChildrenCode.indexOf(item.bizId) !== -1) { // 判断当前级是否被选中
                    const index = checkedChildrenCode.indexOf(item.bizId);
                    checkedChildrenCode.splice(index,1);
                    this.setState({
                        checkedChildrenCode
                    })
                }
                this.checkedChildren(item.list)
            }
        })
        return checkedChildrenCode
    }
    // 点击取消
    handleCancel = () => {
        this.props.handleCancel();
    }
    onCheck = (checkedKeys) => {
        this.setState({ menuCodes:checkedKeys });
    };
    // 渲染树结构
    renderTreeNodes = (data) => {
        const treeItem = data.map(item => {
            if (item.list && item.list.length) {
                return <TreeNode title={item.fullName} key={item.bizId} dataRef={item}>
                    {this.renderTreeNodes(item.list)}
                </TreeNode>
            }
            return <TreeNode key={item.bizId} title={item.fullName} />;
        })
        return treeItem
    }
    render(){
        const { authorizationShow, menuData } = this.props;
        const { menuCodes } = this.state;
        return(
            <Modal
                className="Authorization"
                title='角色授权'
                width={420}
                visible={authorizationShow}
                onCancel={this.handleCancel}
                onOk={this.handleaddsetMeal}
            >
                <div className="authorization-box">
                    <Tree
                        checkable
                        defaultExpandAll
                        onCheck={this.onCheck}
                        checkedKeys={menuCodes}
                    >
                        {this.renderTreeNodes(menuData)}
                    </Tree>
                </div>
            </Modal>
        )
    }
}
export default Authorization