import React, { Component } from 'react'
import { Button, Pagination } from 'antd';
import './index.less'
// import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread';
import SearchForm from "./search-form"; // 搜索条件
import Table from "./table-list"; // 搜索条件
class testStatistics extends Component {
  apis = window.apis;
  constructor(props) {
    super(props)
    this.state = {
      breadcrumbData: [
        { name: '考试管理' },
        { name: '试卷管理' }
      ],
      dataList: [],     // 试卷列表数据
      searchValue: {},  // 查询
      loading: false,   // 列表是否正在加载中
      dataList: [],     // 列表数据
      pageSize: 10,     // 分页Size
      pageNo: 1,        // 分页页码
    }
  }
  componentDidMount() {
    this.queryTableList();
  }
  // 开始查询
  // 开始查询
  handleSearchBtn = (value) => {
    console.log(value);
    console.log(value, '----search---');
    this.setState({
      searchValue: { ...value }
    }, () => {
      this.queryTableList();
    })
  }
  // 查询试卷列表
  queryTableList = (pageNo = 1, pageSize = 10) => {
    this.setState({
      loading: true,
    }, () => {
      const params = {
        ...this.state.searchValue,
        pageNo,
        pageSize,
      }
      this.apis.pageListExamTotal({ ...params }, (res) => {
        if (res.code === '200') {
          this.setState({
            dataList: res.data.list,
            total: res.data.total,
            loading: false,
            pageNo,
            pageSize
          })
        }
      })
    })
  }
  // 数据循环
  changeData = (data) => {
    data.map(item => {
      console.log(item);
      if (item.list && item.list.length) {
        const val = item['list'];
        delete item['list'];
        item['children'] = val;
        this.changeData(item.children);
      }
      return { ...item }
    })
    return data
  }
  // 切换页码
  handleChangePage = (pageNo, pageSize, sorter) => {
    this.queryTableList();
    console.log(pageNo, pageSize, sorter, '----pageNo, pageSize---')
  }
  // 删除试卷
  handleDelete = (code) => {
    console.log(code, '-----删除---')
  }
  render() {
    const { breadcrumbData, dataList, loading } = this.state;
    return (
      <div className="testStatistics">
        <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
        {/* 一定要加p-content-wrapper */}
        <div className='p-content-wrapper'>
          {/* 搜索条件 */}
          <div className="p-search">
            <SearchForm handleSearchBtn={this.handleSearchBtn} />
          </div>
          <div className="addBtn">
            <Button>重新发起考试</Button>
            <Button>导出</Button>
          </div>
          <div className="table-list">
            <Table
              loading={loading}
              data={dataList}
              handleChangePage={this.handleChangePage}
              handleDelete={this.handleDelete}
            />
            <Pagination className="pagination" total={50} showSizeChanger showQuickJumper />
          </div>
        </div>
      </div>
    )
  }
}
export default testStatistics