import React, { Component } from 'react'
import { Form, Input, Modal, Select, DatePicker } from 'antd'
import './index.less'
import Api from '../../../api/index';
const { MonthPicker, RangePicker, WeekPicker } = DatePicker
const { Option } = Select;
const { TextArea } = Input;
const App = Form.create({ name: 'form_in_modal' })(
    class extends Component {
        constructor(props) {
            super(props)
            this.state = {
                selectTime: [],
                listTypeDetailsData: [],//培训类别
                listExamForPlanData: [],//考试试卷
            }
        }
        componentDidMount() {
            this.listTypeDetails()//培训类别
            this.listExamForPlan()//考试试卷
        }
        onChange = (e, dateString) => {
            console.log(e, dateString,'555555555555');
            this.setState({
                selectTime: dateString
            })
        }    
        onChangeTwo(date, dateString) {
            console.log(date, dateString,'4444444444');
        }

        //培训类别
        listTypeDetails = () => {
            Api.listTypeDetails({
                itemsId: 4
            }, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listTypeDetailsData: res.data.list
                    })
                }
            })
        }
        //考试试卷
        listExamForPlan = () => {
            Api.listExamForPlan({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listExamForPlanData: res.data
                    })
                }
            })
        }
        render() {
            const {
                visible, onCancel, onCreate, form, labelList, isEdit,
            } = this.props;
            const {
                listTypeDetailsData,
                listExamForPlanData
            } = this.state;
            const { getFieldDecorator } = form;
            const formItemLayout = {
                labelCol: { span: 6 },
                wrapperCol: { span: 18 },
            };

            //培训类别
            const listTypeDetails = listTypeDetailsData ? listTypeDetailsData.map(res => {
                return (<Option key={res.bizId} values={res.bizId}>{res.itemName}</Option>)
            }) : null
            //考试试卷
            const listExamForPlan = listExamForPlanData ? listExamForPlanData.map(res => {
                return (<Option key={res.examId} values={res.examId}>{res.examName}</Option>)
            }) : null

            const _title = isEdit ? "培训配置--编辑" : "培训配置--添加"
            return (
                <Modal
                    visible={visible}
                    title={_title}
                    okText="确定"
                    cancelText='取消'
                    onCancel={onCancel}
                    onOk={onCreate}
                    maskClosable={false}
                >
                    <Form>
                        <Form.Item
                            label={'培训类别'}
                            {...formItemLayout}
                        >
                            {getFieldDecorator('bizId', {
                                // initialValue: isEdit ? 
                                rules: [{
                                    required: true, message: `请选择培训类别!`,
                                }],
                            })(
                                <Select
                                    allowClear
                                    placeholder="请选择"
                                    onChange={this.queryEleList}
                                    getPopupContainer={triggerNode => triggerNode.parentNode}
                                >
                                    {listTypeDetails}
                                </Select>
                            )}
                        </Form.Item>
                        <Form.Item
                            label={'培训名称'}
                            {...formItemLayout}
                        >
                            {getFieldDecorator('trainName', {
                                rules: [{
                                    required: true, message: `请输入培训名称!`,
                                }],
                            })(
                                <Input style={{ width: 340 }} placeholder="请输入" onKeyDown={this.handleKey} />
                            )}
                        </Form.Item>
                        <Form.Item
                            label={'考试试卷'}
                            {...formItemLayout}
                        >
                            {getFieldDecorator('examId', {
                                rules: [{
                                    required: true, message: `请选择考试试卷!`,
                                }],
                            })(
                                <Select
                                    allowClear
                                    placeholder="请选择"
                                    onChange={this.queryEleList}
                                    getPopupContainer={triggerNode => triggerNode.parentNode}
                                >
                                    {listExamForPlan}
                                </Select>
                            )}
                        </Form.Item>
                        <Form.Item
                            label={'及格分数'}
                            {...formItemLayout}
                        >
                            {getFieldDecorator('passPoint', {
                                rules: [
                                    {
                                        required: true,
                                        message: `请输入及格分数!`,
                                    },
                                    // {
                                    //     max: 100,
                                    //     message: "项目名称不得超过100个字"
                                    // }
                                ],
                            })(
                                <Input style={{ width: 340 }} placeholder="请输入" onKeyDown={this.handleKey} />
                            )}
                        </Form.Item>
                        <Form.Item
                            label={'应培训时间'}
                            {...formItemLayout}
                        >
                            {getFieldDecorator('readFen', {
                                rules: [
                                    {
                                        required: true,
                                        message: `请选输入应培训时间!`,
                                    },
                                    // {
                                    //     max: 200,
                                    //     message: "内容不得超过200个字"
                                    // }
                                ],
                            })(
                                <Input style={{ width: 340 }} placeholder="请输入" onKeyDown={this.handleKey} />
                            )}
                        </Form.Item>
                        <Form.Item
                            label={'培训内容'}
                            {...formItemLayout}
                        >
                            {getFieldDecorator('trainDescription', {
                                rules: [{
                                    required: true, message: `请输入培训内容!`,
                                }],
                            })(
                                <TextArea style={{ width: 340, height: 100 }} placeholder="请输入" />
                            )}
                        </Form.Item>
                        <Form.Item
                            label={'开始日期'}
                            {...formItemLayout}
                        >
                            {getFieldDecorator('startDate', {
                                rules: [{
                                    required: true, message: `请输入开始日期!`,
                                }],
                            })(
                                <DatePicker onChange={this.onChange} onClick={ () => this.props.GetStates(this.state.dataTime)} />
                            )}
                        </Form.Item>
                        <Form.Item
                            style={{ marginBottom: 0 }}
                            {...formItemLayout}
                            label="结束日期"
                        >
                            {getFieldDecorator('endDate', {
                                rules: [
                                ],
                            })(
                                <DatePicker onChange={this.onChangeTwo} />
                            )}
                        </Form.Item>
                    </Form>
                </Modal>
            );
        }
    }
);
export default App