import React, { Component } from 'react'
import { Form, Input, Table, Row, Button, message, Col, DatePicker, Tabs, Select } from 'antd';
import {
    HashRouter as Router, Route, Switch, Link, withRouter,
} from 'react-router-dom';
import './index.less'
// import '../../../assets/css/common.less'
import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread'
import ProcessFromWrap from './processForm';
import WordFromWrap from './wordForm';
import ExcelFromWrap from './excelForm';
// import EditFromWrap from './editForm';
const Option = Select
const { TabPane } = Tabs
const Search = Input.Search;
const { RangePicker } = DatePicker;
class onBanner extends Component {
    constructor(props) {
        super(props)
        this.state = {
            checkColumns: [],
            pageSize: 10,
            page: 1,
            isEdit: true,
            listFileTypesData: [],//文件类别
            listApplyDeptInFileModelData: [],//编写部门下拉
            writtenData: [],//编写人
            listAllApplyTypesData: [],//申请类别
            processVisible: false,//流程记录 弹窗
            wordVisible: false,//新增Word 弹窗
            excelVisible: false,//新增excel 弹窗
            editVisible: false,//点击 编辑 弹窗
            // data: {
            //     list: [
            //         {
            //             planName: "chuu chloe",
            //             startTime: "2019-10-01",
            //             creator: "30",
            //             responsibleUserName: "21232",
            //             siteName: "2018-02-12",
            //             shenqingshijian: "shenqingshijian",
            //             zhuangtai: "zhuangtai",
            //             baopileixing: "baopileixing",
            //         },
            //         {
            //             planName: "詹小翾",
            //             startTime: "1996-10-03",
            //             creator: "30",
            //             responsibleUserName: "sjf",
            //             siteName: "1890-02-12",
            //             shenqingshijian: "shenqingshijian",
            //             zhuangtai: "zhuangtai",
            //             baopileixing: "baopileixing",
            //         }
            //     ]
            // },
            selectTime: [],
            breadcrumbData: [
                { name: '文件管理' },
                { name: '文件报批' }
            ],
        }
    }
    componentDidMount() {
        this.setState({
            checkColumns: [{
                title: '文件编号',
                key: 'fileNo',
                width: '200px',
                render: (text, record, index) => {
                    if (record.fileNo) {
                        return (
                            <div className='ellipsis'>{record.fileNo}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '文件名称',
                key: 'fileName',
                render: (text, record, index) => {
                    if (record.fileName) {
                        return (
                            <div className='ellipsis'>{record.fileName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '附件',
                key: 'fujian',
                dataIndex: 'fujian',
                width: 200,
                render: (text, record) => {
                    return (
                        <div>
                            <a onClick={this.handleFujian}>查看附件</a> &nbsp;
                        </div>
                    )
                }
            }, {
                title: '版本/修订号',
                key: 'fileVersion',
                render: (text, record, index) => {
                    if (record.fileVersion) {
                        return (
                            <div className='ellipsis'>{record.fileVersion}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '申请部门',
                key: 'applyDeptName',
                render: (text, record, index) => {
                    if (record.applyDeptName) {
                        return (
                            <div className='ellipsis'>{record.applyDeptName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '申请人',
                key: 'applyUserName',
                render: (text, record, index) => {
                    if (record.applyUserName) {
                        return (
                            <div className='ellipsis'>{record.applyUserName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '申请时间',
                key: 'applyDate',
                render: (text, record, index) => {
                    if (record.applyDate) {
                        return (
                            <div className='ellipsis'>{record.applyDate}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '状态',
                key: 'fileStatus',
                render: (text, record, index) => {
                    if (record.fileStatus === 1) {
                        return (
                            <div className='ellipsis'>未提交</div>
                        )
                    } if (record.fileStatus === 2) {
                        return (
                            <div className='ellipsis'>已提交</div>
                        )
                    } if (record.fileStatus === 3) {
                        return (
                            <div className='ellipsis'>生效</div>
                        )
                    } if (record.fileStatus === 4) {
                        return (
                            <div className='ellipsis'>归档</div>
                        )
                    } if (record.fileStatus === 5) {
                        return (
                            <div className='ellipsis'>作废</div>
                        )
                    } if (record.fileStatus === 6) {
                        return (
                            <div className='ellipsis'>退回</div>
                        )
                    } if (record.fileStatus === 7) {
                        return (
                            <div className='ellipsis'>待审批</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '报批类型',
                key: 'applyType',
                render: (text, record, index) => {
                    if (record.applyType === 1) {
                        return (
                            <div className='ellipsis'>新增</div>
                        )
                    } if (record.applyType === 2) {
                        return (
                            <div className='ellipsis'>修订</div>
                        )
                    } if (record.applyType === 3) {
                        return (
                            <div className='ellipsis'>归档</div>
                        )
                    } if (record.applyType === 4) {
                        return (
                            <div className='ellipsis'>作废</div>
                        )
                    } if (record.applyType === 5) {
                        return (
                            <div className='ellipsis'>删除</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '操作',
                key: 'operation',
                dataIndex: 'itemStatus',
                fixed: 'right',
                width: 200,
                render: (text, record) => {
                    return (
                        <div>
                            <a onClick={this.handleEdit.bind(this, record)}>编辑</a> &nbsp;
                            <a onClick={this.feedDelete.bind(this, record)}>删除</a> &nbsp;
                            <a onClick={this.processRecording.bind(this, record)}>流程记录</a>
                        </div>
                    )
                }
            }]
        }, () => {
            this.pageListFile()
            this.listFileTypes()//文件类别下拉
            this.listUsers()//编写人下拉
            this.listApplyDeptInFileModel()//编写部门下拉
            this.listAllApplyTypes()//申请类别
        }
        )
    }
    //初始化 列表 信息
    pageListFile() {
        const { pageSize, page } = this.state
        const data = {
            pageSize,
            pageNo: page
        }
        Api.pageListFile(data, (res) => {
            if (res.code === "200") {
                this.setState({
                    planData: res.data.list
                },
                    message.success(res.message)
                )
            } else {
                message.warning(res.message)
            }
        })
    }
    //点击查询
    handleSearchPeople = () => {
        this.setState({
            page: 1,
        }, () => {
            this.props.form.validateFieldsAndScroll((err, values) => {
                if (true) {
                    const { pageSize, page } = this.state;
                    this.setState({
                        deviceLoading: true
                    })
                    Api.pageListFile({
                        pageSize,
                        pageNo: page,
                        fileNo: values.fileNo == undefined ? values.fileNo : values.fileNo.trim(),
                        fileName: values.fileName == undefined ? values.fileName : values.fileName.trim(),
                        fileTypeId: values.fileTypeId,
                        applyDeptId: values.applyDeptId,
                        userId: values.userId,
                        applyTypeId: values.applyTypeId,
                    }, (res) => {
                        if (res.code === '200') {
                            this.setState({
                                page,
                                total: res.data.total,
                                planData: res.data.list
                            })
                        }
                    })
                }
            });
        })
    }
    //点击 重置
    handleReset = () => {
        this.props.form.resetFields();
        this.pageListFile()
    }
    //文件类别下拉
    listFileTypes = () => {
        Api.listFileTypes({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listFileTypesData: res.data.list,
                })
            }
        })
    }
    //编写部门下拉
    listApplyDeptInFileModel = () => {
        Api.listApplyDeptInFileModel({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listApplyDeptInFileModelData: res.data.list
                })
            }
        })
    }
    //编写人
    listUsers = () => {
        Api.listUsers({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    writtenData: res.data.list,
                })
            }
        })
    }
    //申请类别
    listAllApplyTypes = () => {
        Api.listAllApplyTypes({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listAllApplyTypesData: res.data.list,
                })
            }
        })
    }

    //点击删除
    feedDelete = (record) => {
        Api.deleteFileInFileQuery({
            bizId: record.bizId
        }, (res) => {
            if (res.code === "200") {
                message.success("删除成功")
                this.pageListFile()
            } else {
                message.warning(res.message)
            }
        })

    }
    //点击 流程记录
    processRecording = ( record ) => {
        
        Api.pageListLogInfo({
            fileId: record.bizId
        }, (res) => {
            if ( res.code === '200'){
                this.setState({
                    pageListLogInfoData: res.data.list
                })
            }
        })
        this.setState({
            processVisible: true,
        })
    }
    //流程记录 点击确定
    processCan = () => {
        this.setState({
            processVisible: false,
        })
    }
    //流程记录 点击取消
    processNo = () => {
        this.setState({
            processVisible: false,
        })
    }
    //新增Word 点击取消
    wordNo = () => {
        this.setState({
            wordVisible: false,
        })
    }
    //点击 新增excel
    addExcel = () => {
        this.props.history.push('/fileManagement/feedBack/excelFrom')
    }
    //点击 新增ppt
    addPPT = () => {
        this.props.history.push('/fileManagement/feedBack/addPptForm')
    }
    //点击 编辑
    handleEdit = (record) => {
        this.props.history.push(`/fileManagement/feedBack/wordForm?bizId=${record.bizId}&&isEdit=${this.state.isEdit}`)
    }
    //点击新增WORD
    addWord = () => {
        this.props.history.push('/fileManagement/feedBack/wordForm')
    }
    render() {
        const formItemLayout = {
            labelCol: { span: 8 },
            wrapperCol: { span: 16 },
        };
        const { getFieldDecorator } = this.props.form;
        const {
            breadcrumbData,
            planData,
            checkColumns,
            processVisible,
            wordVisible,
            excelVisible,
            editVisible,
            listFileTypesData,
            listApplyDeptInFileModelData,
            writtenData,
            listAllApplyTypesData,
            pageListLogInfoData
        } = this.state;

        //文件类别
        const listFileTypes = listFileTypesData ? listFileTypesData.map(res => {
            return (<Option key={res.fileTypeId} values={res.fileTypeId}>{res.fileTypeIdName}</Option>)
        }) : null
        //编写部门
        const listApplyDeptInFileModel = listApplyDeptInFileModelData ? listApplyDeptInFileModelData.map(res => {
            return (<Option key={res.applyDeptId} values={res.applyDeptId}>{res.applyDeptName}</Option>)
        }) : null
        //编写人
        const writtenBy = writtenData ? writtenData.map(res => {
            return (<Option key={res.userId} values={res.userId}>{res.userName}</Option>)
        }) : null
        //申请类别
        const listAllApplyTypes = listAllApplyTypesData ? listAllApplyTypesData.map(res => {
            return (<Option key={res.applyTypeId} values={res.applyTypeId}>{res.applyTypeName}</Option>)
        }) : null

        // const pagination = total > 10 ? {
        //     total: this.state.total,
        //     pageSize: this.state.pageSize,
        //     current: this.state.page,
        //     onChange: this.handleTableChange,
        //     showQuickJumper: true,
        //     showSizeChanger,
        //     onShowSizeChange: this.onShowSizeChange,
        // } : false
        return (
            <div className="feedBcak">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="p-search">
                        <Form style={{ position: 'relative', top: -3 }}>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件编号"
                                    >
                                        {getFieldDecorator('fileNo', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件名称"
                                    >
                                        {getFieldDecorator('fileName', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件类别"
                                    >
                                        {getFieldDecorator('fileTypeId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listFileTypes}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="编写部门"
                                    >
                                        {getFieldDecorator('applyDeptId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listApplyDeptInFileModel}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="编写人"
                                    >
                                        {getFieldDecorator('userId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {writtenBy}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="申请类别"
                                    >
                                        {getFieldDecorator('applyTypeId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listAllApplyTypes}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Button onClick={this.handleReset} style={{ float: 'right' }}>重置</Button>
                                    <Button type="primary" htmlType="submit" onClick={this.handleSearchPeople} style={{ marginRight: 20, float: 'right' }}>查询</Button>
                                </Col>
                            </Row>
                        </Form>
                    </div>
                    <div>
                        <Button style={{ marginLeft: '20px', background: '#4897FF', color: '#FFFFFF' }} onClick={this.addWord}>新增WORD</Button> &nbsp;
                        <Button style={{ border: '1px solid #4897FF', color: '#4897FF' }} onClick={this.addExcel}>新增Excel</Button> &nbsp;
                        <Button style={{ border: '1px solid #4897FF', color: '#4897FF' }} onClick={this.addPPT}>新增PPT</Button>
                    </div>
                    <div style={{ marginTop: "30px" }}>
                        <Table
                            style={{ marginTop: '-12px', marginLeft: '20px' }}
                            columns={checkColumns}
                            dataSource={planData}
                            // pagination={pagination}
                            key="bizId"
                        ></Table>
                    </div>
                    {/* 流程记录 */}
                    <ProcessFromWrap
                        visible={processVisible}
                        onCancel={this.processCan}
                        onCreate={this.processNo}
                        pageListLogInfoData={pageListLogInfoData}
                    />
                    {/* 编辑 */}
                    {/* <EditFromWrap
                        visible={editVisible}
                        onCancel={this.editCan}
                        onCreate={this.editNo}
                    /> */}
                </div>
            </div>
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner