import React, { Component } from 'react'
import { Form, Input, Table, Row, Button, message, Col, DatePicker, Popconfirm, Select } from 'antd';
import {
    HashRouter as Router, Route, Switch, Link, withRouter,
} from 'react-router-dom';
import './index.less'
// import '../../../assets/css/common.less'
import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread'
import FromWrap from './form';
import RoleFromWrap from './roleForm';
import ProcessFromWrap from './processForm';
import RoleAuthorization from '../../../components/roleAuthorization'

const Search = Input.Search;
const { RangePicker } = DatePicker;
const Option = Select

class onBanner extends Component {
    constructor(props) {
        super(props)
        this.state = {
            pageSize: 10,
            page: 1,
            pageListInsertLogcom: {
                pageSize: 10,
                page: 1
            },
            planData: [],
            fileId: '',
            checkColumns: [],
            departmentalVisible: false,//部门授权弹窗
            roleVisible: false,//角色授权弹窗
            processVisible: false,//流程记录弹窗
            selectedRowKeys: [],
            selectTime: [],
            writtenData: [],//编写人
            listYearsData: [],//发布年度
            listFileTypesData: [],//文件类别
            roleList: [], //角色列表
            listApplyDeptInFileModelData: [],//编写部门下拉
            checkedKeys: [],
            breadcrumbData: [
                { name: '文件管理' },
                { name: '文件查询' }
            ],
        }
    }
    componentDidMount() {
        this.setState({
            checkColumns: [{
                title: '文件编号',
                key: 'fileNo',
                width: '200px',
                render: (text, record, index) => {
                    if (record.fileNo) {
                        return (
                            <div className='ellipsis'>{record.fileNo}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '文件名称',
                key: 'fileName	',
                render: (text, record, index) => {
                    if (record.fileName) {
                        return (
                            <div className='ellipsis'>{record.fileName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '附件',
                key: 'fileContent',
                render: (text, record) => {
                    if (record.fileContent) {
                        return (
                            <div>
                                <a onClick={this.fileContent}>查看附件</a>
                            </div>
                        )
                    }
                }
            }, {
                title: '版本/修订号',
                key: 'fileVersion',
                render: (text, record, index) => {
                    if (record.fileVersion) {
                        return (
                            <div className='ellipsis'>{record.fileVersion}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '生效日期',
                key: 'effectDate',
                render: (text, record, index) => {
                    if (record.effectDate) {
                        return (
                            <div className='ellipsis'>{record.effectDate}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '文件类别',
                key: 'fileTypeIdName',
                render: (text, record, index) => {
                    if (record.fileTypeIdName) {
                        return (
                            <div className='ellipsis'>{record.fileTypeIdName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '编写部门',
                key: 'applyDeptName',
                render: (text, record, index) => {
                    if (record.applyDeptName) {
                        return (
                            <div className='ellipsis'>{record.applyDeptName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '编写人',
                key: 'applyUserName',
                render: (text, record, index) => {
                    if (record.applyUserName) {
                        return (
                            <div className='ellipsis'>{record.applyUserName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, , {
                title: '审核人',
                key: 'approverUserName',
                render: (text, record, index) => {
                    if (record.approverUserName) {
                        return (
                            <div className='ellipsis'>{record.approverUserName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '审批人',
                key: 'auditorName',
                render: (text, record, index) => {
                    if (record.auditorName) {
                        return (
                            <div className='ellipsis'>{record.auditorName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '操作',
                key: 'operation',
                dataIndex: 'itemStatus',
                // width: 200,
                render: (text, record) => {
                    return (
                        <div>
                            <a onClick={this.departmentalAuthorization.bind(this, record)}>部门授权</a> &nbsp;
                            <a onClick={this.roleAuthorization.bind(this, record)}>角色授权</a> &nbsp;
                            <Popconfirm title="确定要删除这条信息吗" onConfirm={this.fileDelete.bind(record)}>
                                <a className="operation" href="javascripy;">删除</a>
                            </Popconfirm>&nbsp;
                            <a onClick={this.processRecording.bind(this, record)}>流程记录</a> &nbsp;
                            <a onClick={this.handleDownLoad}>下载</a>
                        </div>
                    )
                }
            }]
        }, () => {
            this.pageListQueryFiles()//初始化列表
            this.listUsers()//编写人下拉
            this.listYears()//发布年度下拉
            this.listFileTypes()//文件类别下拉
            this.listApplyDeptInFileModel()//编写部门下拉
            // this.queryRoleList();
        }
        )
    }
    //编写人
    listUsers = () => {
        Api.listUsers({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    writtenData: res.data.list,
                })
            }
        })
    }
    //发布年度下拉
    listYears = () => {
        Api.listYears({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listYearsData: res.data.list,
                })
            }
        })
    }
    //文件类别下拉
    listFileTypes = () => {
        Api.listFileTypes({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listFileTypesData: res.data.list,
                })
            }
        })
    }
    //编写部门下拉
    listApplyDeptInFileModel = () => {
        Api.listApplyDeptInFileModel({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listApplyDeptInFileModelData: res.data.list
                })
            }
        })
    }
    //初始化 列表 数据
    pageListQueryFiles() {
        const { pageSize, page } = this.state
        const data = {
            pageSize,
            pageNo: page
        }
        Api.pageListQueryFiles(data, (res) => {
            if (res.code === "200") {
                this.setState({
                    planData: res.data.list
                },
                    message.success(res.message)
                )
            } else {
                message.waring(res.message)
            }
        })
    }
    //点击查询
    handleSearchPeople = () => {
        this.setState({
            page: 1,
        }, () => {
            this.props.form.validateFieldsAndScroll((err, values) => {
                if (true) {
                    const { pageSize, page } = this.state;
                    this.setState({
                        deviceLoading: true
                    })
                    Api.pageListQueryFiles({
                        pageSize,
                        pageNo: page,
                        applyDeptId: values.applyDeptId,
                        fileNo: values.fileNo == undefined ? values.fileNo : values.fileNo.trim(),
                        applyDeptName: values.applyDeptName == undefined ? values.applyDeptName : values.applyDeptName.trim(),
                        applyUser: values.applyUser == undefined ? values.applyUser : values.applyUser.trim(),
                        applyYear: values.applyYear,
                        fileName: values.fileName == undefined ? values.fileName : values.fileName.trim(),
                        fileTypeIdName: values.fileTypeIdName,
                        auditorName: values.auditorName == undefined ? values.auditorName : values.auditorName.trim(),
                    }, (res) => {
                        if (res.code === '200') {
                            this.setState({
                                page,
                                total: res.data.total,
                                planData: res.data.list
                            })
                        }
                    })
                }
            });
        })
    }
    //点击 重置
    handleReset = () => {
        this.props.form.resetFields();
        this.handleSearchPeople()
    }
    //点击部门授权
    departmentalAuthorization = (record) => {
        this.setState({
            departmentalVisible: true,
            depatId: record.bizId
        })
    }
    //部门授权 点击取消
    departmentalCan = () => {
        this.setState({
            departmentalVisible: false,
        })
    }
    //部门授权 点击确定
    departmentalNo = (depatId) => {
        Api.addFileInDeptRole({
            fileId: depatId,
            deptIdList: this.state.deptIdListData,
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    departmentalVisible: false,
                })
                message.success(res.message)
            }
        })
    }
    onCheckList = (checkedKeys) => {
        this.setState({
            checkedKeys,
            deptIdListData: checkedKeys,
        });
    };
    onSelectList = (selectedKeys, info) => {
        console.log('onSelect', info);
        this.setState({ selectedKeys });
    };
    //点击角色授权
    roleAuthorization = (record) => {
        this.setState({
            roleVisible: true,
            userDetail: record
        })
    }
    // 角色授权确认弹框
    handleRoleOk = (checkedList) => {
        const params = {
            userId: this.state.userDetail.bizId,
            roleIdList: checkedList
        }
        this.apis.addUserInRole(params, (res) => {
            if (res.code === '200') {
                message.success('角色授权成功！');
                this.handleRoleCancle();
                this.queryTableList(this.state.pageNo, this.state.pageSize);
            }
        })
    }
    //角色的授权 点击取消
    roleNo = () => {
        this.setState({
            roleVisible: false,
        })
    }
    //点击 流程记录
    processRecording = (record) => {
        console.log(record, record.bizId, "555555555")
        const data = {
            fileId: record.bizId
        }
        Api.pageListInsertLog(data, (res) => {
            if (res.code === '200') {
                this.setState({
                    pageListInsertLogData: res.data.list,
                    total: res.data.total
                })
            }
        })

        Api.pageListUpdateLog(data, (res) => {
            if (res.code === '200') {
                this.setState({
                    pageListUpdateLogData: res.data.list,
                    total: res.data.total
                })
            }
        })

        Api.pageListFileYearRecord({}, (res) => {
            if (res.code === '200') {
                this.setState({
                    pageListFileYearRecordData: res.data.list,
                    total: res.data.total
                })
            }
        })

        Api.selectByPrimaryKey({
            bizId: record.bizId
        }, (res) => {
            if (res.code === '200') {
                this.setState({
                    selectByPrimaryKeyData: res.data,
                    total: res.data.total
                })
            }
        })
        this.setState({
            ddd: record,
            processVisible: true,
        })
    }
    //流程记录 点击确定
    processCan = () => {
        this.setState({
            processVisible: false,
        })
    }
    //流程记录 点击取消
    processNo = () => {
        this.setState({
            processVisible: false,
        })
    }
    //点击删除
    fileDelete = () => {

    }
    onSelectChange = (selectedRowKeys, rowKey) => {
        console.log(selectedRowKeys, rowKey, "3333333333333")
        this.setState({
            selectedRowKeys,
            fileIdsTwo: selectedRowKeys,
        });
    };
    //导出
    exportFiles = () => {
        const { fileIdsTwo } = this.state
        this.props.form.validateFieldsAndScroll((err, values) => {
            if (true) {
                Api.downloadFile('/query/exportFiles', {
                    fileIds: fileIdsTwo == undefined ? null : fileIdsTwo,
                    applyDeptId: values.applyDeptId == undefined ? null : values.applyDeptId,
                    fileNo: values.fileNo == undefined ? null : values.fileNo.trim(),
                    applyDeptName: values.applyDeptName == undefined ? null : values.applyDeptName.trim(),
                    applyUser: values.applyUser == undefined ? null : values.applyUser.trim(),
                    applyYear: values.applyYear == undefined ? null : values.applyYear,
                    fileName: values.fileName == undefined ?null : values.fileName.trim(),
                    fileTypeIdName: values.fileTypeIdName == undefined ? null : values.fileTypeIdName,
                    auditorName: values.auditorName == undefined ? null : values.auditorName.trim(),
                })
            }
        })
    }

    render() {
        const formItemLayout = {
            labelCol: { span: 8 },
            wrapperCol: { span: 16 },
        };
        const { getFieldDecorator } = this.props.form;
        const {
            breadcrumbData,
            planData,
            checkColumns,
            departmentalVisible,
            roleVisible,
            processVisible,
            selectedRowKeys,
            writtenData,
            listYearsData,
            listFileTypesData,
            listApplyDeptInFileModelData,
            depatId,
            checkedKeys,
            pageListInsertLogData,
            pageListUpdateLogData,
            pageListFileYearRecordData,
            selectByPrimaryKeyData,
            roleList,
            userDetail,
            fileIdsTwo
        } = this.state;
        // console.log(selectByPrimaryKeyData, '4444444444444444')
        const rowSelection = {
            selectedRowKeys,
            onChange: this.onSelectChange,
        };

        //编写人
        const writtenBy = writtenData ? writtenData.map(res => {
            return (<Option key={res.userId} values={res.userId}>{res.userName}</Option>)
        }) : null
        //发布年度
        const listYears = listYearsData ? listYearsData.map(res => {
            return (<Option key={res.applyYear} values={res.applyYear}>{res.applyYear}</Option>)
        }) : null
        //文件类别
        const listFileTypes = listFileTypesData ? listFileTypesData.map(res => {
            return (<Option key={res.fileTypeId} values={res.fileTypeId}>{res.fileTypeIdName}</Option>)
        }) : null
        //编写部门
        const listApplyDeptInFileModel = listApplyDeptInFileModelData ? listApplyDeptInFileModelData.map(res => {
            return (<Option key={res.applyDeptId} values={res.applyDeptId}>{res.applyDeptName}</Option>)
        }) : null
        // const pagination = total > 10 ? {
        //     total: this.state.total,
        //     pageSize: this.state.pageSize,
        //     current: this.state.page,
        //     onChange: this.handleTableChange,
        //     showQuickJumper: true,
        //     showSizeChanger,
        //     onShowSizeChange: this.onShowSizeChange,
        // } : false
        return (
            <div className="feedBcak">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="p-search">
                        <Form style={{ position: 'relative', top: -3 }}>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件编号"
                                    >
                                        {getFieldDecorator('fileNo', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="编写部门"
                                    >
                                        {getFieldDecorator('applyDeptId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listApplyDeptInFileModel}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="编写人"
                                    >
                                        {getFieldDecorator('applyUser', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {writtenBy}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="发布年度"
                                    >
                                        {getFieldDecorator('applyYear', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listYears}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件名称"
                                    >
                                        {getFieldDecorator('fileName', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件类别"
                                    >
                                        {getFieldDecorator('fileTypeId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listFileTypes}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="审批人"
                                    >
                                        {getFieldDecorator('auditorName', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Button onClick={this.handleReset} style={{ float: 'right' }}>重置</Button>
                                    <Button type="primary" htmlType="submit" onClick={this.handleSearchPeople} style={{ marginRight: 20, float: 'right' }}>查询</Button>
                                </Col>
                            </Row>
                        </Form>
                    </div>
                    <Button onClick={this.exportFiles.bind(fileIdsTwo)} className="download"><a>导出</a></Button>
                    <div style={{ marginTop: '65px', }}>
                        <Table
                            rowSelection={rowSelection}
                            style={{ marginTop: '-12px', marginLeft: '20px' }}
                            columns={checkColumns}
                            dataSource={planData}
                            key="bizId"
                            rowKey={record => record.bizId}
                        ></Table>
                    </div>
                    {/* 部门授权 */}
                    <FromWrap
                        style={{ width: '500px' }}
                        visible={departmentalVisible}
                        onCancel={this.departmentalCan}
                        onCreate={this.departmentalNo.bind(this, depatId)}
                        onCheck={this.onCheckList}
                        onSelect={this.onSelectList}
                        checkedKeys={checkedKeys}
                    // depatId={depatId}
                    />
                    {/* 角色授权 */}
                    <RoleAuthorization
                        ref="RoleAuthorization"
                        visible={roleVisible}
                        data={roleList}
                        // checkedList={userDetail.roleIdList}
                        handleCancel={this.handleRoleCancle}
                        handleOk={this.handleRoleOk}
                    />
                    {/* 流程记录 */}
                    <ProcessFromWrap
                        visible={processVisible}
                        onCancel={this.processCan}
                        onCreate={this.processNo}
                        pageListInsertLogData={pageListInsertLogData}
                        pageListUpdateLogData={pageListUpdateLogData}
                        pageListFileYearRecordData={pageListFileYearRecordData}
                        selectByPrimaryKeyData={selectByPrimaryKeyData}
                    // bbbbbb={ddd}
                    />

                </div>
            </div>
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner