import React, { Component } from 'react'
import { Button,message } from 'antd';
import './index.less'
import BreadcrumbLink from '../../../components/bread'
import Table from './table-list';
import JobModal from './job-modal';

class postManagement extends Component {
    apis = window.apis;
    constructor(props) {
        super(props)
        this.state = {
            breadcrumbData: [
                { name: '系统管理' },
                { name: '岗位管理' }
            ],
            loading: false,   // 列表加载中
            dataList: [],     // 角色列表数据
            total: 0,         // 角色总数
            pageSize: 10,     // 分页Size
            pageNum: 1,       // 分页页码
            type: 0,          // 弹框类型 0:新增，1:修改
            jobDetail: {},    // 点击选中的role的行内容
            deptData: [],     // 部门列表
            setJobModalShow: false,    // 新增，修改职位弹框的显隐            

        }
    }
    componentDidMount() {
        this.queryTableList();
        this.queryDeptListAll();
    }
    // 查询岗位列表
    queryTableList = (pageNum=1, pageSize=10) =>{
        this.setState({
            loading: true,
        },() => {
            const params = {
                pageNum,
                pageSize,
            }
            this.apis.getPageListJobs({...params},(res) => {
                if(res.code === '200') {
                    this.setState({
                        dataList: res.data.list,
                        total: res.data.total,
                        loading: false,
                        pageNum,
                        pageSize
                    })
                }else{
                    this.setState({
                        loading: false,
                    })
                }
            })
        })
    }
    // 查询部门列表
    queryDeptListAll = () => {
        this.apis.queryDeptList({},(res) => {
            if(res.code === '200') {
                this.setState({
                    deptData: res.data,
                })
            }
        })
    }
    // 删除
    handleDelete = (bizId) => {
        this.apis.deleteQMSJob({bizId},(res) => {
            if(res.code === '200') {
                message.success('删除成功！');
                this.queryTableList();
            }
        })
    }
    // 取消弹框
    handleCancel = () => {
        this.setState({
            setJobModalShow:false
        },() => {
            this.refs.JobModal.resetFields();
        })
    }
    // 新增职位弹框
    handleAddModal = () => {
        this.setState({
            setJobModalShow:true,
            type: 0,
            jobDetail: {}
        })
    }
    // 修改职位弹框
    handleEdit= (item) => {
        this.setState({
            setJobModalShow:true,
            type: 1,
            jobDetail: {...item}
        })
    }
    // 职位弹框--确定按钮
    handleaddsetMeal = (values) => {
        const params = {
            ...values
        }
        delete params['type'];
        if (values.type === 0) { // 新增
            this.apis.addQMSJob(params,(res) => {
                if(res.code === '200') {
                    message.success('新增成功！');
                    this.handleCancel();
                    this.queryTableList();
                }
            })
        }else{ // 修改
            params['bizId'] = this.state.jobDetail.bizId;
            this.apis.updateQMSJob(params,(res) => {
                if(res.code === '200') {
                    message.success('修改成功！');
                    this.handleCancel();
                    this.queryTableList();
                }
            })
        }
    }
    // 表格单个修改
    handleSingleUpdate = (typeval, checked, record) => {
        const value = {
            ...record,
            [typeval]: checked
        }
        this.apis.updateQMSJob({...value},(res) => {
            if(res.code === '200') {
                message.success('修改成功！');
                this.queryTableList();
            }
        })
    }
    render() {
        const { breadcrumbData, loading, dataList, pageNo, pageSize, setJobModalShow, type, jobDetail, deptData } = this.state;
        return (
            <div className="postManagement">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="btn">
                        <Button type="primary" onClick={this.handleAddModal}>新增</Button>
                    </div>
                    <div className="table-list">
                        <Table 
                            loading={loading} 
                            data={dataList} 
                            pageNo={pageNo}
                            pageSize={pageSize}
                            handleChangePage={this.queryTableList} 
                            handleDelete={this.handleDelete}
                            handleEdit={this.handleEdit}
                            handleSingleUpdate={this.handleSingleUpdate}
                        />
                    </div>
                </div>
                <JobModal
                    ref="JobModal"
                    setJobModalShow={setJobModalShow}
                    type={type}
                    jobDetail={jobDetail}
                    deptData={deptData}
                    handleCancel={this.handleCancel}
                    handleaddsetMeal={this.handleaddsetMeal}
                />
            </div>
        )
    }
}
export default postManagement