import React, { Component } from 'react';
import './App.less';
import { NavLink, withRouter } from 'react-router-dom';
import {
	Layout, Menu, Dropdown, Avatar, message, Icon
} from 'antd';
import Api from './api/index'
import userDefault from './assets/images/userDefault.png'

const { Content, Sider } = Layout;
const { SubMenu } = Menu

class App extends Component {
	constructor(props) {
		super(props)
		var today = new Date(),
			dataList = today.getFullYear() + '-' + (today.getMonth() + 1) + '-' + today.getDate();
		this.state = {
			dataList: dataList,
			userData: [],
			collapsed: false,
			// 菜单数据
			menuData: [],
			// 当前一级菜单
			activeMenu: {
				opMenuDTOS: []
			},
			selectKey: [],
			// 是否展示侧边栏
			isShowSide: true,
			// 用户信息
			userData: {},
			data: {
				menuDTOList: [
					{
						menuName: "文件管理",
						menuUrl: "/fileManagement",
						opMenuDTOS: [
							{
								menuName: "我的文档",
								menuUrl: "/fileManagement/allocation"
							},
							{
								menuName: "文件查询",
								menuUrl: "/fileManagement/fileSearch"
							},
							{
								menuName: "文件报批",
								menuUrl: "/fileManagement/feedBack"
							},
							{
								menuName: "文件审批",
								menuUrl: "/fileManagement/documentApproval"
							},
							{
								menuName: "文件接收",
								menuUrl: "/fileManagement/filesReceiving"
							},
							{
								menuName: "修改申请",
								menuUrl: "/fileManagement/application"
							},
							{
								menuName: "申请审核",
								menuUrl: "/fileManagement/applicationAudit"
							},
							{
								menuName: "文件年审",
								menuUrl: "/fileManagement/annualReview"
							},
							{
								menuName: "年审记录",
								menuUrl: "/fileManagement/annualReviewRecords"
							},
							{
								menuName: "归档文件",
								menuUrl: "/fileManagement/archiveFile"
							},
							{
								menuName: "作废文件",
								menuUrl: "/fileManagement/documentsScrapped"
							},
						]
					},
					{
						menuName: "培训管理",
						menuUrl: "/system",
						opMenuDTOS: [
							{
								menuName: "培训配置",
								menuUrl: "/system/maintenance"
							},
							{
								menuName: "我的培训",
								menuUrl: "/system/myTraining"
							},
							{
								menuName: "培训及时率计算",
								menuUrl: "/system/promptness"
							},
							{
								menuName: "培训统计",
								menuUrl: "/system/trainingStatistics"
							},
						]
					},
					{
						menuName: "考试管理",
						menuUrl: "/testManagement",
						opMenuDTOS: [
							{
								menuName: "试卷管理",
								menuUrl: "/testManagement/testPaper"
							}, {
								menuName: '考试安排',
								menuUrl: '/testManagement/testArrange'
							}, {
								menuName: '参加考试',
								menuUrl: '/testManagement/testJoin'
							}, {
								menuName: '批卷管理',
								menuUrl: '/testManagement/markingManagement'
							}, {
								menuName: '考试统计',
								menuUrl: '/testManagement/testStatistics'
							}
						]
					},
					{
						menuName: "试题库管理",
						menuUrl: "/itemBankManagement",
						opMenuDTOS: [
							{
								menuName: "试题管理",
								menuUrl: "/itemBankManagement/questionBank"
							},
						]
					},
					{
						menuName: "系统管理",
						menuUrl: "/systemManagement",
						opMenuDTOS: [
							{
								menuName: "菜单管理",
								menuUrl: "/systemManagement/menuManage"
							},
							{
								menuName: "用户管理",
								menuUrl: "/systemManagement/userManagement"
							},
							{
								menuName: "角色管理",
								menuUrl: "/systemManagement/roleManagement"
							},
							{
								menuName: "岗位管理",
								menuUrl: "/systemManagement/postManagement"
							},
							{
								menuName: "部门设置",
								menuUrl: "/systemManagement/branchManagement"
							},
							{
								menuName: "系统日志",
								menuUrl: "/systemManagement/systemLog"
							},
							{
								menuName: "登陆日志",
								menuUrl: "/systemManagement/loginLog"
							},
							{
								menuName: "字典管理",
								menuUrl: "/systemManagement/dictionaryManagement"
							},
							{
								menuName: "数据库资源",
								menuUrl: "/systemManagement/databaseResources"
							},
							{
								menuName: "数据库管理",
								menuUrl: "/systemManagement/mySQLManagement"
							}
						]
					}
				]
			}
		}
	}
	// 切换一级菜单
	handleMenu = (val) => {
		let _arr = [];
		_arr.push(val.menuUrl)
		this.setState({
			activeMenu: val,
			selectKey: _arr
		}, () => {
			this.props.history.push(val.opMenuDTOS[0].menuUrl);
		})
	}
	componentWillMount() {
		this.setInitMenu();
		// 如果是工作台则不显示
		if (this.props.match.path === '/workBench') {
			this.setState({ isShowSide: false })
		}
	}
	componentDidMount() {
		const self = this;
		// 登录轮询
		let getIsLogin = setInterval(function () {
			if (window.sessionStorage.getItem('isLogin')) {

				// 初始化菜单
				self.setInitMenu();
				// 获取用户信息
				// self.getUserInfo();

				clearInterval(getIsLogin);
			} else {

			}
		}, 50)
	}
	setInitMenu() {
		const { data } = this.state
		this.setState({
			menuData: data.menuDTOList
		}
		)
	}
	//退出
	loginOut = () => {
		Api.logout({
		}, (res) => {
			if (res.code === "200") {
				window.sessionStorage.removeItem("isLogin");
				this.props.history.push('/');
			}
		})
	}
	onCollapse = () => {
		const { collapsed } = this.state
		this.setState({ collapsed });
	};

	onClickItem = (e) => {
		this.setState({ SelectedKeys: e.keyPath });
		localStorage.setItem('SelectedKeys', JSON.stringify(e.keyPath))
	}
	//获取用户信息
	// getUserInfo() {
	// 	Api.getUserInfo({
	// 	}, (res) => {
	// 		if (res.data) {
	// 			this.setState({
	// 				userData: res.data
	// 			}, () => {

	// 			})
	// 		}
	// 	})
	// }
	render() {
		const { children } = this.props
		const {
			menuData,
			activeMenu,
			// userData
		} = this.state
		const menu = (
			<Menu>
				<Menu.Item key="0">
					<a href="javascript:;" onClick={this.loginOut.bind(this)}>退出登录</a>
				</Menu.Item>

			</Menu>
		);

		return (
			<div className='layout-box'>
				{/* <h1>这里是首页</h1> */}
				<Sider
					width='200'
					style={{
						overflow: 'auto', height: '90vh', position: 'fixed', left: 0, zIndex: 3, top: '70px', background: "#FFFFFF"
					}}
				>
					<Menu
						className="content-side"
						mode="inline"
						selectedKeys={this.state.SelectedKeys}
						onClick={this.onClickItem.bind(this)}

					>
						{
							menuData.map((val, ind, arr) => {
								return <SubMenu
									key={val.menuUrl}
									title={
										val.menuName
									}
								>
									{
										val.opMenuDTOS.map((values, ined) => {
											return <Menu.Item key={values.menuUrl}
												style={{ color: '#7C8495', background: '#FFFFFF' }}
											>
												<NavLink
													style={{ color: '#7C8495', background: '#FFFFFF', paddingLeft: 'auto !import' }}
													to={values.menuUrl}
													exact={false}
												>{values.menuName}</NavLink></Menu.Item>
										})
									}
								</SubMenu>

							})
						}
					</Menu>
				</Sider>
				<div className='layout'>
					<div className='layout-header'>
						{/* <h2>{activeMenu.menuName}</h2> */}
						<div style={{ height: '60px' }}>
							<div className="layout-log">
								<div style={{ paddingTop: '5px', paddingLeft: '30px', height: '60px', width: '200px', float: 'left', background: '#4897FF', color: '#FFFFFF', fontSize: "30px" }}>Elab care</div>
								<div style={{ paddingTop: '11px', height: '60px', color: '#F4F9FF', fontSize: "18px", paddingLeft: '20px', float: 'left' }}>授权单位：杭州第一人民医院检验科</div>
							</div>
							{/* <Dropdown overlay={menu} trigger={['click']}>
								<div className="layout-user">
									{
										userData.avatar ? <Avatar src={userData.avatar}></Avatar> : <Avatar src={userDefault}></Avatar>

									}
									<span style={{ marginLeft: 8 }}>{userData.name}</span>
								</div>
							</Dropdown> */}
							<div className='calendar' >
								<a />{this.state.dataList}
							</div>
							<div className="layout-user">
								{/* <span onClick={this.loginOut} style={{ color: '#FFFFFF', float: 'right', marginRight: '20px' }}>这里是退出</span> */}
								<Dropdown overlay={menu} trigger={['click']}>
									<div className="layout-login">
										<Avatar src={userDefault}></Avatar>
										<span style={{ marginLeft: 8, color: '#FFFFFF', position: 'relative', top: '3px' }}>管理员</span>
									</div>
								</Dropdown>

							</div>
						</div>
					</div>
					<div className='content-box'>
						{
							(activeMenu && (activeMenu.menuUrl === '/fileManagement' || activeMenu.menuUrl === '/system' || activeMenu.menuUrl === '/systemManagement' || activeMenu.menuUrl === '/testManagement')) ?
								<Content
									className='content-box-main'
								>
									<div className='content-main'
									>
										{children}
									</div>
								</Content> : <Content
									className='content-box-main'
								>
									<div className='content-main-bench'
									>
										{children}
									</div>
								</Content>
						}
					</div>
				</div>
			</div>
		);
	}
}

export default withRouter(App);
