import React, { Component } from 'react'
import { Form, Input, Modal, Select, Radio } from 'antd'
import './index.less'
import Api from '../../../api/index';
const { Option } = Select;
const { TextArea } = Input;
const App = Form.create({ name: 'form_in_modal' })(
    class extends Component {
        constructor(props) {
            super(props)
            this.state = {
            }
        }
        componentDidMount() {
        }
        render() {
            const {
                visible, onCancel, onCreate, form
            } = this.props;
            const {
            } = this.state;
            const { getFieldDecorator } = form;
            const formItemLayout = {
                labelCol: { span: 6 },
                wrapperCol: { span: 18 },
            };
            return (
                <Modal
                    visible={visible}
                    title={'角色授权'}
                    okText="确定"
                    cancelText='取消'
                    onCancel={onCancel}
                    onOk={onCreate}
                    maskClosable={false}
                >
                    <Form>
                        <div>
                            <Radio.Group onChange={this.onChange} defaultValue="a">
                                <Radio.Button value="a">Hangzhou</Radio.Button>
                            </Radio.Group>
                        </div>
                    </Form>
                </Modal>
            );
        }
    }
);
export default App