/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { Component } from 'react';
import { Table, Popconfirm, Checkbox } from 'antd';

class tableList extends Component {
    constructor(props) {
        super(props)
        this.columns = [
            {
                title: '角色名称',
                dataIndex: 'roleName',
                width: 210,
                fixed: 'left',
            },{
                title: '查询',
                dataIndex: 'allowQuery',
                width: 60,
                render: (text, record) => <Checkbox record={record} checked={text} typeval='allowQuery' onChange={this.onChange}></Checkbox>
            },{
                title: '阅读',
                dataIndex: 'allowRead',
                width: 60,
                render: (text, record) => <Checkbox record={record} checked={text} typeval='allowRead' onChange={this.onChange}></Checkbox>
            },{
                title: '修改',
                dataIndex: 'allowEdit',
                width: 60,
                render: (text, record) => <Checkbox record={record} checked={text} typeval='allowEdit' onChange={this.onChange}></Checkbox>
            },{
                title: '删除',
                dataIndex: 'allowDelete',
                width: 60,
                render: (text, record) => <Checkbox record={record} checked={text} typeval='allowDelete' onChange={this.onChange}></Checkbox>
            },{
                title: '作废',
                dataIndex: 'allowBad',
                width: 60,
                render: (text, record) => <Checkbox record={record} checked={text} typeval='allowBad' onChange={this.onChange}></Checkbox>
            },{
                title: '上传',
                dataIndex: 'allowUpload',
                width: 60,
                render: (text, record) => <Checkbox record={record} checked={text} typeval='allowUpload' onChange={this.onChange}></Checkbox>
            },{
                title: '下载',
                dataIndex: 'allowDown',
                width: 60,
                render: (text, record) => <Checkbox record={record} checked={text} typeval='allowDown' onChange={this.onChange}></Checkbox>
            },{
                title: '审核',
                dataIndex: 'allowAudit',
                width: 60,
                render: (text, record) => <Checkbox record={record} checked={text} typeval='allowAudit' onChange={this.onChange}></Checkbox>
            },{
                title: '审批',
                dataIndex: 'allowApprove',
                width: 60,
                render: (text, record) => <Checkbox record={record} checked={text} typeval='allowApprove' onChange={this.onChange}></Checkbox>
            },{
                title: '接收',
                dataIndex: 'allowReceive',
                width: 60,
                render: (text, record) => <Checkbox record={record} checked={text} typeval='allowReceive' onChange={this.onChange}></Checkbox>
            },{
                title: '授权',
                dataIndex: 'allowAuthorization',
                width: 60,
                render: (text, record) => <Checkbox record={record} checked={text} typeval='allowReceive' onChange={this.onChange}></Checkbox>
            },{
                title: '打印',
                dataIndex: 'allowPrint',
                width: 60,
                render: (text, record) => <Checkbox record={record} checked={text} typeval='allowPrint' onChange={this.onChange}></Checkbox>
            },{
                title: '归档',
                dataIndex: 'allowArchive',
                width: 60,
                render: (text, record) => <Checkbox record={record} checked={text} typeval='allowArchive' onChange={this.onChange}></Checkbox>
            },{
                title: '配置',
                dataIndex: 'allowConfigure',
                width: 60,
                render: (text, record) => <Checkbox record={record} checked={text} typeval='allowConfigure' onChange={this.onChange}></Checkbox>
            },{
                title: '启用',
                dataIndex: 'allowEnable',
                width: 60,
                render: (text, record) => <Checkbox record={record} checked={text} typeval='allowEnable' onChange={this.onChange}></Checkbox>
            },{
                title: '冻结',
                dataIndex: 'allowFreeze',
                width: 60,
                render: (text, record) => <Checkbox record={record} checked={text} typeval='allowFreeze' onChange={this.onChange}></Checkbox>
            },{
                title: '年审',
                dataIndex: 'allowYear',
                width: 60,
                render: (text, record) => <Checkbox record={record} checked={text} typeval='allowYear' onChange={this.onChange}></Checkbox>
            },{
                title: '仪器授权',
                dataIndex: 'allowDevice',
                width: 120,
                render: (text, record) => <Checkbox record={record} checked={text} typeval='allowDevice' onChange={this.onChange}></Checkbox>
            },{
                title: '源文件下载',
                dataIndex: 'allowDownSourceFile',
                width: 150,
                render: (text, record) => <Checkbox record={record} checked={text} typeval='allowDownSourceFile' onChange={this.onChange}></Checkbox>
            },{
                title: '排序码',
                dataIndex: 'sortCode',
                width: 150
            },{
                title: '操作',
                dataIndex: 'operation',
                width: 170,
                render: (text, record) =>
                <div>
                    <a className="operation" onClick={this.handleEdit.bind(this,record)}>编辑</a>
                    <Popconfirm title="确定要删除该数据吗" onConfirm={this.handleDelete.bind(this,record.bizId)}>
                        <a className="operation">删除</a>
                    </Popconfirm>
                    <a className="operation" onClick={this.hanleWarrant.bind(this,record)}>角色授权</a>
                </div>
                
            },
        ]
    }
    handleChangePage = (pageNo, pageSize) => {
        this.props.handleChangePage(pageNo, pageSize)
    }
    handleDelete = (code) => {
        this.props.handleDelete(code)
    }
    onShowSizeChange = (pageNo, pageSize) => {
        this.props.handleChangePage(1, pageSize)
    }
    handleEdit = (item,e) => {
        e.preventDefault();
        this.props.handleEdit(item)
    }
    onChange = (e) => {
        e.preventDefault();
        const { typeval,checked,record } = e.target;
        this.props.handleSingleUpdate(typeval, checked, record)
        
    }
    // 角色授权
    hanleWarrant = (record,e) => {
        e.preventDefault();
        this.props.hanleWarrant(record)
    }
    render() {
        return (
            <Table
                rowKey={(record) => record.bizId }
                loading={this.props.loading}
                columns={this.columns} 
                dataSource={this.props.data}
                scroll={{ x: 1810 }}
                pagination={{
                    hideOnSinglePage: true,
                    showSizeChanger: true,
                    showQuickJumper: true,
                    current: this.props.pageNo,
                    total: this.props.total,
                    pageSize: this.props.pageSize,
                    onChange: this.handleChangePage,
                    onShowSizeChange: this.onShowSizeChange,
                }}
            />
        )
    }
}
export default tableList