import React,{ Component } from 'react'
import { Input, Form, Row, Col, Button, TreeSelect } from 'antd'
const FormItem = Form.Item;
const { TreeNode } = TreeSelect;

class FormListuser extends Component{
    // 提交表单
    handleSubmit = () => {
        this.props.form.validateFields((err,values)=>{
            if(err){
                return false
            }
            const data = {};
            // 去除含有undefined的item
            Object.keys(values).forEach(res=>{
                if(values[res]){
                    data[res] = values[res]
                }
            })
            this.props.handleSearchBtn(data);
        })
    }
    handleReset = () => {
        this.props.form.resetFields();
        this.props.handleSearchBtn({});
    }
    // 渲染树结构
    renderTreeNodes = (data) => {
        const treeItem = data.map(item => {
            if (item.children && item.children.length) {
                return <TreeNode value={item.bizId} title={item.deptName} key={item.bizId} dataRef={item}>
                    {this.renderTreeNodes(item.children)}
                </TreeNode>
            }
            return <TreeNode value={item.bizId} title={item.deptName} key={item.bizId} />;
        })
        return treeItem
    }
    render(){
        const { getFieldDecorator } = this.props.form;
        return (
            <Form labelCol={{ span: 5 }} wrapperCol={{ span: 16 }}>
                <Row gutter={24}>
                    <Col span={8}>
                        <FormItem label="姓名/账号">
                            {
                                getFieldDecorator('account')(<Input placeholder="请输入" autoComplete='off' />)
                            }
                        </FormItem>
                    </Col>
                    <Col span={8}>
                        <FormItem label="部门">
                            {
                                getFieldDecorator('deptId')(
                                    <TreeSelect placeholder="请选择" allowClear treeDefaultExpandAll>
                                        {this.renderTreeNodes(this.props.deptList)}
                                    </TreeSelect>
                                )
                            }
                        </FormItem>
                    </Col>
                    <Col span={4}>
                        <Button onClick={this.handleReset} style={{ float: 'right' }}>重置</Button>
                        <Button type="primary"  onClick={this.handleSubmit} style={{ marginRight: 20, float: 'right' }}>查询</Button>
                    </Col>
                </Row>
            </Form>
        )
    }
}
export default Form.create()(FormListuser)

