import React, { Component } from 'react';
import { Table } from 'antd';

class tableList extends Component {
    constructor(props) {
        super(props)
        this.state= {
          current: 2,
          selectedRowKeys: [],
        }
        this.columns = [
          {
            title: '姓名',
            dataIndex: 'dspName',
            width: '200',
            sorter: true,
          },{
            title: '部门',
            dataIndex: 'deptName',
            sorter: true,
          },{
            title: '考试名称',
            dataIndex: 'trainName',
            sorter: true,
          },{
            title: '得分',
            dataIndex: 'pointStr',
            sorter: true,
          },{
            title: '及格/总分',
            dataIndex: 'totalPoints',
            sorter: true,
            render: (text, record, $index) => {
              return `${record.passScore}/${record.allScore}`
            }
          },{
            title: '完成时间',
            dataIndex: 'finishTime',
            sorter: true,
          },{
            title: '是否合格',
            dataIndex: 'pass',
            sorter: true,
            render: (text, record, $index) => {
              return {
                  'true': '合格',
                  'false': '不合格'
              }[text]
            }
          }]
    }
    handleChangePage = (pageNo, pageSize, sorter) => {
      this.props.handleChangePage(pageNo, pageSize,sorter)
      this.setState({
        current: pageNo
      })
    }
    handleDelete = (code) => {
      this.props.handleDelete(code)
    }
    onShowSizeChange = (pageNo, pageSize) => {
      console.log(pageNo, pageSize);
      this.props.handleChangePage(1, pageSize)
    }
    onSelectChange = (selectedRowKeys) => {
      console.log('selectedRowKeys changed: ', selectedRowKeys);
      this.setState({ selectedRowKeys });
    }
    render() {
      const { selectedRowKeys } = this.state;
        return (
          <Table
            rowKey={(record) => { return `${record.name}${Math.random(10)}` }}
            loading={this.props.loading}
            columns={this.columns} 
            dataSource={this.props.data}
            rowSelection={{
              selectedRowKeys,
              onChange: this.onSelectChange,
            }}
            onChange={this.handleChangePage}
            pagination={false}
          />
        )
    }
}
export default tableList