import React, { Component } from 'react'
import { Form, Input, Table, Row, Button, message, Col, DatePicker, Select } from 'antd';
import {
    HashRouter as Router, Route, Switch, Link, withRouter,
} from 'react-router-dom';
import './index.less'
import '../../../assets/css/common.less'
import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread'
const { Option } = Select;
const Search = Input.Search;
const { RangePicker } = DatePicker;
class onBanner extends Component {
    constructor(props) {
        super(props)
        this.state = {
            pageSize: 10,
            page: 1,
            planData: [],
            checkColumns: [],
            selectTime: [],
            listFileTypesData: [],//文件类别
            listApplyDeptInFileModelData: [],//编写部门下拉
            writtenData: [],//编写人
            listAllApplyTypesData: [],//申请类别
            breadcrumbData: [
                { name: '文件管理' },
                { name: '文件审批' }
            ],
        }
    }
    componentDidMount() {
        this.setState({
            checkColumns: [{
                title: '文件编号',
                key: 'fileNo',
                width: '200px',
                render: (text, record, index) => {
                    if (record.fileNo) {
                        return (
                            <div className='ellipsis'>{record.fileNo}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '文件名称',
                key: 'fileName',
                render: (text, record, index) => {
                    if (record.fileName) {
                        return (
                            <div className='ellipsis'>{record.fileName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '文件类别',
                key: 'fileTypeIdName',
                render: (text, record, index) => {
                    if (record.fileTypeIdName) {
                        return (
                            <div className='ellipsis'>{record.fileTypeIdName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '版本/修订号',
                key: 'fileVersion',
                render: (text, record, index) => {
                    if (record.fileVersion) {
                        return (
                            <div className='ellipsis'>{record.fileVersion}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '编写部门',
                key: 'applyDeptName',
                render: (text, record, index) => {
                    if (record.applyDeptName) {
                        return (
                            <div className='ellipsis'>{record.applyDeptName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '编写人',
                key: 'applyUserName',
                render: (text, record, index) => {
                    if (record.applyUserName) {
                        return (
                            <div className='ellipsis'>{record.applyUserName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '申请时间',
                key: 'applyDate',
                render: (text, record, index) => {
                    if (record.applyDate) {
                        return (
                            <div className='ellipsis'>{record.applyDate}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '当前状态',
                key: 'fileStatusName',
                render: (text, record, index) => {
                    if (record.fileStatusName) {
                        return (
                            <div className='ellipsis'>{record.fileStatusName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '操作',
                key: 'operation',
                dataIndex: 'itemStatus',
                // width: 200,
                render: (text, record) => {
                    return (
                        <div>
                            <a onClick={this.downLoad.bind(this, record)}>审批</a>
                        </div>
                    )
                }
            }
            ]
        }, () => {
            this.pageListFilesInApproval()//初始化列表
            this.listFileTypes()//文件类别下拉
            this.listApplyDeptInFileModel()//编写部门下拉
            this.listUsers()//编写人下拉
            this.listAllApplyTypes()//申请类别
        }
        )
    }
    //文件类别下拉
    listFileTypes = () => {
        Api.listFileTypes({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listFileTypesData: res.data.list,
                })
            }
        })
    }
    //编写部门下拉
    listApplyDeptInFileModel = () => {
        Api.listApplyDeptInFileModel({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listApplyDeptInFileModelData: res.data.list
                })
            }
        })
    }
    //编写人
    listUsers = () => {
        Api.listUsers({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    writtenData: res.data.list,
                })
            }
        })
    }
    //申请类别
    listAllApplyTypes = () => {
        Api.listAllApplyTypes({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listAllApplyTypesData: res.data.list,
                })
            }
        })
    }

    //点击新增WORD
    addWord = () => {
        this.props.history.push()
    }
    //选择时间后的回调
    onTimeChange = (e, dateString) => {
        this.setState({
            selectTime: dateString
        })
    }
    //初始化列表
    pageListFilesInApproval() {
        const { pageSize, page } = this.state
        const data = {
            pageSize,
            pageNo: page
        }
        Api.pageListFilesInApproval(data, (res) => {
            if (res.code === "200") {
                this.setState({
                    planData: res.data.list
                },
                    message.success(res.message)
                )
            } else {
                message.waring(res.message)
            }
        })
    }
    //点击查询
    handleSearchPeople = () => {
        this.setState({
            page: 1,
        }, () => {
            this.props.form.validateFieldsAndScroll((err, values) => {
                if (true) {
                    const { pageSize, page } = this.state;
                    this.setState({
                        deviceLoading: true
                    })
                    Api.pageListFilesInApproval({
                        pageSize,
                        pageNo: page,
                        fileNo: values.fileNo == undefined ? values.fileNo : values.fileNo.trim(),
                        fileName: values.fileName == undefined ? values.fileName : values.fileName.trim(),
                        fileTypeId: values.fileTypeId,
                        applyDeptId: values.applyDeptId,
                        userId: values.userId,
                        applyTypeId: values.applyTypeId,
                    }, (res) => {
                        if (res.code === '200') {
                            this.setState({
                                page,
                                total: res.data.total,
                                planData: res.data.list
                            })
                        }
                    })
                }
            });
        })
    }
    //点击 重置
    handleReset = () => {
        this.props.form.resetFields();
        this.pageListFilesInApproval()
    }
    // 点击审批
    downLoad = ( record ) => {
        this.props.history.push(`/fileManagement/documentApproval/exaMine/?bizId=${record.bizId}`)
    }
    render() {
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 16 },
        };
        const { getFieldDecorator } = this.props.form;
        const {
            breadcrumbData,
            planData,
            checkColumns,
            listFileTypesData,
            listApplyDeptInFileModelData,
            writtenData,
            listAllApplyTypesData,

        } = this.state;

        //文件类别
        const listFileTypes = listFileTypesData ? listFileTypesData.map(res => {
            return (<Option key={res.fileTypeId} values={res.fileTypeId}>{res.fileTypeIdName}</Option>)
        }) : null
        //编写部门
        const listApplyDeptInFileModel = listApplyDeptInFileModelData ? listApplyDeptInFileModelData.map(res => {
            return (<Option key={res.applyDeptId} values={res.applyDeptId}>{res.applyDeptName}</Option>)
        }) : null
        //编写人
        const writtenBy = writtenData ? writtenData.map(res => {
            return (<Option key={res.userId} values={res.userId}>{res.userName}</Option>)
        }) : null
        //申请类别
        const listAllApplyTypes = listAllApplyTypesData ? listAllApplyTypesData.map(res => {
            return (<Option key={res.applyTypeId} values={res.applyTypeId}>{res.applyTypeName}</Option>)
        }) : null

        // const pagination = total > 10 ? {
        //     total: this.state.total,
        //     pageSize: this.state.pageSize,
        //     current: this.state.page,
        //     onChange: this.handleTableChange,
        //     showQuickJumper: true,
        //     showSizeChanger,
        //     onShowSizeChange: this.onShowSizeChange,
        // } : false
        return (
            <div className="feedBcak">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="p-search">
                        <Form style={{ position: 'relative', top: -3 }}>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件编号"
                                    >
                                        {getFieldDecorator('planName', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件名称"
                                    >
                                        {getFieldDecorator('startTime', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件类别"
                                    >
                                        {getFieldDecorator('fileTypeId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listFileTypes}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="编写部门"
                                    >
                                        {getFieldDecorator('applyDeptId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listApplyDeptInFileModel}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="编写人"
                                    >
                                        {getFieldDecorator('applyUser', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {writtenBy}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="申请类别"
                                    >
                                        {getFieldDecorator('applyTypeId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listAllApplyTypes}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6} offset={0}>
                                    <Button onClick={this.handleReset} style={{ float: 'right' }}>重置</Button>
                                    <Button type="primary" htmlType="submit" onClick={this.handleSearchPeople} style={{ marginRight: 20, float: 'right' }}>查询</Button>
                                </Col>
                            </Row>
                        </Form>
                    </div>
                    <Table
                        columns={checkColumns}
                        dataSource={planData}
                        // pagination={pagination}
                        key="bizId"
                    ></Table>

                </div>
            </div>
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner