import React, { Component } from 'react'
import { Breadcrumb, } from 'antd'
import {
    HashRouter as Router, Route, Link
} from 'react-router-dom';
import '../assets/css/common.less'

/*
数据格式如下： 
breadcrumbData: 
[
    {name: '资产台账', url: '/ledger/device', query: {}},
    {name: '设备管理'}
]

*/

class Bread extends Component {

    render() {

        const breadcrumbItems = this.props.breadcrumbData ? this.props.breadcrumbData.map((val, ind) => {
            return (
                <Breadcrumb.Item key={ind}>
                    {
                        val.url ?
                            <Link to={{ pathname: val.url, query: val.query, state: val.state }}>
                                {val.name}
                            </Link>
                            :
                            <span>
                                {val.name}
                            </span>
                    }
                </Breadcrumb.Item>
            )
        }) : ''

        const numberStaging = Number(this.props.breadcrumbData.length) > 1 ? Number(this.props.breadcrumbData.length) - 1 : 0
        // let _breadcrumbItems = `当前位置：${breadcrumbItemsOn}`

        // console.log(breadcrumbItemsOn,'0000000000000000')
        return (
            <div className={`p-bread-wrapper ${this.props.breadClass ? this.props.breadClass : ''}`}>
              {
                this.props.children
                ? this.props.children
                : 
                                (
                  <div className="breadcrumbLink-left">
                    {this.props.breadcrumbData[numberStaging].name}
                  </div>
                )
              }
              <Breadcrumb style={{ float: 'right'}}>
                {breadcrumbItems}
              </Breadcrumb>
              <span style={{ float: 'right'}}>当前位置：</span>
            </div>
        )
    }
}
export default Bread