import React,{Component} from 'react';
import './index.less'
import { Modal, Form, Input, Checkbox, TreeSelect } from "antd";
const FormItem = Form.Item;
const { TreeNode } = TreeSelect;

class setDeptModal extends Component{
    constructor(props) {
        super(props)
        this.deptData = [];
    }
    // 点击确定
    handleaddsetMeal = (e) => {
        e.preventDefault();
        const { form, type } = this.props;
        form.validateFields((err, values) => {
            if (!err) {
                values.type = type;
                this.props.handleaddsetMeal(values)
            }
        })
    }
    // 点击取消
    handleCancel = () => {
        this.props.handleCancel();
    }
    // 渲染树结构
    renderTreeNodes = (data) => {
        const treeItem = data.map(item => {
            if (item.children && item.children.length) {
                return <TreeNode value={item.bizId} title={item.deptName} key={item.bizId} dataRef={item}>
                    {this.renderTreeNodes(item.children)}
                </TreeNode>
            }
            return <TreeNode value={item.bizId} title={item.deptName} key={item.bizId} />;
        })
        return treeItem
    }
    render(){
        const { setdeptModalShow, form, type, deptDetail, deptData } = this.props;
        const { getFieldDecorator } = form;
        return(
            <Modal
                className="setDeptModal"
                title={type === 0 ? '新增部门' : '修改部门'}
                visible={setdeptModalShow}
                width={500}
                onCancel={this.handleCancel}
                onOk={this.handleaddsetMeal}
            >
                <div className="setRole-box">
                    <Form labelCol={{ span: 7 }} wrapperCol={{ span: 12 }}>
                        <FormItem 
                            label="部门名称">
                            {getFieldDecorator('deptName', {
                                initialValue: deptDetail.deptName,
                                rules: [
                                    { required: true, message: '请输入部门名称' },
                                ],
                            })(
                                <Input placeholder="请输入" />
                            )}
                        </FormItem>
                        <FormItem 
                            label="上级">
                            {getFieldDecorator('parentId', {
                                initialValue: deptDetail.parentId,
                                rules: [
                                    { required: true, message: '请选择上级' },
                                ],
                            })(
                                <TreeSelect placeholder="请选择" allowClear treeDefaultExpandAll onChange={this.onChange}>
                                    <TreeNode value="0" title="父级" />
                                    {this.renderTreeNodes(deptData)}
                                </TreeSelect>
                            )}
                        </FormItem>
                        <FormItem 
                            label="部门代码">
                            {getFieldDecorator('code', {
                                initialValue: deptDetail.code
                            })(
                                <Input placeholder="请输入" />
                            )}
                        </FormItem>
                        <FormItem 
                            label="部门层次">
                            {getFieldDecorator('deptLevel', {
                                initialValue: deptDetail.deptLevel
                            })(
                                <Input placeholder="请输入" />
                            )}
                        </FormItem>
                        <FormItem label="启用">
                            {getFieldDecorator('allowEnable', {
                                valuePropName: 'checked',
                                initialValue: deptDetail['allowEnable'] ? true : false
                            })(
                                <Checkbox>启用</Checkbox>
                            )}
                        </FormItem>
                        <FormItem label="冻结">
                            {getFieldDecorator('allowFreeze', {
                                valuePropName: 'checked',
                                initialValue: deptDetail['allowFreeze'] ? true : false
                            })(
                                <Checkbox>冻结</Checkbox>
                            )}
                        </FormItem>
                        <FormItem label="删除">
                            {getFieldDecorator('allowDelete', {
                                valuePropName: 'checked',
                                initialValue: deptDetail['allowDelete'] ? true : false
                            })(
                                <Checkbox>删除</Checkbox>
                            )}
                        </FormItem>
                        <FormItem 
                            label="排序">
                            {getFieldDecorator('sortCode', {
                                initialValue: deptDetail.sortCode,
                                rules: [
                                    { required: true, message: '请输入排序' },
                                    { pattern: /^\d{1,10}$/, message: '请输入一至十位数字' }
                                ],
                            })(
                                <Input placeholder="请输入" />
                            )}
                        </FormItem>
                    </Form>
                </div>
            </Modal>
        )
    }
}
export default Form.create()(setDeptModal)