import React, { Component } from 'react'
import { Form, Input, Table, Row, Button, message, Col, DatePicker, Switch, Select, Modal } from 'antd';
import {
    HashRouter as Router, Route, Link, withRouter,
} from 'react-router-dom';
import './index.less'
import moment from 'moment'
// import '../../../assets/css/common.less'
import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread'
import FromMaintenance from './maintenanceForm';
import FromConfiguration from './configurationForm';
import FromReportForms from './reportForms';
const { Option } = Select;
const { TextArea } = Input;
const Search = Input.Search;
const { RangePicker } = DatePicker;
const dateFormat = 'YYYY/MM/DD HH:mm:ss'
class onBanner extends Component {
    constructor(props) {
        super(props)
        this.state = {
            selectTime: [],
            listTypeDetailsData: [],//培训类别
            listExamForPlanData: [],//考试试卷
            checkColumns: [],
            disabled: true,
            pageSize: 10,
            page: 1,
            maintenanceVisible: false,//新增 弹窗
            configurationVisible: false,//文件配置 弹窗
            reportFormsVisible: false,//报表 弹窗
            isEdit: false,//判断是否是编辑弹窗
            planDataFiles: [],//待选文件
            planDataFilesed: [],//已选文件
            selectTime: [],
            planData: [],
            reportForm: [],//报表
            reportFormTwo: [],
            reportFormTwoData: "",
            dataFiles: {
                pageSize: 10,
                page: 1,
            },
            dataFilesed: {
                pageSize: 10,
                page: 1,
            },
            breadcrumbData: [
                { name: '培训管理' },
                { name: '培训配置' }
            ],
        }
    }
    componentDidMount() {
        this.setState({
            checkColumns: [{
                title: '类别',
                key: 'trainTypeName',
                width: '200px',
                render: (text, record, index) => {
                    if (record.trainTypeName) {
                        return (
                            <div className='ellipsis'>{record.trainTypeName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '培训名称',
                key: 'trainName',
                render: (text, record, index) => {
                    if (record.trainName) {
                        return (
                            <div className='ellipsis'>{record.trainName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '备注',
                key: 'creator',
                render: (text, record, index) => {
                    if (record.creator) {
                        return (
                            <div className='ellipsis'>{record.creator}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '起始日期',
                key: 'startDate',
                render: (text, record, index) => {
                    if (record.startDate) {
                        return (
                            <div className='ellipsis'>{record.startDate}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '结束日期',
                key: 'endDate',
                render: (text, record, index) => {
                    if (record.endDate) {
                        return (
                            <div className='ellipsis'>{record.endDate}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '启用',
                key: 'isStart',
                render: (text, record, index) => {
                    if (record.isStart === false) {
                        return (
                            <Switch onChange={this.handleTurnOn} disabled={this.state.disabled} checkedChildren="开" unCheckedChildren="关" />
                        )
                    } if (record.isStart === true) {
                        return (
                            <Switch onChange={this.handleTurnOn} disabled={this.state.disabled} checkedChildren="开" unCheckedChildren="关" defaultChecked />
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                    // return (
                    //     <div>
                    //         <Switch onChange={this.handleTurn} checkedChildren="开" unCheckedChildren="关" defaultChecked />
                    //     </div>
                    // )
                },
            }, {
                title: '创建日期',
                key: 'gmtCreated',
                render: (text, record, index) => {
                    if (record.gmtCreated) {
                        return (
                            <div className='ellipsis'>{record.gmtCreated}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '操作',
                key: 'operation',
                dataIndex: 'itemStatus',
                // fixed: 'right',
                width: 310,
                render: (text, record) => {
                    return (
                        <div>
                            <a onClick={this.handelRevise.bind(this, record )}>编辑</a> &nbsp;
                            <a onClick={this.handelDelete.bind(this, record)}>删除</a> &nbsp;
                            <a onClick={this.handelConfiguration.bind(this, record)}>文件配置</a> &nbsp;
                            <a onClick={this.handelPeople}>人员配置</a> &nbsp;
                            {
                                record.isStart === false ?
                                    <a onClick={this.handelRunning.bind(this, record)}>启动</a>
                                    :
                                    null
                            } &nbsp;
                            <a onClick={this.handelReportForms.bind(this, record)}>报表</a>
                        </div>
                    )
                }
            }
            ]
        }, () => {
            this.pageListTrains()
            this.listTypeDetails()//培训类别
            this.listExamForPlan()//考试试卷
        }
        )
    }
    onChange = (e, dateString) => {
        console.log(e, dateString, '555555555555');
        this.setState({
            selectStartTime: dateString
        })
    }
    onChangeTwo = (e, dateString) => {
        console.log(e, dateString, '4444444444');
        this.setState({
            selectEndTime: dateString
        })
    }

    //培训类别
    listTypeDetails = () => {
        Api.listTypeDetails({
            itemsId: 4
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    listTypeDetailsData: res.data.list
                })
            }
        })
    }
    //考试试卷
    listExamForPlan = () => {
        Api.listExamForPlan({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listExamForPlanData: res.data
                })
            }
        })
    }
    //初始化 列表 数据
    pageListTrains() {
        const { pageSize, page } = this.state
        const data = {
            pageSize,
            pageNo: page
        }
        Api.pageListTrains(data, (res) => {
            if (res.code === "200") {
                this.setState({
                    planData: res.data.list
                },
                    message.success("000000000")
                )
            } else {
                message.warning(res.message)
            }
        })
    }
    //启用
    handelRunning = (record) => {
        Api.updateTrain({
            bizId: record.bizId,
            isStart: true,
        }, (res) => {
            if (res.code === "200") {
                message.success(res.message)
            }
            this.pageListTrains()
        })
    }

    //点击启用开关
    handleTurnOff = (checked) => {
        this.setState({
            checked: true
        })
    }
    handleTurnOn = (checked) => {
        this.setState({
            checked: false
        })
    }
    //点击新增 打开弹窗
    addMaintenance = () => {
        this.setState({
            maintenanceVisible: true,
        })
    }
    //新增/编辑 弹窗 点击取消
    maintenanceCancel = () => {
        this.setState({
            maintenanceVisible: false,
            isEdit: false,
        })
    }
    // saveFormRef = (formRef) => {
    //     this.formRef = formRef;
    // }
    //新增/编辑 弹窗 点击确定
    maintenanceCreate = ( bizId ) => {
        const { isEdit } = this.state
        if (isEdit === true ) {
            this.props.form.validateFieldsAndScroll((err, values) => {
                console.log(values,(10).toString(), values.trainDescription, '555555555555')
                if (true) {
                    var num=123123;
                    const ddd = num.toString()
                    const { selectStartTime, selectEndTime } = this.state
                    Api.updateTrain({
                        bizId: bizId,
                        trainTypeId: values.trainTypeId,
                        trainName: values.trainName == undefined ? values.trainName : values.trainName.trim(),
                        trainDescription: values.trainDescription == undefined ? values.trainDescription : values.trainDescription.trim(),
                        examName: values.examName == undefined ? values.examName : values.examName.trim(),
                        examId: values.examId,
                        totalPoint: values.totalPoint == undefined ? values.totalPoint : values.totalPoint.trim(),
                        fileTypeIdName: values.fileTypeIdName,
                        passPoint: values.passPoint == undefined ? values.passPoint : values.passPoint.trim(),
                        readFen: values.readFen == undefined ? (values.readFen).toString() : (values.readFen).toString().trim(),
                        startDate: values.startDate._i,
                        endDate: values.endDate._i,
                    }, (res) => {
                        if (res.code === '200') {
                            this.setState({
                                maintenanceVisible: false,
                                isEdit: false,
                            },
                                message.success("新增成功")
                            )
                            this.pageListTrains()
                        }
                    })
                }
            });

        } else {
            this.props.form.validateFieldsAndScroll((err, values) => {
                console.log(values, '6666666666666666')
                if (true) {
                    const { selectStartTime, selectEndTime } = this.state
                    Api.addTrain({
                        trainTypeId: values.trainTypeId,
                        trainName: values.trainName == undefined ? values.trainName : values.trainName.trim(),
                        trainDescription: values.trainDescription == undefined ? values.trainDescription : values.trainDescription.trim(),
                        examName: values.examName == undefined ? values.examName : values.examName.trim(),
                        examId: values.examId,
                        totalPoint: values.totalPoint == undefined ? values.totalPoint : values.totalPoint.trim(),
                        fileTypeIdName: values.fileTypeIdName,
                        passPoint: values.passPoint == undefined ? values.passPoint : values.passPoint.trim(),
                        readFen: values.readFen == undefined ? values.readFen : values.readFen.trim(),
                        startDate: selectStartTime,
                        endDate: selectEndTime,
                    }, (res) => {
                        if (res.code === '200') {
                            this.setState({
                                maintenanceVisible: false,
                                isEdit: false,
                            },
                                message.success("新增成功")
                            )
                            this.pageListTrains()
                        }
                    })
                }
            });
        }
    }
    //点击编辑 打开弹窗
    handelRevise = (record) => {
        console.log(record,'777777777')
        if (record.isStart === true) {
            message.warning("已启动培训项目不能编辑")
        } else {
            let _bizId = '';
            _bizId = record.bizId
    
            Api.getTrainConfigStatement({
                bizId: record.bizId
            }, (res) => {
                if (res.code === "200") {
                    this.setState({
                        reportFormTwo: res.data,
                        reportFormTwoData: res.data.startDate
                    })
                }
            })
            this.setState({
                bizId: record.bizId,
                maintenanceVisible: true,
                isEdit: true,
            })
        }
    }
    //点击删除
    handelDelete = (record) => {
        Api.deleteTrain({
            bizId: record.bizId,
        }, (res) => {
            if (res.code === "200") {
                this.pageListTrains()
                message.success("删除成功")
            } else {
                message.warning(res.message)
            }
        }
        )

    }
    //点击文件配置 打开弹窗
    handelConfiguration = (record) => {
        if (record.isStart === true) {
            message.warning("已启动培训项目不能文件配置")
        } else {
            this.setState({
                configurationVisible: true,
                filesTrainId: record.bizId
            })
            this.pageListTrainFilesSelect(record)
            this.pageListTrainFilesSelected(record)
        }
    }
    pageListTrainFilesSelect = (record) => {
        const { dataFiles } = this.state
        Api.pageListTrainFilesSelect({
            pageSize: dataFiles.pageSize,
            pageNo: dataFiles.page,
            trainId: record.bizId,
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    planDataFiles: res.data.list
                })
            }
        })

    }

    pageListTrainFilesSelected = (record) => {
        const { dataFilesed } = this.state
        Api.pageListTrainFilesSelected({
            pageSize: dataFilesed.pageSize,
            pageNo: dataFilesed.page,
            trainId: record.bizId,
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    planDataFilesed: res.data.list,
                    recordId: record.bizId
                })
            }
        })
    }

    //文件配置弹窗 点击取消
    configurationCancel = () => {
        this.setState({
            configurationVisible: false,
        })
    }
    //文件配置弹窗 点击确定
    configurationCreate = () => {
        this.setState({
            configurationVisible: false,
        })
    }
    //点击 报表 打开弹窗
    handelReportForms = (record) => {
        Api.getTrainConfigStatement({
            bizId: record.bizId
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    reportForm: res.data
                })
            }
        })
        this.setState({
            reportFormsVisible: true,
        })
    }
    //报表 点击取消
    reportFormsCancel = () => {
        this.setState({
            reportFormsVisible: false,
        })
    }
    //报表 点击确定
    reportFormsCreate = () => {
        this.setState({
            reportFormsVisible: false,
        })
    }
    render() {
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 16 },
        };
        const { getFieldDecorator } = this.props.form;
        const {
            breadcrumbData,
            planData,
            checkColumns,
            maintenanceVisible,
            configurationVisible,
            isEdit,
            reportFormsVisible,
            trainId,
            planDataFiles,
            planDataFilesed,
            recordId,
            filesTrainId,
            reportForm,
            listTypeDetailsData,
            listExamForPlanData,
            reportFormTwo,
            reportFormTwoData,
            bizId
        } = this.state;

        const startTime = moment(reportFormTwo.startDate).format('YYYY/MM/DD HH:mm:ss');
        const endTime = moment(reportFormTwo.endDate).format('YYYY/MM/DD HH:mm:ss');
        console.log(reportFormTwo, reportFormTwoData, "22222222222")
        //培训类别
        const listTypeDetails = listTypeDetailsData ? listTypeDetailsData.map(res => {
            return (<Option key={res.bizId} values={res.bizId}>{res.itemName}</Option>)
        }) : null
        //考试试卷
        const listExamForPlan = listExamForPlanData ? listExamForPlanData.map(res => {
            return (<Option key={res.examId} values={res.examId}>{res.examName}</Option>)
        }) : null
        // const pagination = total > 10 ? {
        //     total: this.state.total,
        //     pageSize: this.state.pageSize,
        //     current: this.state.page,
        //     onChange: this.handleTableChange,
        //     showQuickJumper: true,
        //     showSizeChanger,
        //     onShowSizeChange: this.onShowSizeChange,
        // } : false
        const _title = isEdit ? "培训配置--编辑" : "培训配置--添加"
        return (
            <div className="maintenance">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <Button type="primary" onClick={this.addMaintenance}>新增</Button>
                    <Table
                        style={{ marginTop: '20px' }}
                        columns={checkColumns}
                        dataSource={planData}
                        // pagination={pagination}
                        key="bizId"
                    ></Table>
                    {/* 文件配置 */}
                    <FromConfiguration
                        visible={configurationVisible}
                        onCancel={this.configurationCancel}
                        onCreate={this.configurationCreate}
                        planDataFiles={planDataFiles}
                        planDataFilesed={planDataFilesed}
                        recordId={recordId}
                        filesTrainId={filesTrainId}
                        filesFunction={this.pageListTrainFilesSelect}
                        filesFunctioned={this.pageListTrainFilesSelected}
                    />
                    {/* 报表 */}
                    <FromReportForms
                        visible={reportFormsVisible}
                        onCancel={this.reportFormsCancel}
                        onCreate={this.reportFormsCreate}
                        reportForm={reportForm}
                    />
                    {/* 新增 */}
                    {/* <FromMaintenance
                        wrappedComponentRef={this.saveFormRef}
                        visible={maintenanceVisible}
                        onCancel={this.maintenanceCancel}
                        onCreate={this.maintenanceCreate}
                        isEdit={isEdit}
                    /> */}

                    <Modal
                        visible={maintenanceVisible}
                        title={_title}
                        okText="确定"
                        cancelText='取消'
                        // wrappedComponentRef={this.saveFormRef}
                        onCancel={this.maintenanceCancel}
                        onOk={this.maintenanceCreate.bind(this,bizId)}
                        maskClosable={false}
                    >
                        <Form>
                            <Form.Item
                                label={'培训类别'}
                                {...formItemLayout}
                            >
                                {getFieldDecorator('trainTypeId', {
                                    // initialValue: isEdit ? { label: reportFormTwo.trainTypeName, key: reportFormTwo.trainTypeId } : null,
                                    rules: [{
                                        required: true, message: `请选择培训类别!`,
                                    }],
                                })(
                                    <Select
                                        allowClear
                                        placeholder="请选择"
                                        onChange={this.queryEleList}
                                        getPopupContainer={triggerNode => triggerNode.parentNode}
                                    >
                                        {listTypeDetails}
                                    </Select>
                                )}
                            </Form.Item>
                            <Form.Item
                                label={'培训名称'}
                                {...formItemLayout}
                            >
                                {getFieldDecorator('trainName', {
                                    initialValue: isEdit ? reportFormTwo.trainName : null,
                                    rules: [{
                                        required: true, message: `请输入培训名称!`,
                                    }],
                                })(
                                    <Input style={{ width: 340 }} placeholder="请输入" onKeyDown={this.handleKey} />
                                )}
                            </Form.Item>
                            <Form.Item
                                label={'考试试卷'}
                                {...formItemLayout}
                            >
                                {getFieldDecorator('examId', {
                                    // initialValue: isEdit ? { label: reportFormTwo.trainTypeName, key: reportFormTwo.trainTypeId } : null,
                                    rules: [{
                                        required: true, message: `请选择考试试卷!`,
                                    }],
                                })(
                                    <Select
                                        allowClear
                                        placeholder="请选择"
                                        onChange={this.queryEleList}
                                        getPopupContainer={triggerNode => triggerNode.parentNode}
                                    >
                                        {listExamForPlan}
                                    </Select>
                                )}
                            </Form.Item>
                            <Form.Item
                                label={'及格分数'}
                                {...formItemLayout}
                            >
                                {getFieldDecorator('passPoint', {
                                    initialValue: isEdit ? reportFormTwo.passPoint : null,
                                    rules: [
                                        {
                                            required: true,
                                            message: `请输入及格分数!`,
                                            pattern: new RegExp(/^[0-9]*$/),
                                            message: '只能输入数字'
                                        },
                                        // {
                                        //     max: 100,
                                        //     message: "项目名称不得超过100个字"
                                        // }
                                    ],
                                })(
                                    <Input style={{ width: 340 }} placeholder="请输入" onKeyDown={this.handleKey} />
                                )}
                            </Form.Item>
                            <Form.Item
                                label={'应培训时间'}
                                {...formItemLayout}
                            >
                                {getFieldDecorator('readFen', {
                                    initialValue: isEdit ? reportFormTwo.readFen : null,
                                    rules: [
                                        {
                                            required: true,
                                            message: `请选输入应培训时间!`,
                                            pattern: new RegExp(/^[0-9]*$/),
                                            message: '只能输入数字'
                                        },
                                        // {
                                        //     max: 200,
                                        //     message: "内容不得超过200个字"
                                        // }
                                    ],
                                })(
                                    <Input style={{ width: 340 }} placeholder="请输入" onKeyDown={this.handleKey} />
                                )}
                            </Form.Item>
                            <Form.Item
                                label={'培训内容'}
                                {...formItemLayout}
                            >
                                {getFieldDecorator('trainDescription', {
                                    initialValue: isEdit ? reportFormTwo.trainDescription : null,
                                    rules: [{
                                        required: true, message: `请输入培训内容!`,
                                    }],
                                })(
                                    <TextArea style={{ width: 340, height: 100 }} placeholder="请输入" />
                                )}
                            </Form.Item>
                            <Form.Item
                                label={'开始日期'}
                                {...formItemLayout}
                            >
                                {getFieldDecorator('startDate', {
                                    initialValue: isEdit ? moment(startTime, 'YYYY/MM/DD HH:mm:ss') : null,
                                    rules: [{
                                        required: true, message: `请输入开始日期!`,
                                    }],
                                })(
                                    < DatePicker format={dateFormat} onChange={this.onChange} onClick={() => this.props.GetStates(this.state.dataTime)} />
                                )}
                            </Form.Item>
                            <Form.Item
                                style={{ marginBottom: 0 }}
                                {...formItemLayout}
                                label="结束日期"
                            >
                                {getFieldDecorator('endDate', {
                                    initialValue: isEdit ? moment(endTime, 'YYYY/MM/DD HH:mm:ss') : null,
                                    rules: [{
                                        required: true, message: `请输入结束日期!`,
                                    }
                                    ],
                                })(
                                    <DatePicker format={dateFormat} onChange={this.onChangeTwo} onClick={() => this.props.GetStates(this.state.dataTime)} />
                                )}
                            </Form.Item>
                        </Form>
                    </Modal>

                </div>
            </div>
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner