import React, { Component } from 'react'
import { Form, Input, Table, Row, Button, message, Col, DatePicker, Select, Upload } from 'antd';
import {
    HashRouter as Router, Route, Switch, Link, withRouter,
} from 'react-router-dom';
import './index.less'
// import '../../../assets/css/common.less'
import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread'
import QuestionFromWrap from './questionForm';
const Option = Select
const Search = Input.Search;
const { RangePicker } = DatePicker;
class onBanner extends Component {
    constructor(props) {
        super(props)
        this.state = {
            checkColumns: [],
            questionVisible: false, //点击 新增/编辑 的弹窗
            isEdit: false,//判断 是否为 编辑
            selectTime: [],
            pageSize: 10,
            page: 1,
            reviseData: [],
            showSizeChanger: true,//是否允许改变分页
            planData: [],//表格
            listDetailsOneData: [],//培训类别
            inquiry: false,//控制点击分页时触发的方法
            listDetailsTwoData: [],//试题库类别
            listDetailsThreeData: [],//试题类别
            fileList: [],
            dataFileList: [],
            breadcrumbData: [
                { name: '试题库管理' },
                { name: '试题管理' }
            ],
        }
    }
    componentDidMount() {
        this.setState({
            checkColumns: [{
                title: '试题名称',
                key: 'questionTitle',
                width: '200px',
                render: (text, record, index) => {
                    if (record.questionTitle) {
                        return (
                            <div className='ellipsis'>{record.questionTitle}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '试题类型',
                key: 'questionsTypeName',
                render: (text, record, index) => {
                    if (record.questionsTypeName) {
                        return (
                            <div className='ellipsis'>{record.questionsTypeName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '编写人',
                key: 'modifyUserName',
                render: (text, record) => {
                    if (record.modifyUserName) {
                        return (
                            <div className='ellipsis'>{record.modifyUserName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                }
            }, {
                title: '编写时间',
                key: 'modifyDate',
                render: (text, record, index) => {
                    if (record.modifyDate) {
                        return (
                            <div className='ellipsis'>{record.modifyDate}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '状态',
                key: 'isUsed',
                render: (text, record, index) => {
                    if (record.isUsed === 1) {
                        return (
                            <div className='ellipsis'>已使用</div>
                        )
                    } if (record.isUsed === 0) {
                        return (
                            <div className='ellipsis'>未使用</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '操作',
                key: 'operation',
                dataIndex: 'itemStatus',
                // width: 200,
                render: (text, record) => {
                    return (
                        <div>
                            <a onClick={this.handleEdit.bind(this, record)}>编辑</a> &nbsp;
                            <a onClick={this.handleDelete}>删除</a>
                        </div>
                    )
                }
            }
            ]
        }, () => {
            this.listQuestionsByQuery()//初始化列表
            this.listDetailsByItemsIdForQuestionOne()//培训类别 下拉
            this.listDetailsByItemsIdForQuestionTwo()//试题库类别 下拉
            this.listDetailsByItemsIdForQuestionThree()//试题类别 下拉
        }
        )
    }
    //培训类别 下拉
    listDetailsByItemsIdForQuestionOne = () => {
        Api.listDetailsByItemsIdForQuestion({
            type: "examTypeId"
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    listDetailsOneData: res.data
                })
            }
        })
    }
    //试题库类别 下拉
    listDetailsByItemsIdForQuestionTwo = () => {
        Api.listDetailsByItemsIdForQuestion({
            type: "questionsBankTypeId"
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    listDetailsTwoData: res.data
                })
            }
        })
    }
    //试题类别 下拉
    listDetailsByItemsIdForQuestionThree = () => {
        Api.listDetailsByItemsIdForQuestion({
            type: "questionsTypeId"
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    listDetailsThreeData: res.data
                })
            }
        })
    }
    //初始化列表
    listQuestionsByQuery() {
        const { pageSize, page } = this.state
        Api.listQuestionsByQuery({
            pageSize,
            pageNo: page
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    planData: res.data.list,
                    total: res.data.total,
                })
                // message.success(res.message)
            } else {
                message.warning(res.message)
            }
        })
    }
    //点击查询
    handleSearchPeople = () => {
        this.setState({
            page: 1,
        }, () => {
            this.props.form.validateFieldsAndScroll((err, values) => {
                console.log(values, "55555555555")
                if (true) {
                    const { pageSize, page } = this.state;
                    this.setState({
                        deviceLoading: true
                    })
                    Api.pageListMyFiles({
                        pageSize,
                        pageNo: page,
                        examTypeId: values.examTypeId,
                        questionsBankTypeId: values.questionsBankTypeId,
                        questionsTypeId: values.questionsTypeId,
                        questionTitle: values.startTime == undefined ? values.startTime : values.startTime.trim(),
                    }, (res) => {
                        if (res.code === '200') {
                            this.setState({
                                page,
                                inquiry: true,
                                total: res.data.total,
                                planData: res.data.list
                            })
                        }
                    })
                }
            });
        })
    }
    //管理库列表分页
    handleTableChange = (page) => {
        const { inquiry } = this.state
        this.setState({
            page,
            inquiry: false
        }, () => {
            {
                inquiry === false ?
                    this.listQuestionsByQuery()
                    :
                    this.props.form.validateFieldsAndScroll((err, values) => {
                        if (true) {
                            const { pageSize, page } = this.state;
                            Api.pageListMyFiles({
                                pageSize,
                                pageNo: page,
                                examTypeId: values.examTypeId,
                                questionsBankTypeId: values.questionsBankTypeId,
                                questionsTypeId: values.questionsTypeId,
                                questionTitle: values.startTime == undefined ? values.startTime : values.startTime.trim(),
                            }, (res) => {
                                if (res.code === '200') {
                                    this.setState({
                                        inquiry: true,
                                        page,
                                        total: res.data.total,
                                        planData: res.data.list
                                    })
                                }
                            })
                        }
                    })
            }
        })
    }
    //点击 重置
    handleReset = () => {
        this.props.form.resetFields();
        this.listQuestionsByQuery()
    }
    //点击新增 打开弹窗
    handleAdd = () => {
        this.setState({
            questionVisible: true
        })
    }
    //点击编辑 打开弹窗
    handleEdit = (record) => {
        Api.getQuestionById({
            bizId: record.bizId,
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    reviseData: res.data,
                })
            }
        })
        this.setState({
            questionVisible: true,
            isEdit: true,
        })
    }
    //点击 取消
    processCan = () => {
        this.setState({
            questionVisible: false
        })
    }
    saveFormRef = (formRef) => {
        this.formRef = formRef;
    }
    //点击 确定
    processNo = () => {
        const form = this.formRef.props.form;
        form.validateFields((err, values) => {
            console.log(values, '777777777777')
            if (err) {
                return;
            }
            Api.saveQuestion({
                questionTitle: values.questionTitle == undefined ? values.questionTitle : values.questionTitle.trim(),
                examTypeId: values.examTypeId,
                questionsTypeId: values.questionsTypeId,
                questionsBankTypeId: values.questionsBankTypeId,
                questionA: values.questionA == undefined ? values.questionA : values.questionA.trim(),
                questionB: values.questionB == undefined ? values.questionB : values.questionB.trim(),
                questionC: values.questionC == undefined ? values.questionC : values.questionC.trim(),
                questionD: values.questionD == undefined ? values.questionD : values.questionD.trim(),
                questionE: values.questionE == undefined ? values.questionE : values.questionE.trim(),
                questionF: values.questionF == undefined ? values.questionF : values.questionF.trim(),
                answer: values.answer == undefined ? values.answer : values.answer.trim(),
            }, (res) => {
                if (res.code === '200') {
                    form.resetFields();
                    this.setState({
                        questionVisible: false
                    }, () => {
                        this.listQuestionsByQuery()
                    });
                    message.success('新增成功')
                } else {
                    // message.error('检查项目已存在')
                }
            })
        });
    }

        // 文件上传
        handleChange = (info) => {
            //把fileList拿出来
            let { fileList } = info;
            const status = info.file.status;
            if (status) {
                if (status === 'done') {
                    fileList = fileList.slice(-1);
                    if (fileList[0].response.code === '200') {
                        this.setState({
                            current: 1
                        }, () => {
                            this.setState({
                                leadingVisible: true,
                                dataFileList: fileList[0].response.data
                            })
                            this.listQuestionsByQuery();
                        })
                        // message.success(fileList[0].response.message);
                    } else if (fileList[0].response.code === '500') {
                        this.setState({
                            current: 1
                        }, () => {
                            this.setState({
                                leadingVisible: true,
                                dataFileList: fileList[0].response.data
                            })
                            this.listQuestionsByQuery();
                        })
                        // message.error(fileList[0].response.message)
                    } else if (fileList[0].response.code === '40000') {
                        message.warning(fileList[0].response.message);
                    }
                } else if (status === 'error') {
                    message.error(`${info.file.name} 上传失败`);
                }
                //重新设置state
                this.setState({ fileList });
            }
        }
        //导入
        beforeUpload = (file) => {
            let nameArr = file.name.split('.');
            let name = nameArr[nameArr.length - 1];
            // 校验 格式
            if (['xlsx', 'xls'].indexOf(name) === -1) {
                // message.error('只支持 .xlsx 文件');
                return;
            }
            const isLt = file.size / 1024 / 1024 < 100;
            if (!isLt) {
                message.error('文件过大');
            }
            return isLt;
        }
    
    render() {
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 16 },
        };
        const { getFieldDecorator } = this.props.form;
        const {
            breadcrumbData,
            planData,
            checkColumns,
            questionVisible,
            isEdit,
            listDetailsOneData,
            listDetailsTwoData,
            listDetailsThreeData,
            total,
            showSizeChanger,
            reviseData
        } = this.state;
        console.log(reviseData, '45556565')
        //培训类别 下拉
        const listDetailsOne = listDetailsOneData ? listDetailsOneData.map(res => {
            return (<Option key={res.itemId} values={res.itemId}>{res.itemName}</Option>)
        }) : null
        //试题库类别 下拉
        const listDetailsTwo = listDetailsTwoData ? listDetailsTwoData.map(res => {
            return (<Option key={res.itemId} values={res.itemId}>{res.itemName}</Option>)
        }) : null
        //试题类别 下拉
        const listDetailsThree = listDetailsThreeData ? listDetailsThreeData.map(res => {
            return (<Option key={res.itemId} values={res.itemId}>{res.itemName}</Option>)
        }) : null

        const uploadProps = {
            action: `/question/upload`,
            onChange: this.handleChange,
            multiple: false,
            showUploadList: false,
            beforeUpload: this.beforeUpload,
            headers: {
                menuUrl: window.location.pathname
            }
        };
        const pagination = total > 10 ? {
            total: this.state.total,
            pageSize: this.state.pageSize,
            current: this.state.page,
            onChange: this.handleTableChange,
            showQuickJumper: true,
            showSizeChanger,
            onShowSizeChange: this.onShowSizeChange,
        } : false
        return (
            <div className="feedBcak">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="p-search">
                        <Form style={{ position: 'relative', top: -3 }}>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="培训类别"
                                    >
                                        {getFieldDecorator('examTypeId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listDetailsOne}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="题库类别"
                                    >
                                        {getFieldDecorator('questionsBankTypeId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listDetailsTwo}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="试题类别"
                                    >
                                        {getFieldDecorator('questionsTypeId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listDetailsThree}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row style={{ position: 'relative', top: '-10px' }}>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="名称"
                                    >
                                        {getFieldDecorator('startTime', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6} style={{}}>
                                    <Button type="primary" htmlType="submit" onClick={this.handleSearchPeople} style={{ marginRight: 20 }}>查询</Button>
                                    <Button onClick={this.handleReset}>重置</Button>
                                </Col>
                            </Row>
                        </Form>
                    </div>
                    <div><Button type="primary" onClick={this.handleAdd}>新增</Button>
                        <Upload {...uploadProps} fileList={this.state.fileList}>
                            {<a style={{ marginRight: 20 }}>
                                导入</a>}
                        </Upload>
                        <a style={{ marginLeft: '20px' }}>试题库导入模板.xls</a> <a style={{ marginLeft: '20px' }}>下载</a></div>
                    <div style={{ position: "relative", top: '50px' }}>
                        <Table
                            style={{ marginTop: '-12px', }}
                            columns={checkColumns}
                            dataSource={planData}
                            pagination={pagination}
                            key="bizId"
                        ></Table>
                    </div>

                    {/* 新增/编辑 */}
                    <QuestionFromWrap
                        wrappedComponentRef={this.saveFormRef}
                        visible={questionVisible}
                        onCancel={this.processCan}
                        onCreate={this.processNo}
                        isEdit={isEdit}
                        listDetailsOne={listDetailsOne}
                        listDetailsTwo={listDetailsTwo}
                        listDetailsThree={listDetailsThree}
                        reviseData={reviseData}
                    />
                </div>
            </div>
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner