import React, { Component } from 'react'
import { Form, Input, Table, Row, Button, message, Col, DatePicker, Select } from 'antd';
import {
    HashRouter as Router, Route, Switch, Link, withRouter,
} from 'react-router-dom';
import './index.less'
// import '../../../assets/css/common.less'
import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread'
const Option = Select
const Search = Input.Search;
const { RangePicker } = DatePicker;
class onBanner extends Component {
    constructor(props) {
        super(props)
        this.state = {
            pageSize: 10,
            page: 1,
            planData: [],
            checkColumns: [],
            selectTime: [],
            writtenData: [],//编写人
            deptData: [],//接收部门
            listYearsData: [],//发布年度
            listFileTypesData: [],//文件类别
            listRolesInFileModelData: [],//分配角色下拉    
            listApplyDeptInFileModelData: [],//编写部门下拉
            breadcrumbData: [
                { name: '文件管理' },
                { name: '我的文档' }
            ],
        }
    }
    componentDidMount() {
        this.setState({
            checkColumns: [{
                title: '文件编号',
                key: 'fileNo',
                width: '200px',
                render: (text, record, index) => {
                    if (record.fileNo) {
                        return (
                            <div className='ellipsis'>{record.fileNo}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '文件名称',
                key: 'fileName',
                render: (text, record, index) => {
                    if (record.fileName) {
                        return (
                            <div className='ellipsis'>{record.fileName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '附件',
                key: 'fileContent',
                render: (text, record) => {
                    if (record.fileContent) {
                        return (
                            <div>
                                <a onClick={this.lookFujian}>查看附件</a> &nbsp;
                            </div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                }
            }, {
                title: '版本/修订号',
                key: 'fileVersion',
                render: (text, record, index) => {
                    if (record.fileVersion) {
                        return (
                            <div className='ellipsis'>{record.fileVersion}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '生效日期',
                key: 'effectDate',
                render: (text, record, index) => {
                    if (record.effectDate) {
                        return (
                            <div className='ellipsis'>{record.effectDate}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '文件类别',
                key: 'fileTypeIdName',
                render: (text, record, index) => {
                    if (record.fileTypeIdName) {
                        return (
                            <div className='ellipsis'>{record.fileTypeIdName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '编写部门',
                key: 'applyDeptName',
                render: (text, record, index) => {
                    if (record.applyDeptName) {
                        return (
                            <div className='ellipsis'>{record.applyDeptName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '编写人',
                key: 'applyUserName',
                render: (text, record, index) => {
                    if (record.applyUserName) {
                        return (
                            <div className='ellipsis'>{record.applyUserName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, , {
                title: '审核人',
                key: 'approverUserName',
                render: (text, record, index) => {
                    if (record.approverUserName) {
                        return (
                            <div className='ellipsis'>{record.approverUserName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '审批人',
                key: 'auditorName',
                render: (text, record, index) => {
                    if (record.auditorName) {
                        return (
                            <div className='ellipsis'>{record.auditorName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '操作',
                key: 'operation',
                dataIndex: 'itemStatus',
                // width: 200,
                render: (text, record) => {
                    return (
                        <div>
                            <a onClick={this.applicationAmendment}>下载文件</a> &nbsp;
                        </div>
                    )
                }
            }
            ]
        }, () => {
            this.pageListMyFiles()//信息 列表
            this.listUsers()//编写人下拉
            this.listReceiveDept()//接收部门下拉
            this.listYears()//发布年度下拉
            this.listFileTypes()//文件类别下拉
            this.listRolesInFileModel()//分配角色下拉
            this.listApplyDeptInFileModel()//编写部门下拉
        }
        )
    }

    //编写人
    listUsers = () => {
        Api.listUsers({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    writtenData: res.data.list,
                })
            }
        })
    }
    //接收部门
    listReceiveDept = () => {
        Api.listReceiveDept({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    deptData: res.data.list,
                })
            }
        })
    }
    //发布年度下拉
    listYears = () => {
        Api.listYears({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listYearsData: res.data.list,
                })
            }
        })
    }
    //文件类别下拉
    listFileTypes = () => {
        Api.listFileTypes({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listFileTypesData: res.data.list,
                })
            }
        })
    }
    //分配角色下拉
    listRolesInFileModel = () => {
        Api.listRolesInFileModel({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listRolesInFileModelData: res.data.list,
                })
            }
        })
    }
    //编写部门下拉
    listApplyDeptInFileModel = () => {
        Api.listApplyDeptInFileModel({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listApplyDeptInFileModelData: res.data.list
                })
            }
        })
    }
    //点击新增WORD
    addWord = () => {
        this.props.history.push()
    }
    //选择时间后的回调
    onTimeChange = (e, dateString) => {
        this.setState({
            selectTime: dateString
        })
    }
    //初始化 列表
    pageListMyFiles() {
        const { pageSize, page } = this.state
        const data = {
            pageSize,
            pageNo: page,
        }
        Api.pageListMyFiles(data, (res) => {
            if (res.code === "200") {
                this.setState({
                    planData: res.data.list
                },
                    message.success(res.message)
                )
            } else {
                message.waring(res.message)
            }
        })
    }
    //点击查询
    handleSearchPeople = () => {
        this.setState({
            page: 1,
        }, () => {
            this.props.form.validateFieldsAndScroll((err, values) => {
                if (true) {
                    const { pageSize, page } = this.state;
                    this.setState({
                        deviceLoading: true
                    })
                    Api.pageListMyFiles({
                        pageSize,
                        pageNo: page,
                        applyDeptName: values.applyDeptName == undefined ? values.applyDeptName : values.applyDeptName.trim(),
                        applyDeptId: values.applyDeptId,
                        fileNo: values.fileNo == undefined ? values.fileNo : values.fileNo.trim(),
                        fileName: values.fileName == undefined ? values.fileName : values.fileName.trim(),
                        applyUser: values.applyUser == undefined ? values.applyUser : values.applyUser.trim(),
                        auditorName: values.auditorName == undefined ? values.auditorName : values.auditorName.trim(),
                        fileTypeId: values.fileTypeId,
                        receiveDeptId: values.receiveDeptId,
                        roleId: values.roleId,
                        applyYear: values.applyYear,
                    }, (res) => {
                        if (res.code === '200') {
                            this.setState({
                                page,
                                total: res.data.total,
                                planData: res.data.list
                            })
                        }
                    })
                }
            });
        })
    }
    //点击 重置
    handleReset = () => {
        this.props.form.resetFields();
        this.handleSearchPeople()
    }
    render() {
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 16 },
        };
        const { getFieldDecorator } = this.props.form;
        const {
            breadcrumbData,
            planData,
            checkColumns,
            writtenData,
            deptData,
            listYearsData,
            listFileTypesData,
            listRolesInFileModelData,
            listApplyDeptInFileModelData
        } = this.state;
        //编写人
        const writtenBy = writtenData ? writtenData.map(res => {
            return (<Option key={res.userId} values={res.userId}>{res.userName}</Option>)
        }) : null
        //接收部门
        const departmentData = deptData ? deptData.map(res => {
            return (<Option key={res.receiveDeptId} values={res.receiveDeptId}>{res.receiveDeptName}</Option>)
        }) : null
        //发布年度
        const listYears = listYearsData ? listYearsData.map(res => {
            return (<Option key={res.applyYear} values={res.applyYear}>{res.applyYear}</Option>)
        }) : null
        //文件类别
        const listFileTypes = listFileTypesData ? listFileTypesData.map(res => {
            return (<Option key={res.fileTypeId} values={res.fileTypeId}>{res.fileTypeIdName}</Option>)
        }) : null
        //分配角色
        const listRolesInFileModel = listRolesInFileModelData ? listRolesInFileModelData.map(res => {
            return (<Option key={res.roleId} values={res.roleId}>{res.roleName}</Option>)
        }) : null
        //编写部门
        const listApplyDeptInFileModel = listApplyDeptInFileModelData ? listApplyDeptInFileModelData.map(res => {
            return (<Option key={res.applyDeptId} values={res.applyDeptId}>{res.applyDeptName}</Option>)
        }) : null
        // const pagination = total > 10 ? {
        //     total: this.state.total,
        //     pageSize: this.state.pageSize,
        //     current: this.state.page,
        //     onChange: this.handleTableChange,
        //     showQuickJumper: true,
        //     showSizeChanger,
        //     onShowSizeChange: this.onShowSizeChange,
        // } : false
        return (
            <div className="feedBcak">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="p-search">
                        <Form style={{ position: 'relative', top: -3 }}>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件编号"
                                    >
                                        {getFieldDecorator('fileNo', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="编写部门"
                                    >
                                        {getFieldDecorator('applyDeptId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listApplyDeptInFileModel}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="编写人"
                                    >
                                        {getFieldDecorator('applyUserName', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {writtenBy}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row style={{ position: 'relative', top: '-10px' }}>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件名称"
                                    >
                                        {getFieldDecorator('fileName', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="接收部门"
                                    >
                                        {getFieldDecorator('receiveDeptId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {departmentData}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="审批人"
                                    >
                                        {getFieldDecorator('auditorName', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row style={{ position: 'relative', top: '-10px' }}>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件类别"
                                    >
                                        {getFieldDecorator('fileTypeId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listFileTypes}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="分配角色"
                                    >
                                        {getFieldDecorator('roleId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listRolesInFileModel}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="发布年度"
                                    >
                                        {getFieldDecorator('applyYear', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listYears}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6} style={{}}>
                                    <Button onClick={this.handleReset} style={{ float: 'right' }}>重置</Button>
                                    <Button type="primary" htmlType="submit" onClick={this.handleSearchPeople} style={{ marginRight: 20, float: 'right' }}>查询</Button>
                                </Col>
                            </Row>
                        </Form>
                    </div>
                    <Table
                        style={{ marginTop: '-12px', marginLeft: '20px' }}
                        columns={checkColumns}
                        dataSource={planData}
                        // pagination={pagination}
                        key="bizId"
                    ></Table>

                </div>
            </div>
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner