import React,{Component} from 'react';
import './index.less'
import { Modal, Form, Input } from "antd";
const FormItem = Form.Item;

class passwordModal extends Component{
    // 点击确定
    handleaddsetMeal = (e) => {
        e.preventDefault();
        const { form } = this.props;
        form.validateFields((err, values) => {
            if (!err) {
                this.props.handleaddsetPasswordMeal(values)
            }
        })
    }
    // 点击取消
    handleCancel = () => {
        this.props.handlePasswordCancel();
    }
    render(){
        const { newPasswordModalShow, form, userDetail } = this.props;
        const { getFieldDecorator } = form;
        return(
            <Modal
                className="passwordModal"
                title='重置密码'
                visible={newPasswordModalShow}
                width={420}
                onCancel={this.handleCancel}
                onOk={this.handleaddsetMeal}
            >
                <div className="password-box">
                    <Form labelCol={{ span: 5 }} wrapperCol={{ span: 17 }}>
                        <FormItem 
                            label="账号">
                            {getFieldDecorator('account', {
                                initialValue: userDetail.account,
                                rules: [
                                    { required: true, message: '请输入账号' },
                                ],
                            })(
                                <Input readOnly placeholder="请输入" />
                            )}
                        </FormItem>
                        <FormItem label="新密码">
                            {getFieldDecorator('passWord', {
                                initialValue: userDetail.passWord,
                                rules: [
                                    { required: true, message: '请输入密码' },
                                ],
                            })(
                                <Input type="password" placeholder="请输入" />
                            )}
                        </FormItem>
                    </Form>
                </div>
            </Modal>
        )
    }
}
export default Form.create()(passwordModal)