/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { Component } from 'react';
import { Table, Button, Popconfirm } from 'antd';

class tableHostList extends Component {
    constructor(props) {
        super(props)
        this.columns = [{
            title: '备份文件文件名',
            dataIndex: 'sqlFileName',
          },{
            title: '文件大小',
            dataIndex: 'sqlFileSize'
          },{
            title: '备份日期',
            dataIndex: 'backUpDate'
          },{
            title: '文件url',
            dataIndex: 'sqlUrl'
          },{
            title: '操作',
            dataIndex: 'operation',
            width: 100,
            render: (text, record) =>
              <div>
                    <Popconfirm title="确定要删除该数据吗" onConfirm={this.handleDelete.bind(this,record.bizId)}>
                        <a className="operation">删除</a>
                    </Popconfirm>
              </div>
              
          }]
    }
    handleChangePage = (pageNo, pageSize) => {
        this.props.handleChangePage(pageNo, pageSize)
    }
    onShowSizeChange = (pageNo, pageSize) => {
        this.props.handleChangePage(1, pageSize)
    }
    handleDelete = (bizId) => {
        this.props.handleDelete(bizId);
    }
    render() {
        return (
            <div className="hostTable">
                <div className="btn">
                    <Button type="primary" onClick={this.props.handleUpdate}>刷新</Button>
                    <Button type="primary" onClick={this.props.handleExportSql}>备份</Button>
                    <Button type="primary" onClick={this.props.handleSetHost}>定时备份</Button>
                </div>
                <Table
                    rowKey={(record) => { return record.bizId }}
                    loading={this.props.loading}
                    columns={this.columns} 
                    dataSource={this.props.data}
                    pagination={{
                        hideOnSinglePage: true,
                        showSizeChanger: true,
                        showQuickJumper: true,
                        current: this.props.pageNo,
                        total: this.props.total,
                        pageSize: this.props.pageSize,
                        onChange: this.handleChangePage,
                        onShowSizeChange: this.onShowSizeChange,
                    }}
                />
            </div>
        )
    }
}
export default tableHostList