import React, { Component } from 'react';
import { Table } from 'antd';

class tableList extends Component {
    constructor(props) {
        super(props)
        this.state= {
          current: 2,
        }
        this.columns = [
          {
            title: '日期/时间',
            dataIndex: 'time'
          },{
            title: '操作类型',
            dataIndex: 'name'
          },{
            title: '操作数据表',
            dataIndex: 'dept'
          },{
            title: '对象主键值',
            dataIndex: 'role'
          },{
            title: '操作模块',
            dataIndex: 'phone'
          },{
            title: 'IP地址',
            dataIndex: 'email'
          },{
            title: '操作用户',
            dataIndex: 'cishu'
          },{
            title: '查询详细',
            dataIndex: 'operation',
            width: 100,
            render: (text, record) =>
              <div>
                <a className="operation" href="javascripy;">查看</a>
              </div>
              
          },
        ]
    }
    handleChangePage = (pageNo, pageSize) => {
      this.props.handleChangePage(pageNo, pageSize)
      this.setState({
        current: pageNo
      })
    }
    handleDelete = (code) => {
      this.props.handleDelete(code)
    }
    onChange = () => {

    }
    handleDelete = (code) => {
      this.props.handleDelete(code)
    }
    onShowSizeChange = (pageNo, pageSize) => {
      console.log(pageNo, pageSize);
      this.props.handleChangePage(1, pageSize)
      this.setState({
        current: 1
      })
    }
    render() {
        return (
          <Table
            rowKey={(record) => { return `${Math.random(10)}` }}
            loading={this.props.loading}
            columns={this.columns} 
            dataSource={this.props.data}
            pagination={{
              hideOnSinglePage: true,
              showSizeChanger: true,
              showQuickJumper: true,
              current: this.state.current,
              total: 100,
              pageSize: 10,
              onChange: this.handleChangePage,
              onShowSizeChange: this.onShowSizeChange,
            }}
          />
        )
    }
}
export default tableList