var service=axios.create({
    baseURL:process.env.BASE_API,
    timeout: 10000
})

 //添加请求拦截器
service.interceptors.request.use(function(config){
    // if(store.getters.token){
    //     config.headers['TOKEN']=getCookie('TOKEN')
    // }
    config.headers.menuUrl = window.location.pathname;
    // config
    return config
},function(error){
    return Promise.reject(error)
})

//添加响应拦截器
service.interceptors.response.use(function(response){
    
    /**
    * 下面的注释为通过在response里，自定义code来标示请求状态
    * 当code返回如下情况则说明权限有问题，登出并返回到登录页
    * 如想通过xmlhttprequest来状态码标识 逻辑可写在下面error中
    */
   // response => {
   //   const res = response.data
   //   if (res.code !== 20000) {
   //     })
   //     // 50008:非法的token; 50012:其他客户端登录了;  50014:Token 过期了;
   //     if (res.code === 50008 || res.code === 50012 || res.code === 50014) {
   //       }).then(() => {
   //         })
   //       })
   //     }
   //     return Promise.reject('error')
   //   } else {
   //     return response.data
   //   }
   // },
        return response
    },function(error){


        message.warning(error.message);
        return Promise.reject(error)
    }
 )

class Http {
    /* eslint-disable */
    get(url, params, callback) { // GET请求
        const newUrl = params ? this.build(url, params) : url
        return this.request(newUrl, {
            method: 'GET',
            
        }, callback)
    }

    post(url, body, callback) { // POST请求
        let options = {
            method: 'POST'
        }
        if (body) options.body = JSON.stringify( body);
        return this.request(url, options, callback,0)
    }
    downloadFile(url,params, callback){ // 下载文件
        const newUrl = params ? this.build(url, params) : url
        // let requestData = Object.assign({}, data, {
        //     accessToken: sessionStorage.getItem('accessToken')
        //   });
        axios.get(newUrl, {
                responseType: 'blob',
                headers: {
                    menuUrl: window.location.pathname
                }
            }).then(resp => {
            let headers = resp.headers;
            let contentType = headers['content-type'];
      
            // console.log('响应头信息', headers);
            if (!resp.data) {
            //   console.error('响应异常：', resp);
              return false;
            } else {
            //   console.error('下载文件：', resp);
              const blob = new Blob([resp.data], {type: contentType});
      
              const contentDisposition = resp.headers['content-disposition'];
              let fileName = 'unknown';
              if (contentDisposition) {
                fileName = window.decodeURI(resp.headers['content-disposition'].split('=')[1]);
              }
            //   console.log('文件名称：', fileName);
            //   callback(blob, fileName)
              this.downFile(blob, fileName);
            }
        }).catch(function (error) {
            console.log(error);
        });
    }
    downFile(blob, fileName) {
        // 非IE下载
        if ('download' in document.createElement('a')) {
          let link = document.createElement('a');
          link.href = window.URL.createObjectURL(blob); // 创建下载的链接
          link.download = fileName; // 下载后文件名
          link.style.display = 'none';
          document.body.appendChild(link);
          link.click(); // 点击下载
          window.URL.revokeObjectURL(link.href); // 释放掉blob对象
          document.body.removeChild(link); // 下载完成移除元素
        } else {
          // IE10+下载
          window.navigator.msSaveBlob(blob, fileName);
        }
    }
    // postRes(url, body, callback,resCallBack) { // POST请求
    //     let options = {
    //         method: 'POST'
    //     }
    //     if (body) options.body = JSON.stringify(body)
    //     return this.request(url, options, callback,1)
    // }
    // postAsFormData(url, params, callback) {
    //     let options = {
    //         method: 'POST'
    //     }
    //     if (params) options.body = this.buildFormData(params)
    //     return this.request(url, options, callback)
    // }

    // postForm(url, form, callback) {
    //     let options = {
    //         method: 'POST'
    //     }
    //     if (form) options.body = new FormData(form)
    //     return this.request(url, options, callback)
    // }

    request(url, options, callback,resCallBack) {
        const self = this;
        // options.credentials = 'same-origin'
        options.headers = this.defaultHeader()//默认headers
        return service({
                method: options.method,
                url: url,
                data: options.body,
                headers: {
                    'Content-Type':'application/json;charset=UTF-8'
                }
            }).then(function(response) {
                //模拟数据
                //请求返回处理
                // if (!response) {
                //     throw "服务器返回参数错误"
                // } else if (response.data && response.data.code === '200'){
                //     //处理数据
                //     if(!resCallBack){
                //         callback(response.data)
                //     }else{
                //         callback(response);
                //     }
                // }else if(response.data && response.data.code === '40001'){
                //     message.warning(response.result_message);
                // }else if (response.result == false){
                //     //错误的情况
                //     message.warning(response.result_message);

                // } else if (response.errcode == 40001) {

                //     // throw "token失效，请刷新页面"
                // } else if (response.errcode == -1) {

                //     return response
                // }
                if(!response){
                    throw "服务器返回参数错误"
                }else if((typeof(response.data) === 'string')){
                     // message.warning(response.data.message);
                    //  console.log(typeof(response.data) === 'string' && response.data.indexOf('10001'> -1))
                     setTimeout(() => {
                        history.push('/');
                    },500)
                }else if(response.data && response.status === 200){
                    // 用户未登录
                    if(response.data.code === '10001'){
                        message.warning(response.data.message);
                        setTimeout(() => {
                            history.push('/');
                        },500)

                    // 40000: 参数异常; 40001: 参数为空; 40002: 参数超长;
                    }else if(response.data.code === '200') {
                        callback(response.data)
                    }else{
                        message.warning(response.data.message);
                    }
                }
            })
            .catch(err => console.error(err))
    }
    
    defaultHeader() { // 默认头
        const header = {
            'Accept': '*/*',
            'Content-Type': 'application/json',
        };
        return header
    }

    build(url, params) { // URL构建方法
        const ps = []
        if (params) {
            for (let p in params) {
                if (p) {
                    ps.push(p + '=' + encodeURIComponent(params[p]));
                }
            }
        }
        return url + '?' + ps.join('&')
    }

    buildFormData(params) {
        if (params) {
            const data = new FormData()
            for (let p in params) {
                if (p) {
                    data.append(p, params[p])
                }
            }
            return data;
        }
    }
}
/* eslint-disable */
export default new Http()