import React, { Component } from 'react'
import { Form, Input, Table, Row, Button, message, Col, DatePicker, Select } from 'antd';
import {
    HashRouter as Router, Route, Switch, Link, withRouter,
} from 'react-router-dom';
import './index.less'
// import '../../../assets/css/common.less'
import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread'
const Search = Input.Search;
const { Option } = Select;
const { RangePicker } = DatePicker;
class onBanner extends Component {
    constructor(props) {
        super(props)
        this.state = {
            checkColumns: [],
            listFileTypesData: [],//文件类别
            listApplyDeptInFileModelData: [],//编写部门下拉
            writtenData: [],//编写人
            deptData: [],//接收部门
            planData: [],//列表
            selectTime: [],
            breadcrumbData: [
                { name: '文件管理' },
                { name: '文件接收' }
            ],
        }
    }
    componentDidMount() {
        this.setState({
            checkColumns: [{
                title: '文件编号',
                key: 'fileNo',
                width: '200px',
                render: (text, record, index) => {
                    if (record.fileNo) {
                        return (
                            <div className='ellipsis'>{record.fileNo}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '文件名称',
                key: 'fileName',
                render: (text, record, index) => {
                    if (record.fileName) {
                        return (
                            <div className='ellipsis'>{record.fileName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '编写部门',
                key: 'applyDeptName',
                render: (text, record, index) => {
                    if (record.applyDeptName) {
                        return (
                            <div className='ellipsis'>{record.applyDeptName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '接收部门',
                key: 'receiveDeptName',
                render: (text, record, index) => {
                    if (record.receiveDeptName) {
                        return (
                            <div className='ellipsis'>{record.receiveDeptName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '附件',
                key: 'fileContent',
                render: (text, record, index) => {
                    if (record.fileContent) {
                        return (
                            <div className='ellipsis'>{record.fileContent}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '是否接收',
                key: 'isReceived',
                render: (text, record, index) => {
                    if (record.isReceived === false) {
                        return (
                            <div className='ellipsis'>否</div>
                        )
                    } if (record.isReceived === true) {
                        return (
                            <div className='ellipsis'>是</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '接收时间',
                key: 'receiveDate',
                render: (text, record, index) => {
                    if (record.receiveDate) {
                        return (
                            <div className='ellipsis'>{record.receiveDate}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '接收人',
                key: 'receiverName',
                render: (text, record, index) => {
                    if (record.receiverName) {
                        return (
                            <div className='ellipsis'>{record.receiverName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '操作',
                key: 'operation',
                dataIndex: 'itemStatus',
                // width: 200,
                render: (text, record) => {
                    return (
                        <div>
                            <a onClick={this.receivingCan.bind(this, record)}>确定接收</a> &nbsp;
                            {/* <a onClick={this.details}>查看文件</a> */}
                        </div>
                    )
                }
            }
            ]
        }, () => {
            this.pageListForReceive()
            this.listFileTypes()//文件类别下拉
            this.listApplyDeptInFileModel()//编写部门下拉
            this.listUsers()//编写人下拉
            this.listReceiveDept()//接收部门下拉
        }
        )
    }
    //初始化 列表 数据
    pageListForReceive() {
        const { pageSize, page } = this.state
        Api.pageListForReceive({
            pageSize,
            pageNo: page
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    total: res.data.total,
                    planData: res.data.list
                })
            }
        })
    }
    //点击查询
    handleSearchPeople = () => {
        this.setState({
            page: 1,
        }, () => {
            this.props.form.validateFieldsAndScroll((err, values) => {
                if (true) {
                    const { pageSize, page } = this.state;
                    this.setState({
                        deviceLoading: true
                    })
                    Api.pageListForReceive({
                        pageSize,
                        pageNo: page,
                        receiveDeptId: values.receiveDeptId,
                        userId: values.userId,
                        applyDeptId: values.applyDeptId,
                        fileTypeId: values.fileTypeId,
                        fileNo: values.fileNo == undefined ? values.fileNo : values.fileNo.trim(),
                        fileName: values.fileName == undefined ? values.fileName : values.fileName.trim(),
                    }, (res) => {
                        if (res.code === '200') {
                            this.setState({
                                page,
                                total: res.data.total,
                                planData: res.data.list
                            })
                        }
                    })
                }
            });
        })
    }
    //点击 重置
    handleReset = () => {
        this.props.form.resetFields();
        this.pageListForReceive()
    }
    //文件类别下拉
    listFileTypes = () => {
        Api.listFileTypes({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listFileTypesData: res.data.list,
                })
            }
        })
    }
    //编写部门下拉
    listApplyDeptInFileModel = () => {
        Api.listApplyDeptInFileModel({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listApplyDeptInFileModelData: res.data.list
                })
            }
        })
    }
    //编写人
    listUsers = () => {
        Api.listUsers({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    writtenData: res.data.list,
                })
            }
        })
    }
    //接收部门
    listReceiveDept = () => {
        Api.listReceiveDept({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    deptData: res.data.list,
                })
            }
        })
    }
    //点击刷新
    renovate = () => {

    }
    //点击确定接收
    receivingCan = (record) => {
        Api.confirmReceive({
            bizId: record.bizId
        }, (res) => {
            if (res.code === "200") {
                message.success(res.message)
            }
        })

    }
    //点击查看文件
    details = () => {

    }
    render() {
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 16 },
        };
        const { getFieldDecorator } = this.props.form;
        const {
            breadcrumbData,
            planData,
            checkColumns,
            listFileTypesData,
            listApplyDeptInFileModelData,
            writtenData,
            deptData
        } = this.state;

        //文件类别
        const listFileTypes = listFileTypesData ? listFileTypesData.map(res => {
            return (<Option key={res.fileTypeId} values={res.fileTypeId}>{res.fileTypeIdName}</Option>)
        }) : null
        //编写部门
        const listApplyDeptInFileModel = listApplyDeptInFileModelData ? listApplyDeptInFileModelData.map(res => {
            return (<Option key={res.applyDeptId} values={res.applyDeptId}>{res.applyDeptName}</Option>)
        }) : null
        //编写人
        const writtenBy = writtenData ? writtenData.map(res => {
            return (<Option key={res.userId} values={res.userId}>{res.userName}</Option>)
        }) : null
        //接收部门
        const departmentData = deptData ? deptData.map(res => {
            return (<Option key={res.receiveDeptId} values={res.receiveDeptId}>{res.receiveDeptName}</Option>)
        }) : null
        // const pagination = total > 10 ? {
        //     total: this.state.total,
        //     pageSize: this.state.pageSize,
        //     current: this.state.page,
        //     onChange: this.handleTableChange,
        //     showQuickJumper: true,
        //     showSizeChanger,
        //     onShowSizeChange: this.onShowSizeChange,
        // } : false
        return (
            <div className="feedBcak">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="p-search">
                        <Form style={{ position: 'relative', top: -3 }}>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件编号"
                                    >
                                        {getFieldDecorator('planName', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件名称"
                                    >
                                        {getFieldDecorator('startTime', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件类别"
                                    >
                                        {getFieldDecorator('fileTypeId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listFileTypes}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="编写部门"
                                    >
                                        {getFieldDecorator('applyDeptId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listApplyDeptInFileModel}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="编写人"
                                    >
                                        {getFieldDecorator('applyUser', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {writtenBy}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="接收部门"
                                    >
                                        {getFieldDecorator('receiveDeptId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {departmentData}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6} offset={0}>
                                    <Button onClick={this.handleReset} style={{ float: 'right' }}>重置</Button>
                                    <Button type="primary" htmlType="submit" onClick={this.handleSearchPeople} style={{ marginRight: 20, float: 'right' }}>查询</Button>
                                </Col>
                            </Row>
                        </Form>
                    </div>
                    <Table
                        style={{ marginTop: '-12px', marginLeft: '20px' }}
                        columns={checkColumns}
                        dataSource={planData}
                        // pagination={pagination}
                        key="sadas"
                    ></Table>

                </div>
            </div>
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner