/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { Component } from 'react';
import { Table, Popconfirm, Checkbox } from 'antd';

class tableList extends Component {
    constructor(props) {
        super(props)
        this.columns = [
          {
            title: '岗位名称',
            dataIndex: 'jobName',
          },{
            title: '查询',
            dataIndex: 'allowQuery',
            render: (text, record) => <Checkbox record={record} checked={text} typeval='allowQuery' onChange={this.onChange}></Checkbox>
          },{
            title: '阅读',
            dataIndex: 'allowRead',
            render: (text, record) => <Checkbox record={record} checked={text} typeval='allowRead' onChange={this.onChange}></Checkbox>
          },{
            title: '修改',
            dataIndex: 'allowEdit',
            render: (text, record) => <Checkbox record={record} checked={text} typeval='allowEdit' onChange={this.onChange}></Checkbox>
          },{
            title: '启用',
            dataIndex: 'allowEnable',
            render: (text, record) => <Checkbox record={record} checked={text} typeval='allowEnable' onChange={this.onChange}></Checkbox>
          },{
            title: '冻结',
            dataIndex: 'allowFreeze',
            render: (text, record) => <Checkbox record={record} checked={text} typeval='allowFreeze' onChange={this.onChange}></Checkbox>
          },{
            title: '删除',
            dataIndex: 'allowDelete',
            render: (text, record) => <Checkbox record={record} checked={text} typeval='allowDelete' onChange={this.onChange}></Checkbox>
          },{
            title: '部门',
            dataIndex: 'deptName',
          },{
            title: '排序',
            dataIndex: 'sortCode',
          },{
            title: '操作',
            dataIndex: 'operation',
            width: 100,
            render: (text, record) =>
                <div>
                    <a className="operation" onClick={this.handleEdit.bind(this,record)}>编辑</a>
                    <Popconfirm title="确定要删除该数据吗" onConfirm={this.handleDelete.bind(this,record.bizId)}>
                        <a className="operation">删除</a>
                    </Popconfirm>
                </div>
          },
        ]
    }
    handleChangePage = (pageNo, pageSize) => {
        this.props.handleChangePage(pageNo, pageSize)
    }
    handleDelete = (code) => {
        this.props.handleDelete(code)
    }
    onShowSizeChange = (pageNo, pageSize) => {
        this.props.handleChangePage(1, pageSize)
    }
    handleEdit = (item,e) => {
        e.preventDefault();
        this.props.handleEdit(item)
    }
    onChange = (e) => {
        e.preventDefault();
        const { typeval,checked,record } = e.target;
        this.props.handleSingleUpdate(typeval, checked, record)
        
    }
    render() {
        return (
            <Table
                rowKey={(record) => record.bizId }
                loading={this.props.loading}
                columns={this.columns} 
                dataSource={this.props.data}
                pagination={{
                    hideOnSinglePage: true,
                    showSizeChanger: true,
                    showQuickJumper: true,
                    current: this.props.pageNo,
                    total: this.props.total,
                    pageSize: this.props.pageSize,
                    onChange: this.handleChangePage,
                    onShowSizeChange: this.onShowSizeChange,
                }}
            />
        )
    }
}
export default tableList