import React, { Component } from 'react'
import { Table, Checkbox, Button } from 'antd';
import './index.less'
import BreadcrumbLink from '../../../components/bread';
import LeftTree from './left-tree';
class databaseResources extends Component {
    apis = window.apis;
    constructor(props) {
        super(props)
        this.state = {
            breadcrumbData: [
                { name: '系统管理' },
                { name: '数据库资源' }
            ],
            showTree: false,
            tableNameList: [],      // 左侧对象资源管理器列表
            dataBaseName: '',       // 数据库名称
            rightData: [],
            selectedKeys: [],
        }
        this.leftColumns = [{
          title: '列名',
          dataIndex: 'columnName',
          width: 150,
        },{
            title: '数据类型',
            dataIndex: 'columnType',
            width: 100
        },{
            title: '长度',
            dataIndex: 'columnLength',
            width: 70
        },{
          title: '标识',
          dataIndex: 'identification',
          width: 100,
          render: (text, record) => <Checkbox checked={text}></Checkbox>
        },{
            title: '主键',
            dataIndex: 'primaryKey',
            width: 100,
            render: (text, record) => <Checkbox checked={text}></Checkbox>
        },{
          title: '允许空',
          dataIndex: 'allowEmpty',
          width: 100,
          render: (text, record) => <Checkbox checked={text}></Checkbox>
        },{
          title: '默认值',
          dataIndex: 'defaultValue',
          width: 100,
        },{
            title: '说明',
            dataIndex: 'remark',
            width: 200,
        }]
    }
    componentDidMount() {
      this.queryListAllTables();
    }
    // 对象资源管理--左侧列表查询
    queryListAllTables = () => {
        this.apis.listAllTables({},(res) => {
            if(res.code === '200') {
                const selectedKeys = [];
                selectedKeys.push(res.data.tableNameList[0])
                this.setState({
                    tableNameList: res.data.tableNameList,
                    dataBaseName: res.data.dataBaseName,
                    selectedKeys,
                    showTree: true
                },() => {
                    this.handleTreeUpdate(selectedKeys);
                })
            }
        })
    }
    handleTreeUpdate = (tableName) => {
        this.setState({
            loading: true,
            selectedKeys: tableName
        },() => {
            this.apis.listTableInfo({tableName:tableName[0]},(res) => {
                if(res.code === '200') {
                    this.setState({
                        rightData: res.data,
                        loading: false
                    })
                }else{
                    this.setState({
                        loading: false
                    })
                }
            })
        })
    }
    handleUpdate = () => {
        this.setState({
            showTree: false
        },() => {
            this.queryListAllTables();
        })
    }
    render() {
      const { breadcrumbData, showTree, tableNameList, dataBaseName, rightData, selectedKeys } = this.state;
        return (
            <div className="databaseResources">
                <BreadcrumbLink breadcrumbData={breadcrumbData}>
                    <div className="breadcrumbLink-left">
                        <div className="left-wrap">对象资源管理器</div>
                        <div className="right-wrap">QMS ltemDetails 数据字典明细表</div>
                    </div>
                </BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="left-wrap">
                        {showTree && <LeftTree
                            tableNameList={tableNameList}
                            dataBaseName={dataBaseName}
                            selectedKeys={selectedKeys}
                            handleTreeUpdate={this.handleTreeUpdate}
                        />}
                    </div>
                    <div className="right-wrap">
                        <div className="btn">
                            <Button type="primary" onClick={this.handleUpdate}>刷新</Button>
                            <Button type="primary">新增查询</Button>
                        </div>
                        <div className="table-list">
                            <Table
                                rowKey={(record) => { return `${Math.random(10)}` }}
                                columns={this.leftColumns} 
                                dataSource={rightData}
                                pagination={false}
                            />
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
export default databaseResources