import React, { Component } from 'react'
import { Form, Input, Table, Row, Button, message, Col, DatePicker, Select } from 'antd';
import {
    HashRouter as Router, Route, Switch, Link, withRouter,
} from 'react-router-dom';
import './index.less'
// import '../../../assets/css/common.less'
import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread'
import AmendmentFromWrap from './amendmentForm';
const Option = Select
const Search = Input.Search;
const { RangePicker } = DatePicker;
class onBanner extends Component {
    constructor(props) {
        super(props)
        this.state = {
            checkColumns: [],
            amendmentVisible: false,//查看记录 弹窗
            cancelVisible: false,//申请作废
            reviseVisible: false,//修改
            listFileTypesData: [],//文件类别
            listApplyDeptInFileModelData: [],//编写部门下拉
            writtenData: [],//编写人
            selectTime: [],
            planData: [],
            pageSize: 10,
            page: 1,
            breadcrumbData: [
                { name: '文件管理' },
                { name: '文件年审' }
            ],
        }
    }
    componentDidMount() {
        this.setState({
            checkColumns: [{
                title: '文件编号',
                key: 'planName',
                width: '200px',
                render: (text, record, index) => {
                    if (record.planName) {
                        return (
                            <div className='ellipsis'>{record.planName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '文件名称',
                key: 'startTime',
                render: (text, record, index) => {
                    if (record.startTime) {
                        return (
                            <div className='ellipsis'>{record.startTime}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '版本/修订号',
                key: 'bianxiereddn',
                render: (text, record, index) => {
                    if (record.bianxiereddn) {
                        return (
                            <div className='ellipsis'>{record.bianxiereddn}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '生效日期',
                key: 'siteName',
                render: (text, record, index) => {
                    if (record.siteName) {
                        return (
                            <div className='ellipsis'>{record.siteName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '文件类别',
                key: 'wenjianleibie',
                render: (text, record, index) => {
                    if (record.wenjianleibie) {
                        return (
                            <div className='ellipsis'>{record.wenjianleibie}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '编写部门',
                key: 'bianxiebumen',
                render: (text, record, index) => {
                    if (record.bianxiebumen) {
                        return (
                            <div className='ellipsis'>{record.bianxiebumen}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '编写人',
                key: 'bianxieren',
                render: (text, record, index) => {
                    if (record.bianxieren) {
                        return (
                            <div className='ellipsis'>{record.bianxieren}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '审批人',
                key: 'jieshouren',
                render: (text, record, index) => {
                    if (record.jieshouren) {
                        return (
                            <div className='ellipsis'>{record.jieshouren}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                }
            }, {
                title: '完成年审',
                key: 'wanchengnianshen',
                render: (text, record, index) => {
                    if (record.wanchengnianshen) {
                        return (
                            <div className='ellipsis'>{record.wanchengnianshen}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '评审人',
                key: 'pingshenren',
                render: (text, record, index) => {
                    if (record.pingshenren) {
                        return (
                            <div className='ellipsis'>{record.pingshenren}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '年审结论',
                key: 'nianshenjielun',
                render: (text, record, index) => {
                    if (record.nianshenjielun) {
                        return (
                            <div className='ellipsis'>{record.nianshenjielun}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '操作',
                key: 'operation',
                dataIndex: 'itemStatus',
                // width: 200,
                render: (text, record) => {
                    return (
                        <div>
                            <a onClick={this.applicationAmendment}>查看记录</a> &nbsp;
                            <a onClick={this.applicationCancel.bind(this, record)}>年审</a> &nbsp;
                        </div>
                    )
                }
            }
            ]
        }, () => {
            this.pageListFileYear()
            this.listFileTypes()//文件类别下拉
            this.listApplyDeptInFileModel()//编写部门下拉
            this.listUsers()//编写人下拉
        }
        )
    }
    //文件类别下拉
    listFileTypes = () => {
        Api.listFileTypes({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listFileTypesData: res.data.list,
                })
            }
        })
    }
    //编写部门下拉
    listApplyDeptInFileModel = () => {
        Api.listApplyDeptInFileModel({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listApplyDeptInFileModelData: res.data.list
                })
            }
        })
    }
    //编写人
    listUsers = () => {
        Api.listUsers({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    writtenData: res.data.list,
                })
            }
        })
    }
    //初始化 列表 数据
    pageListFileYear() {
        const { pageSize, page } = this.state
        Api.pageListFileYear({
            pageSize,
            pageNo: page
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    planData: res.data.list
                })
            }
        })
    }
    //点击查询
    handleSearchPeople = () => {
        this.setState({
            page: 1,
        }, () => {
            this.props.form.validateFieldsAndScroll((err, values) => {
                if (true) {
                    const { pageSize, page } = this.state;
                    this.setState({
                        deviceLoading: true
                    })
                    Api.pageListFileYear({
                        pageSize,
                        pageNo: page,
                        fileNo: values.fileNo == undefined ? values.fileNo : values.fileNo.trim(),
                        fileName: values.fileName == undefined ? values.fileName : values.fileName.trim(),
                        fileTypeId: values.fileTypeId,
                        applyDeptId: values.applyDeptId,
                        applyUserName: values.applyUserName,
                    }, (res) => {
                        if (res.code === '200') {
                            this.setState({
                                page,
                                total: res.data.total,
                                planData: res.data.list
                            })
                        }
                    })
                }
            });
        })
    }
    //点击 重置
    handleReset = () => {
        this.props.form.resetFields();
        this.pageListFileYear()
    }
    //点击 查看记录
    applicationAmendment = () => {
        this.setState({
            amendmentVisible: true
        })
    }
    //查看记录 点击确定
    amendmentCan = () => {
        this.setState({
            amendmentVisible: false
        })
    }
    //查看记录 点击取消
    amendmentNo = () => {
        this.setState({
            amendmentVisible: false
        })
    }
    //年审
    applicationCancel = ( record ) => {
        this.props.history.push(`/fileManagement/annualReview/approval/?bizId=${record.bizId}`)
    }
    //修改
    handleRevise = () => {

    }
    render() {
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 16 },
        };
        const { getFieldDecorator } = this.props.form;
        const {
            breadcrumbData,
            planData,
            checkColumns,
            amendmentVisible,
            listFileTypesData,
            listApplyDeptInFileModelData,
            writtenData
        } = this.state;

        //文件类别
        const listFileTypes = listFileTypesData ? listFileTypesData.map(res => {
            return (<Option key={res.fileTypeId} values={res.fileTypeId}>{res.fileTypeIdName}</Option>)
        }) : null
        //编写部门
        const listApplyDeptInFileModel = listApplyDeptInFileModelData ? listApplyDeptInFileModelData.map(res => {
            return (<Option key={res.applyDeptId} values={res.applyDeptId}>{res.applyDeptName}</Option>)
        }) : null
        //编写人
        const writtenBy = writtenData ? writtenData.map(res => {
            return (<Option key={res.userId} values={res.userId}>{res.userName}</Option>)
        }) : null

        // const pagination = total > 10 ? {
        //     total: this.state.total,
        //     pageSize: this.state.pageSize,
        //     current: this.state.page,
        //     onChange: this.handleTableChange,
        //     showQuickJumper: true,
        //     showSizeChanger,
        //     onShowSizeChange: this.onShowSizeChange,
        // } : false
        return (
            <div className="feedBcak">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="p-search">
                        <Form style={{ position: 'relative', top: -3 }}>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件编号"
                                    >
                                        {getFieldDecorator('planName', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件名称"
                                    >
                                        {getFieldDecorator('startTime', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件类别"
                                    >
                                        {getFieldDecorator('fileTypeId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listFileTypes}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="编写部门"
                                    >
                                        {getFieldDecorator('applyDeptId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listApplyDeptInFileModel}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="编写人"
                                    >
                                        {getFieldDecorator('applyUserName', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {writtenBy}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6} offset={0}>
                                    <Button onClick={this.handleReset} style={{ float: 'right' }}>重置</Button>
                                    <Button type="primary" htmlType="submit" onClick={this.handleSearchPeople} style={{ marginRight: 20, float: 'right' }}>查询</Button>
                                </Col>
                            </Row>
                        </Form>
                    </div>
                    <Table
                        style={{ marginTop: '10px', marginLeft: '5px' }}
                        columns={checkColumns}
                        dataSource={planData}
                        // pagination={pagination}
                        key="sadas"
                    ></Table>
                    {/* 查看记录 */}
                    <AmendmentFromWrap
                        visible={amendmentVisible}
                        onCancel={this.amendmentCan}
                        onCreate={this.amendmentNo}
                    />
                </div>
            </div>
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner