import React, { Component } from 'react'
import { Button, message } from 'antd';
import './index.less'
import BreadcrumbLink from '../../../components/bread'
import Table from './table-list';
import DeptModal from './dept-modal';

class branchManagement extends Component {
    apis = window.apis;
    constructor(props) {
        super(props)
        this.state = {
            breadcrumbData: [
                { name: '系统管理' },
                { name: '部门管理' }
            ],
            loading: false,   // 列表加载中
            dataList: [],     // 部门列表数据
            type: 0,          // 弹框类型 0:新增，1:修改
            deptDetail: {},    // 点击选中的role的行内容
            setdeptModalShow: false,    // 新增，修改职位弹框的显隐
        }
    }
    componentDidMount() {
      this.queryTableList();
    }
    // 数据循环
    changeData = (data) => {
        data.map(item => {
            console.log(item);
            if (item.list && item.list.length) {
                const val= item['list'];
                delete item['list'];
                item['children']=val;
                this.changeData(item.children);
            }
            return {...item}
        })
        return data
    }
    // 查询部门列表
    queryTableList = () =>{
        this.setState({
            loading: true,
        },() => {
            this.apis.queryDeptList({},(res) => {
                if(res.code === '200') {
                    const list = this.changeData(res.data);
                    this.setState({
                        dataList: list,
                        loading: false,
                    })
                }else{
                    this.setState({
                        loading: false,
                    })
                }
            })
        })
    }
    // 删除
    handleDelete = (bizId) => {
        this.apis.deleteQMSDept({bizId},(res) => {
            if(res.code === '200') {
                message.success('删除成功！');
                this.queryTableList();
            }
        })
    }
    // 新增部门弹框
    handleAddModal = () => {
        this.setState({
            setdeptModalShow:true,
            type: 0,
            deptDetail: {}
        })
    }
    // 修改职位弹框
    handleEdit= (item) => {
        this.setState({
            setdeptModalShow:true,
            type: 1,
            deptDetail: {...item}
        })
    }  
    // 取消弹框
    handleCancel = () => {
        this.setState({
            setdeptModalShow:false
        },() => {
            this.refs.DeptModal.resetFields();
        })
    }
    handleaddsetMeal = (values) => {
        const params = {
            ...values
        }
        delete params['type'];
        if (values.type === 0) { // 新增
            this.apis.addQMSDept(params,(res) => {
                if(res.code === '200') {
                    message.success('新增成功！');
                    this.handleCancel();
                    this.queryTableList();
                }
            })
        }else{ // 修改
            params['bizId'] = this.state.deptDetail.bizId;
            this.apis.updateQMSDept(params,(res) => {
                if(res.code === '200') {
                    message.success('修改成功！');
                    this.handleCancel();
                    this.queryTableList();
                }
            })
        }
    }
    render() {
        const { breadcrumbData, loading, dataList, setdeptModalShow, type, deptDetail } = this.state;
        return (
            <div className="branchManagement">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="btn">
                    <Button type="primary" onClick={this.handleAddModal}>新增</Button>
                    </div>
                    <div className="table-list">
                        <Table 
                            loading={loading} 
                            data={dataList} 
                            handleDelete={this.handleDelete}
                            handleEdit={this.handleEdit}
                        />
                    </div>
                </div>
                <DeptModal
                    ref="DeptModal"
                    setdeptModalShow={setdeptModalShow}
                    type={type}
                    deptDetail={deptDetail}
                    deptData={dataList}
                    handleCancel={this.handleCancel}
                    handleaddsetMeal={this.handleaddsetMeal}
                />
            </div>
        )
    }
}
export default branchManagement