/* eslint-disable jsx-a11y/anchor-is-valid */
import React, { Component } from 'react';
import { Table, Popconfirm, Checkbox } from 'antd';

class tableList extends Component {
    constructor(props) {
        super(props)
        this.columns = [
          {
            title: '部门名称',
            dataIndex: 'deptName',
          },{
            title: '部门代码',
            dataIndex: 'code',
            width: 100,
          },{
            title: '部门层次',
            dataIndex: 'deptLevel',
            width: 100,
          },{
            title: '有效',
            dataIndex: 'allowEnable',
            width: 100,
            render: (text, record) => <Checkbox checked={text}></Checkbox>,
          },{
            title: '冻结',
            dataIndex: 'allowFreeze',
            width: 100,
            render: (text, record) => <Checkbox checked={text}></Checkbox>,
          },{
            title: '删除',
            dataIndex: 'allowDelete',
            width: 100,
            render: (text, record) => <Checkbox checked={text}></Checkbox>,
          },{
            title: '排序',
            dataIndex: 'sortCode',
            width: 100,
          },{
            title: '操作',
            dataIndex: 'operation',
            width: 100,
            render: (text, record) =>
                <div>
                    <a className="operation" onClick={this.handleEdit.bind(this,record)}>编辑</a>
                    <Popconfirm title="确定要删除该数据吗" onConfirm={this.handleDelete.bind(this,record.bizId)}>
                        <a className="operation">删除</a>
                    </Popconfirm>
                </div>
              
          },
        ]
    }
    handleEdit = (item) => {
        this.props.handleEdit(item)
    }
    handleDelete = (code) => {
      this.props.handleDelete(code)
    }
    expandedRowRender = (record, index, indent, expanded) => {
        console.log(record, index, indent, expanded);
        if (record.list && record.list.length) {
            return <Table
                showHeader={false}
                rowKey={(record) => { return `${record.name}${Math.random(10)}${Math.random(10)}` }}
                columns={this.columns} 
                dataSource={record.list}
                pagination={false}
                expandedRowRender={this.expandedRowRender}
            />
        }else{
            return false
        }
    }
    render() {
        return (
            <Table
                rowKey={(record) => record.bizId}
                loading={this.props.loading}
                columns={this.columns} 
                dataSource={this.props.data}
                expandRowByClick={true}
                pagination={false}
            />
        )
    }
}
export default tableList