import moment from 'moment';
import React,{ Component } from 'react'
import { Input, Form, Row, Col, Button, DatePicker, Select } from 'antd'
const { Option } = Select
const FormItem = Form.Item

class FormListLog extends Component{
  constructor(props){
    super(props)
    this.state = {

    }
  }
  // 提交表单
  handleSubmit = () => {
    console.log('1111');
    this.props.form.validateFields((err,values)=>{
      if(err){
        return false
      }
      const data = {};
      // 去除含有undefined的item
      Object.keys(values).forEach(res=>{
        if(values[res]){
          data[res] = values[res]
        }
      })
      // 如果选择时间，解析字符串
      if (data.time) {
        data.time = moment(data.time).format('YYYY-MM-DD HH:mm:ss');
        console.log(moment(data.time).format('YYYY-MM-DD HH:mm:ss'),moment(data.time).format().unix())
      }
      this.props.handleSearchBtn(data);
    })
  }
  handleReset = () => {
    this.props.form.resetFields();
    this.props.handleSearchBtn({});
  }
  render(){
    const { getFieldDecorator } = this.props.form;
    return (
      <Form labelCol={{ span: 5 }} wrapperCol={{ span: 16 }}>
        <Row gutter={24}>
          <Col span={8}>
            <FormItem label="日期/时间">
              {
                getFieldDecorator('time')(<DatePicker showTime placeholder="请选择" />)
              }
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem label="操作类型">
              {
                getFieldDecorator('pxName')(
                  <Select placeholder="请选择">
                    <Option value="china">China</Option>
                    <Option value="usa">U.S.A</Option>
                  </Select>
                )
              }
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem label="操作模块">
              {
                getFieldDecorator('mkName')(<Input placeholder="请输入" autoComplete='off' />)
              }
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem label="操作用户">
              {
                getFieldDecorator('userName')(<Input placeholder="请输入" autoComplete='off' />)
              }
            </FormItem>
          </Col>
          <Col span={4}>
            <Button onClick={this.handleReset} style={{ float: 'right' }}>重置</Button>
            <Button type="primary"  onClick={this.handleSubmit} style={{ marginRight: 20, float: 'right' }}>查询</Button>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(FormListLog)

