import React,{ Component } from 'react'
import { Input, Form, Row, Col, Button, Select } from 'antd'

const { Option } = Select
const FormItem = Form.Item

class FormListTest extends Component{
  constructor(props){
    super(props)
    this.state = {

    }
  }
  // 提交表单
  handleSubmit = () => {
    console.log('1111');
    this.props.form.validateFields((err,values)=>{
      if(err){
        return false
      }
      const data = {};
      // 去除含有undefined的item
      Object.keys(values).forEach(res=>{
        if(values[res]){
          data[res] = values[res]
        }
      })
      // 如果选择时间，解析字符串
      if (data.time && data.time.length > 0) {
        data.startTime = data.time[0].format('YYYY-MM-DD');
        data.endTime = data.time[1].format('YYYY-MM-DD');
        delete data.time;
      }
      this.props.handleSearchBtn(data);
    })
  }
  handleReset = () => {
    this.props.form.resetFields();
    this.props.handleSearchBtn({});
  }
  render(){
    const { getFieldDecorator } = this.props.form;
    return (
      <Form labelCol={{ span: 5 }} wrapperCol={{ span: 16 }}>
        <Row gutter={24}>
          <Col span={8}>
            <FormItem label="输入查询">
              {
                getFieldDecorator('name')(<Input placeholder="请输入用户姓名或账号" autoComplete='off' />)
              }
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem label="部门">
              {
                getFieldDecorator('dept')(<Input placeholder="请输入" autoComplete='off' />)
              }
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem label="培训名称">
              {
                getFieldDecorator('pxName')(<Input placeholder="请输入" autoComplete='off' />)
              }
            </FormItem>
          </Col>
          <Col span={8}>
            <FormItem label="考试结果">
              {
                getFieldDecorator('pxName')(
                  <Select placeholder="请选择">
                    <Option value="china">China</Option>
                    <Option value="usa">U.S.A</Option>
                  </Select>
                )
              }
            </FormItem>
          </Col>
          <Col span={4}>
            <Button onClick={this.handleReset} style={{ float: 'right' }}>重置</Button>
            <Button type="primary"  onClick={this.handleSubmit} style={{ marginRight: 20, float: 'right' }}>查询</Button>
          </Col>
        </Row>
      </Form>
    )
  }
}

export default Form.create()(FormListTest)

