import React, { Component } from 'react'
// import { Button } from 'antd';
import './index.less'
import BreadcrumbLink from '../../../components/bread';
import SearchForm from "./search-form"; // 搜索条件
import Table from "./table-list"; // 搜索条件
class loginLog extends Component {
    apis = window.apis;
    constructor(props) {
        super(props)
        this.state = {
            breadcrumbData: [
              { name: '系统管理' },
              { name: '登陆日志' }
            ],
            loading: false,
            dataList: [],     // 登录日志列表数据
            total: 0,         // 登录日志总数
            pageSize: 10,     // 分页Size
            pageNo: 1,        // 分页页码
            searchVale: {},   // 搜索条件
        }
    }
    componentDidMount() {
        this.queryTableList();
    }
    // 开始查询
    handleSearchBtn = (value) =>{
        this.setState({
            searchVale: {...value}
        },() => {
            this.queryTableList();
        })
    }
    // 查询试卷列表
    queryTableList = (pageNo=1, pageSize=10) =>{
        const params = {
            ...this.state.searchVale,
            pageNo,
            pageSize,
            loading: true
        }
        this.apis.listLoginLogs({...params},(res) => {
            if(res.code === '200') {
                this.setState({
                    dataList: res.data.list,
                    total: res.data.total,
                    pageNo,
                    pageSize,
                    loading: false
                })
            }else{
                this.setState({
                    loading: false
                })
            }
        })
    }
    // 切换页码
    handleChangePage = (pageNo,pageSize) => {
        this.setState({
            pageNo,
            pageSize
        },() => {
            this.queryTableList(pageNo,pageSize);
        })
    }
    render() {
        const { breadcrumbData, dataList, total, pageNo, pageSize, loading } = this.state;
        return (
            <div className="loginLog">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    {/* 搜索条件 */}
                    <div className="p-search">
                        <SearchForm handleSearchBtn={this.handleSearchBtn} />
                    </div>
                    <div className="table-list">
                        <Table 
                            loading={loading} 
                            data={dataList} 
                            total={total}
                            pageNo={pageNo}
                            pageSize={pageSize}
                            handleChangePage={this.handleChangePage} 
                        />
                    </div>
                </div>
            </div>
        )
    }
}
export default loginLog