import React, { Component } from 'react'
import { Button, message } from 'antd';
import './index.less'
import BreadcrumbLink from '../../../components/bread'
import Table from './table-list';
import RoleModal from './role-modal';
import Authorization from "./role-authorization";


class roleManagement extends Component {
    apis = window.apis;
    constructor(props) {
        super(props)
        this.state = {
            breadcrumbData: [
                { name: '系统管理' },
                { name: '角色管理' }
            ],
            loading: false,   // 列表加载中
            dataList: [],     // 角色列表数据
            total: 0,         // 角色总数
            pageSize: 10,     // 分页Size
            pageNo: 1,        // 分页页码
            type: 0,          // 弹框类型 0:新增，1:修改
            roleDetail: {},   // 点击选中的role的行内容
            menuData: [],     // 菜单列表
            setRoleModalShow: false,    // 新增角色/修改角色弹框
            authorizationShow: false,   // 角色授权弹框
            
        }
    }
    componentDidMount() {
      this.queryTableList();
      this.queryMenuDataList();
    }
    // 查询角色列表
    queryTableList = (pageNo=1, pageSize=10) =>{
        this.setState({
            loading: true,
        },() => {
            const params = {
                pageNo,
                pageSize,
            }
            this.apis.pageListRole({...params},(res) => {
                if(res.code === '200') {
                    this.setState({
                        dataList: res.data.list,
                        total: res.data.total,
                        loading: false,
                        pageNo,
                        pageSize
                    })
                }
            })
        })
    }
    // 查询菜单列表
    queryMenuDataList = () => {
        // parentId 未传，系统默认从顶级 即 parentId = 0 开始展开
        this.apis.queryMenuData({},(res) => {
            if(res.code === '200') {
                this.setState({
                    menuData: res.data,
                })
            }
        })
    }
    // 点击新增角色
    handleAddModel = () => {
        this.setState({
            setRoleModalShow: true,
            type: 0,
            roleDetail: {}
        })
    }
    // 表格单个修改
    handleSingleUpdate = (typeval, checked, record) => {
        const value = {
            ...record,
            [typeval]: checked
        }
        this.apis.updateRole({...value},(res) => {
            if(res.code === '200') {
                message.success('修改成功！');
                this.queryTableList();
            }
        })
    }
    // 点击修改
    handleEdit = (item) => {
        this.setState({
            setRoleModalShow: true,
            type: 1,
            roleDetail: {...item}
        })
    }
    // 取消弹框
    handleCancel = () => {
        this.setState({
            setRoleModalShow:false
        },() => {
            this.refs.roleModal.resetFields();
        })
    }
    // 确认弹框
    handleaddsetMeal = (value) => {
        Object.keys(value).forEach(item => {
            value[item] = value[item] ? value[item] : false
        })
        if (value.type === 1) { // 修改
            value['bizId'] = this.state.roleDetail.bizId;
            this.apis.updateRole({...value},(res) => {
                if(res.code === '200') {
                    message.success('修改成功！');
                    this.handleCancel();
                    this.queryTableList();
                }
            })
        }else{
            this.apis.addRole({...value},(res) => {
                if(res.code === '200') {
                    message.success('新增成功！');
                    this.handleCancel();
                    this.queryTableList();
                }
            })
        }
    }
    // 删除
    handleDelete = (bizId) => {
        this.apis.deleteRole({bizId},(res) => {
            if(res.code === '200') {
                message.success('删除成功！');
                this.queryTableList();
            }
        })
    }
    // 角色授权
    hanleWarrant = (item) => {
        this.setState({
            authorizationShow: true,
            roleDetail: {...item}
        })
    }
    // 关闭角色授权弹框
    handleMenuCancel = () => {
        this.setState({
            authorizationShow: false,
            roleDetail: {}
        })
    }
    // 确定角色授权
    handleAuthorization = (val)=> {
        const params = {
            roleId: this.state.roleDetail.bizId,
            menuIdList: val
        }
        this.apis.roleInMenu(params,(res) => {
            if(res.code === '200') {
                message.success('授权成功！');
                this.handleMenuCancel();
                this.queryTableList();
            }
        })
    }
    // 切换页码
    handleChangePage = (pageNo,pageSize) => {
        this.setState({
            pageNo,
            pageSize
        },() => {
            this.queryTableList(pageNo,pageSize);
        })
    }
    render() {
        const { breadcrumbData, dataList, loading, pageNo, pageSize, total, type, roleDetail, setRoleModalShow, authorizationShow, menuData } = this.state;
        return (
            <div className="roleManagement">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="btn">
                        <Button type="primary" onClick={this.handleAddModel}>新增</Button>
                    </div>
                    <div className="table-list">
                        <Table 
                            loading={loading} 
                            data={dataList} 
                            pageNo={pageNo}
                            pageSize={pageSize}
                            total={total}
                            handleChangePage={this.handleChangePage} 
                            handleDelete={this.handleDelete}
                            handleEdit={this.handleEdit}
                            handleSingleUpdate={this.handleSingleUpdate}
                            hanleWarrant={this.hanleWarrant}
                        />
                    </div>
                </div>
                <RoleModal
                    ref="roleModal"
                    type={type}
                    roleDetail={roleDetail}
                    setRoleModalShow={setRoleModalShow}
                    handleCancel={this.handleCancel} // 关闭弹框
                    handleaddsetMeal={this.handleaddsetMeal}
                    handleModifyMeal={this.handleModifyMeal}
                />
                <Authorization
                    ref="authorization"
                    menuData={menuData}
                    checkedMenuCode={roleDetail.menuIdList}
                    authorizationShow={authorizationShow}
                    handleCancel={this.handleMenuCancel} // 关闭弹框
                    handleaddsetMeal={this.handleAuthorization}
                />
            </div>
        )
    }
}
export default roleManagement