import React, { Component } from 'react'
import './index.less'
import { Modal, Input, Form, Row, Col, Checkbox,Select, Transfer } from 'antd'


const FormItem = Form.Item;
const Option = Select.Option;

class categoryModal extends Component {
    constructor(props) {
        super(props)
        this.state = {
            rightData: [],
            leftData: [],
            selectedKeys: [],
        }
    }
    componentDidMount() {

    }
    handleOk = (e) => {
        e.preventDefault();
        const { form, type } = this.props;
        form.validateFields((err, values) => {
            if (!err) {
                values.type = type;
                values['category'] = {
                    '611919992795041792': '系统资料',
                    '611920443401703424': '业务资料'
                }[values.parentId]
                console.log(values)
                this.props.handleaddsetMeal(values)
            }
        })
    }
    // 点击取消
    handleCancel = () => {
        this.props.handleCancel();
    }
    render() {
        const { type, showModal, QMDetail } = this.props;
        const { getFieldDecorator } = this.props.form;
        return (
            <Modal
                className="categoryModal"
                title={type === 0 ? '字典类别-添加' : '字典类别-修改'}
                visible={showModal}
                width={520}
                onOk={this.handleOk}
                onCancel={this.handleCancel}
            >
                <Form labelCol={{ span: 5 }} wrapperCol={{ span: 16 }}>
                    <FormItem label="编号">
                        {getFieldDecorator('code', {
                            initialValue: QMDetail.code,
                            rules: [
                                { required: true, message: '请输入编号' },
                            ],
                        })(
                            <Input placeholder="请输入" />
                        )}
                    </FormItem>
                    <FormItem label="名称">
                        {getFieldDecorator('fullName', {
                            initialValue: QMDetail.fullName,
                            rules: [
                                { required: true, message: '请输入名称' },
                            ],
                        })(
                            <Input placeholder="请输入" />
                        )}
                    </FormItem>
                    <FormItem label="分类">
                        {getFieldDecorator('parentId', {
                            initialValue: QMDetail.parentId,
                            rules: [
                                { required: true, message: '请选择分类' },
                            ],
                        })(
                            <Select placeholder="请选择">
                                <Option value='611919992795041792'>系统资料</Option>
                                <Option value='611920443401703424'>业务资料</Option>
                            </Select>
                        )}
                    </FormItem>
                    <FormItem label="选项" style={{marginBottom: 0}}>
                        <Row gutter={24}>
                            <Col span={6}>
                                <FormItem>
                                    {getFieldDecorator('enabled', {
                                        valuePropName: 'checked',
                                        initialValue: QMDetail['enabled'] ? true : false
                                    })(
                                        <Checkbox>有效</Checkbox>
                                    )}
                                </FormItem>
                            </Col>
                            <Col span={9}>
                                <FormItem>
                                    {getFieldDecorator('allowEdit', {
                                        valuePropName: 'checked',
                                        initialValue: QMDetail['allowEdit'] ? true : false
                                    })(
                                        <Checkbox>允许编辑</Checkbox>
                                    )}
                                </FormItem>
                            </Col>
                            <Col span={9}>
                                <FormItem>
                                    {getFieldDecorator('allowDelete', {
                                        valuePropName: 'checked',
                                        initialValue: QMDetail['allowDelete'] ? true : false
                                    })(
                                        <Checkbox>允许删除</Checkbox>
                                    )}
                                </FormItem>
                            </Col>
                        </Row>
                    </FormItem>
                    <FormItem label="显示顺序">
                        {getFieldDecorator('sortCode', {
                            initialValue: QMDetail.sortCode,
                            rules: [
                                { required: true, message: '请输入顺序' },
                            ],
                        })(
                            <Input placeholder="请输入" />
                        )}
                    </FormItem>
                </Form>
            </Modal>
        )
    }
}
export default Form.create()(categoryModal)