import React, { Component } from 'react'
import { Button, message } from 'antd';
import './index.less'
// import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread';
import SearchForm from "./search-form"; // 搜索条件
import Table from "./table-list"; 
import UserModal from "./add-modal";
import NewPassword from './passWord-modal';
import RoleAuthorization from '../../../components/roleAuthorization'
import DeptModel from './dept-modal'
class userManage extends Component {
    apis = window.apis;
    constructor(props) {
        super(props)
        this.state = {
            breadcrumbData: [
                { name: '系统管理' },
                { name: '用户管理' }
            ],
            loading: false,   // 列表加载中
            searchValue: {},  // 查询
            dataList: [],     // 用户列表数据
            deptList: [],     // 部门列表数据查询
            roleList: [],     // 角色列表数据查询
            userDetail: {},   // 行用户详情
            total: 0,         // 角色总数
            pageSize: 10,     // 分页Size
            pageNo: 1,        // 分页页码
            type: 0,          // 弹框类型 0:新增，1:修改
            setUserModalShow: false, // 用户授权弹框
            newPasswordModalShow: false, // 修改密码弹框
            roleVisible: false,      // 角色授权弹框
            deptModellShow: false,   // 部门设置
        }
    }
    componentDidMount() {
        this.queryTableList();
        this.queryDeptList();
        this.queryRoleList();
    }
    // 开始查询
    handleSearchBtn = (value) =>{
        this.setState({
            searchValue: {...value}
        },() => {
            this.queryTableList();
        })
    }
    // 查询用户列表
    queryTableList = (pageNo=1, pageSize=10) =>{
        this.setState({
            loading: true,
        },() => {
            const params = {
                ...this.state.searchValue,
                pageNo,
                pageSize,
            }
            this.apis.pageListUser({...params},(res) => {
                if(res.code === '200') {
                    if(pageNo > 1 && res.data.list.length === 0) {
                        this.queryTableList(res.data.pages,pageSize);
                    }else{
                        this.setState({
                            dataList: res.data.list,
                            total: res.data.total,
                            loading: false,
                            pageNo: res.data.pageNum,
                            pageSize: res.data.pageSize
                        })
                    }
                }
            })
        })
    }
    // 数据循环
    changeData = (data) => {
        data.map(item => {
            if (item.list && item.list.length) {
                const val= item['list'];
                delete item['list'];
                item['children']=val;
                this.changeData(item.children);
            }
            return {...item}
        })
        return data
    }
    // 查询部门列表
    queryDeptList = () =>{
        this.apis.queryDeptList({},(res) => {
            if(res.code === '200') {
                const list = this.changeData(res.data);
                this.setState({
                    deptList: list,
                })
            }
        })
    }
    // 查询角色列表
    queryRoleList = () =>{
        this.apis.pageListRole({},(res) => {
            if(res.code === '200') {
                const list = this.changeData(res.data.list);
                this.setState({
                    roleList: list,
                })
            }
        })
    }
    // 切换页码
    handleChangePage = (pageNo,pageSize) => {
        this.queryTableList(pageNo, pageSize);
    }
    // 新增用户
    handleClickAdd = () => {
        this.setState({
            setUserModalShow: true,
            type: 0,
            userDetail: {}
        })
    }
    // 新增用户 -- 关闭弹框
    handleCancel = () => {
        this.setState({
            setUserModalShow: false,
        },() => {
            this.refs.UserModal.resetFields();
        })
    }
    // 修改用户弹框
    handleEdit = (item) => {
        this.setState({
            setUserModalShow: true,
            type: 1,
            userDetail: item
        })
    }
    // 修改用户弹框 -- 弹框确定事件
    handleaddsetMeal = (values) => {
        const params = {
            ...values
        }
        delete params['type'];
        if (values.type === 0) { // 新增
            this.apis.addUser(params,(res) => {
                if(res.code === '200') {
                    message.success('新增成功！');
                    this.handleCancel();
                    this.queryTableList();
                }
            })
        }else{ // 修改
            params['bizId'] = this.state.userDetail.bizId;
            this.apis.updateUser(params,(res) => {
                if(res.code === '200') {
                    message.success('修改成功！');
                    this.handleCancel();
                    this.queryTableList(this.state.pageNo,this.state.pageSize);
                }
            })
        }
    }
    //  删除
    handleDelete = (bizId) => {
        this.apis.deleteUser({bizId},(res) => {
            if(res.code === '200') {
                message.success('删除成功！');
                this.queryTableList(this.state.pageNo,this.state.pageSize);
            }
        })
    }
    // 角色授权
    handleClickRole = (item) => {
        this.setState({
            roleVisible: true,
            userDetail: item
        })
    }
    // 角色授权确认弹框
    handleRoleOk = (checkedList) => {
        const params = {
            userId: this.state.userDetail.bizId,
            roleIdList: checkedList
        }
        this.apis.addUserInRole(params,(res) => {
            if (res.code === '200') {
                message.success('角色授权成功！');
                this.handleRoleCancle();
                this.queryTableList(this.state.pageNo,this.state.pageSize);
            }
        })
    }
    // 角色授权取消弹框
    handleRoleCancle = () => {
        this.setState({
            roleVisible: false,
        })
    }
    // 修改密码弹框显示
    handleClickPassword = (item) => {
        this.setState({
            newPasswordModalShow: true,
            userDetail: item
        })
    }
    // 修改密码弹框关闭
    handlePasswordCancel = () => {
        this.setState({
            newPasswordModalShow: false,
        },() => {
            this.refs.NewPassword.resetFields();
        })
    }
    // 修改密码确认事件
    handleaddsetPasswordMeal = (values) => {
        const params = {
            bizId: this.state.userDetail.bizId,
            passWord: values.passWord
        }
        this.apis.updateUser( params, (res) => {
            if(res.code === '200') {
                message.success('修改成功！');
                this.handlePasswordCancel();
            }
        })
    }
    // 部门设置弹框显示
    handleDeptModelShow = (item) => {
        this.setState({
            deptModellShow: true,
            userDetail: item
        })
    }
    // 部门弹框--关闭
    handleDeptCancel = () => {
        this.setState({
            deptModellShow: false,
            userDetail: {}
        })
    }
    // 部门弹框--确定部门设置
    handleDeptSure = (deptList) => {
        console.log(deptList)
        const params = {
            userId: this.state.userDetail.bizId,
            jobIdList: deptList
        }
        console.log(params);
        // this.apis.addUserInRole(params,(res) => {
        //     if (res.code === '200') {
        //         message.success('部门设置成功！');
        //         this.handleRoleCancle();
        //         this.queryTableList(this.state.pageNo,this.state.pageSize);
        //     }
        // })
    }
    render() {
        const { breadcrumbData, dataList, deptList, loading, setUserModalShow, 
            pageNo, pageSize, total, userDetail, roleList, type, newPasswordModalShow,
            roleVisible, deptModellShow } = this.state;
        return (
            <div className="userManage">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    {/* 搜索条件 */}
                    <div className="p-search">
                        <SearchForm deptList={deptList} handleSearchBtn={this.handleSearchBtn} />
                    </div>
                    <div className="addBtn">
                        <Button type="primary" onClick={this.handleClickAdd}>新增</Button>
                    </div>
                    <div className="table-list">
                        <Table 
                            loading={loading} 
                            data={dataList} 
                            pageNo={pageNo}
                            pageSize={pageSize}
                            total={total}
                            handleChangePage={this.queryTableList}  // 切换页码
                            handleDelete={this.handleDelete} // 删除
                            handleEdit={this.handleEdit} // 编辑
                            handleClickPassword={this.handleClickPassword} // 修改密码
                            handleClickRole={this.handleClickRole} // 角色授权
                            handleDept={this.handleDeptModelShow} // 部门设置
                        />
                    </div>
                </div>
                {/* 用户弹框 */}
                <UserModal
                    ref="UserModal"
                    setUserModalShow={setUserModalShow}
                    userDetail={userDetail}
                    type={type}
                    deptList={deptList}
                    roleList={roleList}
                    handleaddsetMeal={this.handleaddsetMeal}
                    handleCancel={this.handleCancel}
                />
                {/* 修改密码 */}
                <NewPassword
                    ref="NewPassword"
                    newPasswordModalShow={newPasswordModalShow}
                    userDetail={userDetail}
                    handleaddsetPasswordMeal={this.handleaddsetPasswordMeal}
                    handlePasswordCancel={this.handlePasswordCancel}
                />
                {/* 角色授权 */}
                <RoleAuthorization
                    ref="RoleAuthorization"
                    visible={roleVisible}
                    data={roleList}
                    checkedList={userDetail.roleIdList}
                    handleCancel={this.handleRoleCancle}
                    handleOk={this.handleRoleOk}
                />
                {/* 部门设置 */}
                <DeptModel
                    ref="DeptModel"
                    deptData={deptList}
                    checkedDeptCode={userDetail.deptIdList}
                    deptModellShow={deptModellShow}
                    handleCancel={this.handleDeptCancel} // 关闭弹框
                    handleaddsetMeal={this.handleDeptSure}
                />
            </div>
        )
    }
}
export default userManage