import React,{Component} from 'react';
import './index.less'
import { Modal, Tree} from "antd";
const { TreeNode } = Tree;

class deptModal extends Component{
    constructor(props){
        super(props);
        this.state = {
            autoExpandParent: true,
            deptCodes: [],
            checkedChildrenCode: [],
        }
    }
    componentWillReceiveProps(props) {
        if (props !== this.props && props.deptModellShow) {
            this.setState({
                deptCodes: props.checkedDeptCode
            })
        }
    }
    // 点击确定
    handleaddsetMeal = (e) => {
        e.preventDefault();
        const { deptCodes } = this.state;
        this.setState({
            checkedChildrenCode: deptCodes
        },() => {
            const needData = this.checkedChildren(this.props.deptData);
            this.props.handleaddsetMeal(needData)
        })
    }
    // 循环拿到选中的子节点
    checkedChildren = (data) => {
        const checkedChildrenCode = this.state.checkedChildrenCode
        data.forEach(item => {
            if (item.children && item.children.length){ // 判断是否有下一级
                if(checkedChildrenCode.indexOf(item.bizId) !== -1) { // 判断当前级是否被选中
                    const index = checkedChildrenCode.indexOf(item.bizId);
                    checkedChildrenCode.splice(index,1);
                    this.setState({
                        checkedChildrenCode
                    })
                }
                this.checkedChildren(item.children)
            }
        })
        return checkedChildrenCode
    }
    // 点击取消
    handleCancel = () => {
        this.props.handleCancel();
    }
    onCheck = (checkedKeys) => {
        this.setState({ deptCodes:checkedKeys });
    };
    // 渲染树结构
    renderTreeNodes = (data) => {
        const treeItem = data.map(item => {
            if (item.children && item.children.length) {
                return <TreeNode title={item.deptName} key={item.bizId} dataRef={item}>
                    {this.renderTreeNodes(item.children)}
                </TreeNode>
            }
            return <TreeNode key={item.bizId} title={item.deptName} />;
        })
        return treeItem
    }
    render(){
        const { deptModellShow, deptData } = this.props;
        const { deptCodes } = this.state;
        return(
            <Modal
                className="deptModal"
                title='部门设置'
                width={420}
                visible={deptModellShow}
                onCancel={this.handleCancel}
                onOk={this.handleaddsetMeal}
            >
                <div className="authorization-box">
                    <Tree
                        checkable
                        defaultExpandAll
                        onCheck={this.onCheck}
                        checkedKeys={deptCodes}
                    >
                        {this.renderTreeNodes(deptData)}
                    </Tree>
                </div>
            </Modal>
        )
    }
}
export default deptModal