import React, { Component } from 'react'
import './index.less'
import difference from 'lodash/difference'
import { Modal, Input, Form, Row, Col, Button, Select, Transfer, Table, Switch, Tag } from 'antd'
import api from '../../../api';
import Api from '../../../api/index'

const FormItem = Form.Item;
const Option = Select.Option;

const leftTableColumns = [
    {
        dataIndex: 'questionTitle',
        title: '试题名称',
        key: 'questionTitle	',
        render: (text, record, index) => {
            if (record.questionTitle) {
                return (
                    <div className='ellipsis'>{record.questionTitle}</div>
                )
            } else {
                return (
                    <div>-</div>
                )
            }
        },
    },
    {
        dataIndex: 'questionsTypeName',
        title: '试题类型',
        key: 'questionsTypeName	',
        render: (text, record, index) => {
            if (record.questionsTypeName) {
                return (
                    <div className='ellipsis'>{record.questionsTypeName}</div>
                )
            } else {
                return (
                    <div>-</div>
                )
            }
        },
    },
    {
        dataIndex: 'modifyUserName',
        title: '编写人',
        key: 'modifyUserName	',
        render: (text, record, index) => {
            if (record.modifyUserName) {
                return (
                    <div className='ellipsis'>{record.modifyUserName}</div>
                )
            } else {
                return (
                    <div>-</div>
                )
            }
        },
    },
    {
        dataIndex: 'modifyDate',
        title: '编写时间',
        key: 'modifyDate	',
        render: (text, record, index) => {
            if (record.modifyDate) {
                return (
                    <div className='ellipsis'>{record.modifyDate}</div>
                )
            } else {
                return (
                    <div>-</div>
                )
            }
        },
    },
];
const rightTableColumns = [
    {
        dataIndex: 'title',
        title: '试题名称',
    },
];

class addModal extends Component {
    constructor(props) {
        super(props)
        this.state = {
            rightData: [],
            planData: [],
            planDataTwo: [],
            leftData: [],
            selectedRowKeys: [],
            selectedRowKeysTwo: [],
            selectedKeys: [],
            mockData: [],
            targetKeys: [],
            disabled: false,
            typeIdData: [],   //选择题型 id
            showSearch: false,
        }
        this.mockData = [{
            name: '这里是文件名称这里是文件名',
            type: '单选题',
            user: '陈乐乐',
            time: '2019-12-10'
        }, {
            name: '这里是文件名称这里是文件名',
            type: '单选题',
            user: '陈乐乐',
            time: '2019-12-10'
        }]
        this.leftTableColumns = [{
            title: '试卷名称',
            dataIndex: 'name'
        }, {
            title: '试题类型',
            dataIndex: 'type'
        }, {
            title: '编写人',
            dataIndex: 'user'
        }, {
            title: '编写时间',
            dataIndex: 'time'
        }]
        this.rightTableColumns = [{
            title: '试卷名称',
            dataIndex: 'name'
        }, {
            title: '试题类型',
            dataIndex: 'type'
        }, {
            title: '编写人',
            dataIndex: 'user'
        }, {
            title: '编写时间',
            dataIndex: 'time'
        }]
    }
    componentDidMount() {

    }

    // 校验试卷类型不为空---不实用required
    checkPaperType = (rule, value, callback) => {
        if (value) {
            callback()
        } else {
            callback('请选择试卷类型')
        }
    }
    // 校验分值不为空---不实用required
    checkGroupCount = (rule, value, callback) => {
        if (value) {
            callback()
        } else {
            callback('请输入分值')
        }
    }

    onChangeTwo = (nextTargetKeys) => {
        console.log(nextTargetKeys, 'PPPPPPPP')
        this.setState({ targetKeys: nextTargetKeys });
    };
    // triggerDisable = (disabled) => {
    //     this.setState({ disabled });
    // };

    // triggerShowSearch = (showSearch) => {
    //     this.setState({ showSearch });
    // };

    //已选择的试题 
    listQuestionByQueryForExam = () => {
        const { typeIdData } = this.state
        Api.listQuestionByQueryForExam({
            questionIds: typeIdData
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    rightData: res.data
                })
            }
        })
    }
    handleChange = (value) => {
        console.log(value, '5555555')
        Api.listQuestionByQueryForExamNotChose({
            questionsTypeId: value
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    leftData: res.data
                })
            }
        })
    }
    onSelectChange = (selectedRowKeys,sourceSelectedKeys, targetSelectedKeys ) => {
            console.log('RRRRRRRRRRRRRRRR' ,selectedRowKeys, sourceSelectedKeys, targetSelectedKeys);
        this.setState({ listSelectedKeys: sourceSelectedKeys});
    }
    render() {
        const { type, addModalShow, questionsType } = this.props;
        console.log(questionsType, '888888888')
        const { getFieldDecorator } = this.props.form;
        const { rightData, leftData, selectedKeys, planData, selectedRowKeys, planDataTwo, selectedRowKeysTwo, mockData, targetKeys, disabled, showSearch } = this.state;

        const TableTransfer = ({ leftColumns, rightColumns, ...restProps }) => (
            <Transfer {...restProps} showSelectAll={false}>
                {({
                    direction,
                    filteredItems,
                    onItemSelectAll,
                    onItemSelect,
                    selectedKeys: listSelectedKeys,
                    // disabled: listDisabled,
                }) => {
                    const columns = direction === 'left' ? leftColumns : rightColumns;

                    const rowSelection = {
                        // getCheckboxProps: item => ({ disabled: listDisabled || item.disabled }),
                        onSelectAll(selected, selectedRows) {
                            console.log(selected, selectedRows, '9999999999999999')
                            const treeSelectedKeys = selectedRows
                                // .filter(item => !item.disabled)
                                .map(({ key }) => key);
                            const diffKeys = selected
                                ? difference(treeSelectedKeys, listSelectedKeys)
                                :
                                difference(listSelectedKeys, treeSelectedKeys);
                            onItemSelectAll(diffKeys, selected);
                        },
                        onSelect({ key }, selected) {
                            onItemSelect(key, selected);
                        },
                        onChange: this.onSelectChange,
                        selectedRowKeys,
                    };

                    return (
                        <Table
                            rowSelection={rowSelection}
                            columns={columns}
                            dataSource={filteredItems}
                            size="small"
                        // style={{ pointerEvents: listDisabled ? 'none' : null }}
                        // onRow={({ key, disabled: itemDisabled  }) => ({
                        //     onClick: () => {
                        //         if (itemDisabled || listDisabled) return;
                        //         onItemSelect(key, !listSelectedKeys.includes(key));
                        //     },
                        // })}
                        />
                    );
                }}
            </Transfer>
        );


        return (
            <Modal
                className="addPaperModal"
                title={type === 0 ? '新增试题' : '修改试题'}
                visible={addModalShow}
                onOk={this.props.handleOk}
                onCancel={this.props.handleCancel}
            >
                <div className="search-wrap-one">
                    <Form labelCol={{ span: 5 }} wrapperCol={{ span: 16 }}>
                        <Row gutter={24}>
                            <Col span={12}>
                                <FormItem label="试题类型">
                                    {getFieldDecorator('paperType', {
                                        rules: [
                                            { validator: this.checkPaperType },
                                        ],
                                    })(
                                        <Select style={{ width: '180px' }} placeholder="请选择" onChange={this.handleChange}>
                                            {
                                                questionsType.map(res => {
                                                    return <Option key={res.bizId} value={res.bizId}>{res.itemName}</Option>
                                                })
                                            }
                                        </Select>
                                    )}
                                </FormItem>
                            </Col>
                            <Col span={12}>
                                <FormItem label="分值">
                                    {getFieldDecorator('groupCount', {
                                        rules: [
                                            { pattern: /^([1-9][0-9]*)$/, message: '有效时间为正整数!' },
                                            { validator: this.checkGroupCount },
                                        ],
                                    })(
                                        <Input placeholder="请输入" suffix="分/题" />
                                    )}
                                </FormItem>
                            </Col>
                        </Row>
                    </Form>
                    <div className="search-wrap-two">
                        <span>待选择题</span>
                        <Select placeholder="请选择培训类型" style={{ 'width': '180px', 'marginLeft': '10px' }}>
                            <Option value={1}>1</Option>
                        </Select>
                        <Select placeholder="请选择题库类型" style={{ 'width': '180px', 'marginLeft': '10px' }}>
                            <Option value={1}>1</Option>
                        </Select>
                        <Input placeholder="请输入名称" style={{ 'width': '180px', 'marginLeft': '10px' }} />
                    </div>
                    <div className="tableTransfer-wrap">
                        <div>
                            <TableTransfer
                                dataSource={leftData}
                                targetKeys={targetKeys}
                                disabled={disabled}
                                showSearch={showSearch}
                                onChange={this.onChangeTwo}
                                // onSelectChange={this.handleSelectChange}
                                filterOption={(inputValue, item) =>
                                    item.title.indexOf(inputValue) !== -1 || item.tag.indexOf(inputValue) !== -1
                                }
                                leftColumns={leftTableColumns}
                                rightColumns={rightTableColumns}
                            />
                            {/* <Switch
                                unCheckedChildren="disabled"
                                checkedChildren="disabled"
                                checked={disabled}
                                onChange={this.triggerDisable}
                                style={{ marginTop: 16 }}
                            /> */}
                            {/* <Switch
                                unCheckedChildren="showSearch"
                                checkedChildren="showSearch"
                                checked={showSearch}
                                onChange={this.triggerShowSearch}
                                style={{ marginTop: 16 }}
                            /> */}
                        </div>
                    </div>
                </div>
            </Modal>
        )
    }
}
export default Form.create()(addModal)