import React, { Component } from 'react'
import { Form, Input, Select, Tabs, Row, Col, message, Button } from 'antd'
import BreadcrumbLink from '../../../components/bread'
import './index.less'
import Api from '../../../api/index';
import { searchToParams } from '../../../utils/utils.js'
const { Option } = Select;
const { TextArea } = Input;
const { TabPane } = Tabs

const App = Form.create({ name: 'form_in_modal' })(
    class extends Component {
        constructor(props) {
            super(props)
            const { bizId } = searchToParams();
            this.state = {
                bizId,
                selectByPrimaryKeyData: {},
                pageListUserData: [],//审核人/审批人
                listFileTypesData: [],//文件类别
                listAllApplyTypesData: [],//申请类别
                listApplyDeptInFileModelData: [],//编写部门下拉
                listQMSDeptData: [],//申请部门/年审部门
                writtenData: [],//编写人
                listAllApplyReasonsData: [],//修改原因
                breadcrumbData: [
                    { name: '文件管理' },
                    { name: '申请审核' },
                    { name: '审核' }
                ],
            }
        }
        componentDidMount() {
            this.selectByPrimaryKey()//详情
            this.pageListUser()//审核人/审批人
            this.listFileTypes()//文件类别下拉
            this.listAllApplyTypes()//申请类别
            this.listApplyDeptInFileModel()//编写部门下拉
            this.listQMSDept()//申请部门/年审部门
            this.listUsers()//编写人下拉
            this.listAllApplyReasons()//修改原因
        }

        //详情
        selectByPrimaryKey = () => {
            const { bizId } = this.state
            Api.selectByPrimaryKey({
                bizId: bizId
            }, (res) => {
                if (res.code === "200") {
                    this.setState({
                        selectByPrimaryKeyData: res.data,
                    })
                }
            })
        }
        //审核人/审批人
        pageListUser = () => {
            Api.pageListUser({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        pageListUserData: res.data.list,
                    })
                }
            })
        }
        //文件类别下拉
        listFileTypes = () => {
            Api.listFileTypes({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listFileTypesData: res.data.list,
                    })
                }
            })
        }
        //申请类别
        listAllApplyTypes = () => {
            Api.listAllApplyTypes({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listAllApplyTypesData: res.data.list,
                    })
                }
            })
        }
        //编写部门下拉
        listApplyDeptInFileModel = () => {
            Api.listApplyDeptInFileModel({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listApplyDeptInFileModelData: res.data.list
                    })
                }
            })
        }
        //申请部门/年审部门
        listQMSDept = () => {
            Api.listQMSDept({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listQMSDeptData: res.data,
                    })
                }
            })
        }
        //编写人
        listUsers = () => {
            Api.listUsers({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        writtenData: res.data.list,
                    })
                }
            })
        }
        //修改原因
        listAllApplyReasons = () => {
            Api.listAllApplyReasons({}, (res) => {
                if (res.code === "200") {
                    this.setState({
                        listAllApplyReasonsData: res.data.list,
                    })
                }
            })
        }
        //点击 返回
        handleSearchPeople = () => {
            this.props.history.push('/fileManagement/applicationAudit')
        }
        //点击提交
        handleReset = () => {
            this.props.form.validateFieldsAndScroll((err, values) => {
                console.log(values, 'HHHHHHHHHHHHHHHHHHHHHH')
                if (true) {
                    Api.agreeOrDisThisApply({
                        bizId: this.state.selectByPrimaryKeyData.bizId,
                        approverContent: values.approverContent == undefined ? values.approverContent : values.approverContent.trim(),
                        fileNo: values.fileNo == undefined ? values.fileNo : values.fileNo.trim(),
                        fileVersion: values.fileVersion == undefined ? values.fileVersion : values.fileVersion.trim(),
                        fileName: values.fileName == undefined ? values.fileName : values.fileName.trim(),
                        effectDate: values.effectDate == undefined ? values.effectDate : values.effectDate.trim(),
                        auditUserId: values.auditUserId,
                        fileTypeId: values.fileTypeId,
                        approverUserId: values.approverUserId,
                        applyType: values.applyType,
                        applyReasonId: values.applyReasonId == undefined ? values.applyReasonId : values.applyReasonId.trim(),
                        applyDeptId: values.applyDeptId,
                        applyDate: values.applyDate == undefined ? values.applyDate : values.applyDate.trim(),
                        fileId: this.state.selectByPrimaryKeyData.fileId,
                        // auditorName: values.auditorName == undefined ? values.auditorName : values.auditorName.trim(),
                        // receiveDeptId: values.receiveDeptId,
                        // roleId: values.roleId,
                        // applyYear: values.applyYear,
                    }, (res) => {
                        if (res.code === '200') {
                            message.success(res.message)
                            this.props.history.push('/fileManagement/applicationAudit')
                        }
                    })
                }
            });
        }
        render() {
            const {
                form,
            } = this.props;
            const {
                breadcrumbData,
                selectByPrimaryKeyData,
                pageListUserData,
                listFileTypesData,
                listAllApplyTypesData,
                listApplyDeptInFileModelData,
                listQMSDeptData,
                writtenData,
                listAllApplyReasonsData,
            } = this.state;

            console.log(selectByPrimaryKeyData, "0000000000")
            //审核人/审批人
            const pageListUser = pageListUserData ? pageListUserData.map(res => {
                return (<Option key={res.bizId} values={res.bizId}>{res.dspName}</Option>)
            }) : null
            //文件类别
            const listFileTypes = listFileTypesData ? listFileTypesData.map(res => {
                return (<Option key={res.fileTypeId} values={res.fileTypeId}>{res.fileTypeIdName}</Option>)
            }) : null
            //申请类别
            const listAllApplyTypes = listAllApplyTypesData ? listAllApplyTypesData.map(res => {
                return (<Option key={res.applyTypeId} values={res.applyTypeId}>{res.applyTypeName}</Option>)
            }) : null
            //编写部门
            const listApplyDeptInFileModel = listApplyDeptInFileModelData ? listApplyDeptInFileModelData.map(res => {
                return (<Option key={res.applyDeptId} values={res.applyDeptId}>{res.applyDeptName}</Option>)
            }) : null
            //申请部门/年审部门
            const listQMSDept = listQMSDeptData ? listQMSDeptData.map(res => {
                return (<Option key={res.bizId} values={res.bizId}>{res.deptName}</Option>)
            }) : null
            //编写人
            const writtenBy = writtenData ? writtenData.map(res => {
                return (<Option key={res.userId} values={res.userId}>{res.userName}</Option>)
            }) : null
            //修改原因
            const listAllApply = listAllApplyReasonsData ? listAllApplyReasonsData.map(res => {
                return (<Option key={res.applyReasonId} values={res.applyReasonId}>{res.applyReasonName}</Option>)
            }) : null
            const { getFieldDecorator } = form;
            const formItemLayout = {
                labelCol: { span: 6 },
                wrapperCol: { span: 18 },
            };

            return (
                <div className="examane">
                    <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                    {/* 一定要加p-content-wrapper */}
                    <div className='p-content-wrapper'>
                        <Form style={{ position: 'relative', top: '30px' }}>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="审核意见"
                                    >
                                        {getFieldDecorator('approverContent', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件编号"
                                    >
                                        {getFieldDecorator('fileNo', {
                                            initialValue: selectByPrimaryKeyData.fileNo
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="版本/修订号"
                                    >
                                        {getFieldDecorator('fileVersion', {
                                            initialValue: selectByPrimaryKeyData.fileVersion
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="编写人"
                                    >
                                        {getFieldDecorator('applyUserName', {
                                            initialValue: selectByPrimaryKeyData.applyUserName
                                        })(
                                            <Select
                                                disabled
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {writtenBy}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件名称"
                                    >
                                        {getFieldDecorator('fileName', {
                                            initialValue: selectByPrimaryKeyData.fileName
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="生效日期"
                                    >
                                        {getFieldDecorator('effectDate', {
                                            initialValue: selectByPrimaryKeyData.effectDate
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="审核人"
                                    >
                                        {getFieldDecorator('auditUserId', {
                                            initialValue: selectByPrimaryKeyData.auditUserName
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {pageListUser}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="文件类别"
                                    >
                                        {getFieldDecorator('fileTypeId', {
                                            initialValue: (selectByPrimaryKeyData.length > 0) ? { key: selectByPrimaryKeyData.fileTypeId, label: selectByPrimaryKeyData.fileTypeName } : undefined,
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listFileTypes}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="编写部门"
                                    >
                                        {getFieldDecorator('applyDeptId', {
                                            initialValue: (selectByPrimaryKeyData && selectByPrimaryKeyData.length > 0 && selectByPrimaryKeyData.applyDeptName) ? {
                                                label: selectByPrimaryKeyData.applyDeptName,
                                                key: selectByPrimaryKeyData.applyDeptId
                                            } : undefined
                                            // initialValue: (selectByPrimaryKeyData.length > 0) ? { key: selectByPrimaryKeyData.applyDeptId, label: selectByPrimaryKeyData.applyDeptName } : undefined,
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listApplyDeptInFileModel}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="审批人"
                                    >
                                        {getFieldDecorator('approverUserId', {
                                            initialValue: selectByPrimaryKeyData.approverUserName
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {pageListUser}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="申请类别"
                                    >
                                        {getFieldDecorator('applyType', {
                                            initialValue: selectByPrimaryKeyData.applyTypeName
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listAllApplyTypes}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="修改原因"
                                    >
                                        {getFieldDecorator('applyReasonId', {
                                            initialValue: selectByPrimaryKeyData.applyReasonName
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listAllApply}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={8}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="申请部门"
                                    >
                                        {getFieldDecorator('makeDeptName', {
                                            initialValue: selectByPrimaryKeyData.makeDeptName
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                onFocus={this.onFocus}
                                                onBlur={this.onBlur}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listQMSDept}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="申请日期"
                                    >
                                        {getFieldDecorator('applyDate', {
                                            initialValue: selectByPrimaryKeyData.applyDate
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="修改内容"
                                    >
                                        {getFieldDecorator('planName', {
                                        })(
                                            <TextArea placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={6} offset={1}>
                                    <Button onClick={this.handleReset} style={{}}>提交</Button>
                                    <Button type="primary" htmlType="submit" onClick={this.handleSearchPeople} style={{ marginLeft: 20, }}>返回</Button>
                                </Col>
                            </Row>
                        </Form>
                    </div>
                </div>
            );
        }
    }
);
export default App