import React,{ Component } from 'react'
import { withRouter } from 'react-router-dom';
// import {
//   } from 'antd';
import './index.less'

// import workBenchUrl from '../../assets/images/workBench.png';
import BreadcrumbLink from '../../components/bread'
import Api from '../../api/index'
import {getQueryStringByName} from '../../utils/utils'

class WorkBench extends Component {
	constructor(props){
		super(props)
		this.state = {
			breadcrumbData: [
				{ name: '工作台' }
            ]
		}
	}
	componentDidMount(){
		var _isLogin = window.sessionStorage.getItem('isLogin');
		if(!_isLogin){	
			let _code = getQueryStringByName('code');
			if(_code){
				Api.login({
					"authCode": getQueryStringByName('code'),
				},(res) => {
					if(res.code === '200'){
						window.sessionStorage.setItem("isLogin", true);
					}else{
						this.props.history.push('/login');
					}
					
				})
			}
		}
		
		
	}
	render(){
		const {breadcrumbData} = this.state;
		return (
		<div>
			<BreadcrumbLink breadcrumbData={breadcrumbData} breadClass={'p-workbench-bread'}></BreadcrumbLink>
			<div className='p-content-wrapper'>
				<div className='p-workbench-wrapper'>
					<div className='p-workbench'>
						{/* <img src={workBenchUrl} alt='工作台' className='p-workbench-img'/> */}
						<div style={{color: '#333', fontSize: '40px',paddingTop: '20px'}}>文档管理工作台</div>
						<div style={{color: '#666', fontSize: '24px'}}>此次欢迎回来，祝您工作顺利</div>
					</div>
				</div>
			</div>
			
		</div>
		)
	}
}


export default withRouter(WorkBench)