import React, { Component } from 'react'
import './index.less'
import BreadcrumbLink from '../../../components/bread';
import TableManage from './tableManage';
import TableHost from './tableHotbackup';
import SetHost from './set-host'
import { message } from 'antd';

class mySQLManagement extends Component {
    apis = window.apis;
    constructor(props) {
        super(props)
        this.state = {
            breadcrumbData: [
                { name: '系统管理' },
                { name: '数据库管理' }
            ],
            tabsClick: '1',
            tableData: [],
            total: 0,
            pageNo: 1,
            pageSize: 10,
            loading: false,
            setHostVisible: false, // 自动备份弹框
        }
    }
    componentDidMount() {
      this.queryTableList();
    }
    // tabs点击事件
    handleTabsClick = (val) => {
        this.setState({
            tabsClick: val,
            tableData: [],
            total: 0,
            pageNo: 1,
            pageSize: 10,
        },() => {
            if(val === '1') {
                this.queryTableList();
            }else{
                this.queryHostList();
            }
        })
    }
    // 查询数据库列表
    queryTableList = (pageNo=1,pageSize=10) =>{
        this.setState({
            loading: true
        },() => {
            const params = {
                pageNo,
                pageSize
            }
            this.apis.showUsageOfTable(params,(res) => {
                if (res.code === '200') {
                    this.setState({
                        loading: false,
                        tableData: res.data.list,
                        total: res.data.total,
                        pageNo: res.data.pageNum,
                        pageSize: res.data.pageSize
                    })
                }else{
                    this.setState({
                        loading: false
                    })
                }
            })
        })
    }
    // 查询备份列表
    queryHostList = (pageNo=1,pageSize=10) =>{
        this.setState({
            loading: true
        },() => {
            const params = {
                pageNo,
                pageSize
            }
            this.apis.pageListBackUpInfo(params,(res) => {
                if (res.code === '200') {
                    this.setState({
                        loading: false,
                        tableData: res.data.list,
                        total: res.data.total,
                        pageNo: res.data.pageNum,
                        pageSize: res.data.pageSize
                    })
                }else{
                    this.setState({
                        loading: false
                    })
                }
            })
        })
    }
    // 刷新
    handleUpdate = () => {
        this.handleTabsClick('1');
    }
    // 数据库备份
    handleExportSql = () => {
        this.apis.exportSql({},(res) => {
            if(res.code === '200') {
                message.success('备份成功！');
                this.queryHostList();
            }
        })
    }
    // 自动备份弹框
    handleSetHost = () =>{
        this.setState({
            setHostVisible: true
        })
    }
    // 自动备份弹框 -- 取消弹框
    handleHostCancel = () => {
        this.setState({
            setHostVisible: false
        },() => {
            this.refs.SetHost.resetFields();
        })
    }
    // 自动备份弹框 -- 确定
    handleaddsetHost = (values) => {
        console.log(values)
        this.apis.timingBackUpDataBase(values,(res) => {
            if(res.code === '200') {
                message.success('设置成功！');
                this.handleHostCancel();
            }
        })
    }
    // 删除备份
    handleDelete = (bizId) => {
        this.apis.deleteSqlBackUp({bizId},(res) => {
            if(res.code === '200') {
                message.success('删除成功！');
                this.queryHostList(this.state.pageNo,this.state.pageSize);
            }
        })
    }
    render() {
        const { breadcrumbData, tabsClick, tableData, loading, setHostVisible } = this.state;
        return (
            <div className="mySQLManagement">
                <BreadcrumbLink breadcrumbData={breadcrumbData}>
                    <div className="breadcrumbLink-left">
                        <div className={tabsClick === '1' ? 'tabs-item blue-bottom' : 'tabs-item'} onClick={this.handleTabsClick.bind(this,'1')}>数据库管理</div>
                        <div className={tabsClick === '2' ? 'tabs-item blue-bottom' : 'tabs-item'} onClick={this.handleTabsClick.bind(this,'2')}>数据库热备份</div>
                    </div>
                </BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    {
                        tabsClick === '1'
                        ? (
                            <TableManage
                                loading={loading}
                                data={tableData}
                                handleChangePage={this.queryTableList}
                            />
                        )
                        : <TableHost
                                loading={loading}
                                data={tableData}
                                handleChangePage={this.queryHostList}
                                handleUpdate={this.handleUpdate}
                                handleExportSql={this.handleExportSql}
                                handleSetHost={this.handleSetHost}
                                handleDelete={this.handleDelete}
                            />
                    }
                    
                </div>
                {/* 自动备份 */}
                <SetHost
                    ref="SetHost"
                    setHostVisible={setHostVisible}
                    handleHostCancel={this.handleHostCancel}
                    handleaddsetHost={this.handleaddsetHost}
                />
            </div>
        )
    }
}
export default mySQLManagement