import React, { Component } from 'react'
import { Icon, Table, Checkbox, message, Popconfirm } from 'antd';
import './index.less'
import BreadcrumbLink from '../../../components/bread';
import CategoryModal from "./categoryModal";
import QMDetailModal from './QMDetailModal';

class dictionaryManagement extends Component {
    apis = window.apis;
    constructor(props) {
        super(props)
        this.state = {
            breadcrumbData: [
                { name: '系统管理' },
                { name: '字典管理' }
            ],
            loading: false,
            detailLoading: false,
            dataQMList: [],         // 字典类型列表
            dataQMDetailList: [],   // 字典详情列表
            QMDetail: {},           // 字典类型行内容
            pageNo: 1,
            pageSize: 10,
            total: 0,
            leftData: [],
            rightData: [],
            checkedCode: '',
            categoryModal: false, // 新增类别弹框显隐
            detailModal: false,    // 新增详情弹框显隐
            detailList: {},       // 详情行内容
        }
        this.columns = [{
            title: '字典类别',
            dataIndex: 'fullName',
            width: 200,
        },{
            title: '分类',
            dataIndex: 'category',
            width: 130
        },{
            title: '',
            width: 120,
            dataIndex: 'bizId',
            render: (text, record) =>
                <div>
                    {
                        text === this.state.checkedCode && record.parentId !== '0'
                        ? (
                        <div className="iconList">
                            <Icon type="edit" onClick={this.handleQMEdit.bind(this,record)}/>
                            <Popconfirm title="确定要删除该数据吗" onConfirm={this.handleQMDelete.bind(this,text)}>
                                <Icon type="delete"/>
                            </Popconfirm>
                            <Icon type="right" />
                        </div>
                        )
                        : ''
                    }
                </div>
        }]
        this.leftColumns = [{
            title: '代码（值）',
            dataIndex: 'itemCode',
            width: 150,
        },{
            title: '字典名称',
            dataIndex: 'itemName',
            width: 150,
        },{
            title: '有效',
            dataIndex: 'enabled',
            width: 70,
            render: (text, record) => <Checkbox checked={text}></Checkbox>
        },{
            title: '允许编辑',
            dataIndex: 'allowEdit',
            width: 100,
            render: (text, record) => <Checkbox checked={text}></Checkbox>
        },{
            title: '允许删除',
            dataIndex: 'allowDelete',
            width: 100,
            render: (text, record) => <Checkbox checked={text}></Checkbox>
        },{
            title: '描述',
            dataIndex: 'description',
        },{
            title: '操作',
            dataIndex: 'bizId',
            width: 100,
            render: (text, record) =>
            <div className="iconList">
                <Icon type="edit" onClick={this.handleDetailEdit.bind(this,record)} />
                {/* <Icon type="delete" onClick={this.handleDetailDetail(text)} /> */}
                <Popconfirm title="确定要删除该数据吗" onConfirm={this.handleDetailDetail.bind(this,text)}>
                    <Icon type="delete"/>
                </Popconfirm>
            </div>
        }]
    }
    componentDidMount() {
        this.queryQMList();
    }
    // 字典类别--数据查询
    queryQMList = () => {
        this.setState({
            loading: true
        },() => {
            this.apis.listQMSItems({}, (res) => {
                if(res.code === '200') {
                    const list = res.data;
                    this.setState({
                        dataQMList: list,
                        loading: false,
                    },() => {
                        if (list[0].list && list[0].list[0]) {
                            this.handleLeftRow(list[0].list[0]);
                        }
                    })
                }else{
                    this.setState({
                        loading: false,
                    })
                }
            })
        })
    }
    // 查询右侧字典详情
    queryQMSItemDetails = (pageNo=1 , pageSize=10) => {
        const params = {
            itemsId: this.state.checkedCode,
            pageNo,
            pageSize
        }
        this.setState({
            detailLoading: true
        },() => {
            this.apis.pageListQMSItemDetails(params,(res) => {
                if(res.code === '200') {
                    if (pageNo > 1 && res.data.list.length === 0) {
                        this.queryQMSItemDetails(res.data.pages,res.data.pageSize);
                    }else{
                        this.setState({
                            dataQMDetailList: res.data.list,
                            total: res.data.total,
                            detailLoading: false,
                            pageNo: res.data.pageNum,
                            pageSize: res.data.pageSize
                        })
                    }
                }else{
                    this.setState({
                        detailLoading: false,
                    })
                }
            })
        })
    }
    // 左侧表格点击更新右侧表格
    handleLeftRow = (record) => {
        if(record.bizId !== this.state.checkedCode) {
            this.setState({
                checkedCode: record.bizId
            },() => {
                this.queryQMSItemDetails();
            })
        }
    }
    rowClassName = (record) => {
        if (record.bizId === this.state.checkedCode) {
            return 'blueText'
        }else {
            return ''
        }
    }
    // 新增类别弹框显隐
    handleCategory = () => {
        this.setState({
            categoryModal: true,
            type: 0,
            QMDetail: {}
        })
    }
    // 编辑类别弹框显隐
    handleQMEdit = (item) => {
        console.log('----');
        this.setState({
            categoryModal: true,
            type: 1,
            QMDetail: item
        })
    }
    // 关闭类别弹框
    handleQmCancle = () => {
        this.setState({
            categoryModal: false
        },() => {
            this.refs.CategoryModal.resetFields();
        })
    }
    // 类别弹框确认
    handleaddsetMeal = (values) => {
        console.log(values);
        const params = {
            ...values
        }
        delete params['type'];
        if (values.type === 0) { // 新增
            this.apis.addQMSItems(params,(res) => {
                if(res.code === '200') {
                    message.success('新增成功！');
                    this.handleQmCancle();
                    this.queryQMList();
                }
            })
        }else{ // 修改
            params['bizId'] = this.state.checkedCode;
            this.apis.updateQMSItems(params,(res) => {
                if(res.code === '200') {
                    message.success('修改成功！');
                    this.handleQmCancle();
                    this.queryQMList();
                }
            })
        }
    }
    // 类别删除
    handleQMDelete = (bizId) => {
        this.apis.deleteQMSItems({bizId},(res) => {
            if(res.code === '200') {
                message.success('删除成功！');
                this.queryQMList();
            }
        })
    }
    // 字典详情新增
    handleDetailModel = () => {
        this.setState({
            detailModal: true,
            type: 0,
            detailList: {}
        })
    }
    // 字典详情--关闭弹框
    handleDetailEdit = (item) => {
        this.setState({
            detailModal: true,
            type: 1,
            detailList: item
        })
    }
    // 关闭字典详情弹框
    handleQmDetailCancle = () => {
        this.setState({
            detailModal: false
        },() => {
            this.refs.QMDetailModal.resetFields();
        })
    }
    // 字典详情--删除
    handleDetailDetail = (bizId) => {
        this.apis.deleteQMSItemDetails({bizId},(res) => {
            if(res.code === '200') {
                message.success('删除成功！');
                this.queryQMSItemDetails();
            }
        })
    }
    // 详情弹框确认
    handleaddsetDetailMeal = (values) => {
        console.log(values);
        const params = {
            ...values
        }
        delete params['type'];
        params['itemsId'] = this.state.checkedCode;
        if (values.type === 0) { // 新增
            this.apis.addQMSItemDetails(params,(res) => {
                if(res.code === '200') {
                    message.success('新增成功！');
                    this.handleQmDetailCancle();
                    this.queryQMSItemDetails();
                }
            })
        }else{ // 修改
            params['bizId'] = this.state.detailList.bizId;
            this.apis.updateQMSItemDetails(params,(res) => {
                if(res.code === '200') {
                    message.success('修改成功！');
                    this.handleQmDetailCancle();
                    this.queryQMSItemDetails();
                }
            })
        }
    }
    // 分页
    handleChangePage = (pageNo, pageSize) => {
        this.queryQMSItemDetails(pageNo, pageSize);
    }
    onShowSizeChange = (pageNo, pageSize) => {
        this.queryQMSItemDetails(1, pageSize);
    }
    render() {
        const { breadcrumbData, categoryModal, dataQMList, dataQMDetailList, 
                QMDetail, type, total, pageSize, pageNo, detailModal, detailList } = this.state;
        return (
            <div className="dictionaryManagement">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    {/* 左边 */}
                    <div className="left-table">
                        <p className="add-btn">
                            <Icon type="plus-square" />
                            <span onClick={this.handleCategory}>新增类别</span>
                        </p>
                        <div className="table-wrap">
                            <Table
                                rowKey={(record) => { return record.bizId }}
                                onRow={(record) => {
                                    return {
                                        onClick: () => this.handleLeftRow(record), // 点击行
                                    };
                                }}
                                childrenColumnName='list'
                                defaultExpandedRowKeys={['611919992795041792']}
                                rowClassName={(record) => this.rowClassName(record)}
                                columns={this.columns} 
                                dataSource={dataQMList}
                                pagination={false}
                            />
                        </div>
                    </div>
                    <div className="right-table">
                        <p className="add-btn">
                            <Icon type="plus-square" />
                            <span onClick={this.handleDetailModel}>新增字典</span>
                        </p>
                        <div className="table-wrap">
                            <Table
                                rowKey={(record) => { return record.bizId }}
                                columns={this.leftColumns} 
                                dataSource={dataQMDetailList}
                                pagination={{
                                    hideOnSinglePage: true,
                                    showSizeChanger: true,
                                    showQuickJumper: true,
                                    current: pageNo,
                                    total: total,
                                    pageSize: pageSize,
                                    onChange: this.handleChangePage,
                                    onShowSizeChange: this.onShowSizeChange,
                                }}
                            />
                        </div> 
                    </div>
                </div>
                {/* 新增类别弹框 */}
                <CategoryModal 
                    ref="CategoryModal"
                    type={type}
                    showModal={categoryModal}
                    QMDetail={QMDetail}
                    handleCancel={this.handleQmCancle}
                    handleaddsetMeal={this.handleaddsetMeal}
                />
                {/* 字典详情弹框 */}
                <QMDetailModal
                    ref="QMDetailModal"
                    type={type}
                    showModal={detailModal}
                    QMDetail={detailList}
                    handleCancel={this.handleQmDetailCancle}
                    handleaddsetMeal={this.handleaddsetDetailMeal}
                />
            </div>
        )
    }
}
export default dictionaryManagement