import React from 'react';
import { Route, Switch, Router } from 'react-router-dom';
import ledgerRoute from './fileManagement'
import systemRoute from './system'
import managementRoute from './systemManagement'
import itemBankRoute from './itemBankManagement'
import testRoute from './testManagement'
import Home from '../pages/home'
import WorkBench from '../pages/workBench'
import App from '../App'
import NotFound from '../pages/404'
import NoPermission from '../pages/noPermission'
import history from '../utils/history';

const RouterP = () => {
	return (
		<Router history={history}>
			<div>
				<Switch>
					{/* <Route exact path='/index' component={Home}></Route> */}
					<Route exact path='/' component={Home}></Route>
					<Route exact path='/login' component={Home}></Route>
					<Route exact path='/noPermission' component={NoPermission}></Route>
					<Route path='/workBench' exact
						render={
							() => {
								return (
									<App>
										<Route component={WorkBench} ></Route>
									</App>
								)
							}
						}>
					</Route>
					<Route path='/fileManagement'
						render={
							() => {
								return (
									<App>
										<Switch>
											{
												ledgerRoute.map(res => {
													return <Route exact path={res.link} key={res.link} component={res.component} />
												})

											}
											<Route component={NotFound}></Route>
										</Switch>
									</App>
								)
							}
						}
					/>
					<Route path='/system'
						render={
							() => {
								return (
									<App>
										<Switch>
											{
												systemRoute.map(res => {
													return <Route exact path={res.link} key={res.link} component={res.component} />
												})
											}
											<Route component={NotFound}></Route>
										</Switch>
									</App>
								)
							}
						}
					/>
					<Route path='/systemManagement'
						render={
							() => {
								return (
									<App>
										<Switch>
											{
												managementRoute.map(res => {
													return <Route exact path={res.link} key={res.link} component={res.component} />
												})

											}
											<Route component={NotFound}></Route>
										</Switch>
									</App>
								)
							}
						}
					/>
					<Route path='/itemBankManagement'
						render={
							() => {
								return (
									<App>
										<Switch>
											{
												itemBankRoute.map(res => {
													return <Route exact path={res.link} key={res.link} component={res.component} />
												})

											}
											<Route component={NotFound}></Route>
										</Switch>
									</App>
								)
							}
						}
					/>
					<Route path='/testManagement'
						render={
							() => {
								return (
									<App>
										<Switch>
											{
												testRoute.map(res => {
													return <Route exact path={res.link} key={res.link} component={res.component} />
												})

											}
											<Route component={NotFound}></Route>
										</Switch>
									</App>
								)
							}
						}
					/>
					{/* <Route component={NotFound}></Route> */}
					<Route component={Home}></Route>
				</Switch>
			</div>

		</Router>
	)
}
export default RouterP