import React, { Component } from 'react'
import { Form, Input, Table, Row, Button, message, Col, DatePicker, Card, Select } from 'antd';
import {
    HashRouter as Router, Route, Switch, Link, withRouter,
} from 'react-router-dom';
import './index.less'
// import '../../../assets/css/common.less'
import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread'
import echarts from 'echarts/lib/echarts'
import 'echarts/lib/chart/bar'
import axios from 'axios'
import { searchToParams } from '../../../utils/utils.js'

const { Option } = Select;
const Search = Input.Search;
const { RangePicker } = DatePicker
class onBanner extends Component {
    constructor(props) {
        super(props)
        const { examId } = searchToParams();
        this.state = {
            msg: "",
            previewData: [],//预览详情
            examId,
        }
    }
    componentDidMount() {
        this.listExamQuestionForPreview()
    }
    //点击退出
    handleLoginOut = () => {
        this.props.history.push('/testManagement/testArrange')
    }
    listExamQuestionForPreview = () => {
        const { examId } = this.state
        Api.listExamQuestionForPreview({
            examId: examId
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    previewData: res.data
                })
            }
        })
    }
    render() {
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 16 },
        };
        const { getFieldDecorator } = this.props.form;
        const {
            data,
            previewData,
        } = this.state;
        let { bool, days, hours, minutes, seconds } = this.state

        const topic = previewData ? previewData.map((val, i) => {
            console.log(val, val.questionForPreview, '000000000000')
            return (
            <div>
             <div className="examTitle">
                <div style={{ marginLeft: '5px' }}>
                    <span>{val.questionTypeName} (5分/题)</span>
                </div>
             </div>

             {
                val.questionForPreview.map((values,index) => {
                    console.log(values,index, '66666')
                     return             <Card title={<div>第 { Number(index + 1) }题：{values.questionTitle}</div>} style={{ width: 300, marginTop: '50px' }}>
                     <p>A:{values.questionA}</p>
                     <p>B:{values.questionB}</p>
                     <p>C:{values.questionC}</p>
                     <p>D:{values.questionD}</p>
                     <p>E:{values.questionE}</p>
                     <p>F:{values.questionF}</p>
                  </Card>
                 })
        
             }
            </div>
                // val.questionForPreview.map((values) => {
                //     // console.log(values, '1111111111111111')
                //     <Card title={<div>第一题：这里是题目内容</div>} style={{ width: 300, marginTop: '50px' }}>
                //         <p>A:{values.questionA}</p>
                //         <p>B:{values.questionB}</p>
                //         <p>C:{values.questionC}</p>
                //         <p>D:{values.questionD}</p>
                //         <p>E:{values.questionE}</p>
                //     </Card>
                // })
            )
        }) : ''

        const breadcrumbData =
            [
                { name: '培训管理' },
                { name: '我的培训' },
                { name: '查看' }
            ]

        return (
            <div className="startTheExam">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="information">
                        <div style={{ marginTop: "5px" }}>
                            <span>试卷名称：</span><span>这里是试卷名称</span>
                        </div>
                        <div style={{ marginTop: "5px" }}>
                            <span>90 分钟</span>
                            <span style={{ marginLeft: '20px' }}>总分 100 分</span>
                            <span style={{ marginLeft: '20px' }}>合格 60 分</span>
                        </div>
                    </div>
                    {topic}
                    <div style={{ position: "relative", top: "20px" }}>
                        <Button style={{ float: 'right' }} onClick={this.handleLoginOut}>退出</Button>
                    </div>
                </div>
            </div>
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner