import React, { Component } from 'react'
import echarts from 'echarts' //必须
import 'echarts/lib/component/title'
import 'echarts/lib/component/tooltip'
import 'echarts/lib/component/legend'
import 'echarts/lib/chart/bar'
import ReactEcharts from 'echarts-for-react'

class Echart extends Component {
    constructor(props) {
        super(props)
        this.state = {
        }
    }
    componentDidMount() {
        echarts.registerTheme('Imooc');
    }
    render() {
        const { planData } = this.props

        const depaterData = planData.map(v => {
            return v.deptName
        })
        const seriesData = planData.map(v => {
            return v.timelyTotal / v.trainTotal > 0 ? v.timelyTotal / v.trainTotal : 0
        })
        console.log(seriesData, '555555')
        const option = {
            title: {
                text: '及时率图表分析',
                subtext: '数据仅供参考（最终以对账报表为准）',
                x: 'center'
            },
            color: ['#4B85FF'],
            width: ['200px'],
            tooltip: {
                trigger: 'axis',
            },
            xAxis: {
                data: depaterData
            },
            yAxis: {
                type: 'value',
                lineStyle: {
                    color: ['#4B85FF'],
                },
            },
            series: [
                {
                    name: '及时率',
                    type: 'bar',   //这块要定义type类型，柱形图是bar,饼图是pie
                    data: seriesData,
                    barGap: '-100%',
                    barCategoryGap: '90%',
                }
            ]
        }
        return (
            <div className={`p-bread-wrapper ${this.props.breadClass ? this.props.breadClass : ''}`}>
                <ReactEcharts option={option} theme="Imooc" style={{ height: '400px' }} />
            </div>
        )
    }
}
export default Echart