import React, { Component } from 'react'
import { Form, Input, Modal, Select, Table, Tabs, Row, Col, message, Upload, Transfer, Button } from 'antd'
import './index.less'
import Api from '../../../api/index';
import BreadcrumbLink from '../../../components/bread'
import { searchToParams } from '../../../utils/utils.js'
const { Option } = Select;
const { TextArea } = Input;
const { TabPane } = Tabs

const App = Form.create({ name: 'form_in_modal' })(
    class extends Component {
        constructor(props) {
            super(props)
            const { bizId } = searchToParams();
            this.state = {
                fileList: [],
                bizId,
                rightData: [],
                leftData: [],
                checkColumns: [],
                selectedKeys: [],
                pageListLogInfoData: [],//流程列表
                Columns: [],
                pageListFile: {
                    pageSize: 10,
                    page: 1
                },
                pageListFileData: [],//文件附件列表
                selectByPrimaryKeyData: [],//基本信息
                breadcrumbData: [
                    { name: '文件管理' },
                    { name: '文件报批' },
                    { name: '审批' }
                ],
            }
        }
        componentDidMount() {
            this.setState({
                checkColumns: [{
                    title: '文件编号',
                    key: 'fileNo',
                    width: '200px',
                    render: (text, record, index) => {
                        if (record.fileNo) {
                            return (
                                <div className='ellipsis'>{record.fileNo}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '文件名称',
                    key: 'fileName',
                    render: (text, record, index) => {
                        if (record.fileName) {
                            return (
                                <div className='ellipsis'>{record.fileName}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '版本',
                    key: 'fileVersion',
                    render: (text, record, index) => {
                        if (record.fileVersion) {
                            return (
                                <div className='ellipsis'>{record.fileVersion}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '到达时间',
                    key: 'beginDate',
                    render: (text, record, index) => {
                        if (record.beginDate) {
                            return (
                                <div className='ellipsis'>{record.applyDeptName}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '完成时间',
                    key: 'finshedDate',
                    render: (text, record, index) => {
                        if (record.finshedDate) {
                            return (
                                <div className='ellipsis'>{record.finshedDate}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '处理人',
                    key: 'approveUserName',
                    render: (text, record, index) => {
                        if (record.approveUserName) {
                            return (
                                <div className='ellipsis'>{record.approveUserName}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '结果',
                    key: 'result',
                    render: (text, record, index) => {
                        if (record.result === 1) {
                            return (
                                <div className='ellipsis'>提交</div>
                            )
                        } if (record.result === 2) {
                            return (
                                <div className='ellipsis'>待处理</div>
                            )
                        }if (record.result === 1) {
                            return (
                                <div className='ellipsis'>拒绝</div>
                            )
                        }if (record.result === 1) {
                            return (
                                <div className='ellipsis'>ok</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '意见',
                    key: 'remark',
                    render: (text, record, index) => {
                        if (record.remark) {
                            return (
                                <div className='ellipsis'>{record.remark}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                },
                ]
            })
            this.selectByPrimaryKey()//基本信息
            this.pageListFileAttach()//文件附件列表
            this.pageListLogInfo()//流程记录
        }
        //基本信息
        selectByPrimaryKey = () => {
            const { bizId } = this.state
            Api.selectByPrimaryKey({
                bizId: bizId,
            }, (res) => {
                if (res.code === '200') {
                    this.setState({
                        selectByPrimaryKeyData: res.data
                    })
                }
            })
        }
        //文件附件列表
        pageListFileAttach = () => {
            const { bizId } = this.state
            Api.pageListFileAttach({
                bizId: bizId
                // pageSize: pageListFile.pageSize,
                // pageNo: pageListFile.page,
            }, (res) => {
                if (res.code === "200") {
                    this.setState({
                        pageListFileData: res.data
                    })
                }
            })
        }
        //流程记录
        pageListLogInfo = () => {
            const { bizId } = this.state
            Api.pageListLogInfo({
                fileId: bizId
            }, (res) => {
                if ( res.code === "200"){
                    this.setState({
                        pageListLogInfoData: res.data.list
                    })
                }
            })
        }
        callback = (key) => {

        }
        // 文件上传
        handleChange = (info) => {
            //把fileList拿出来
            let { fileList } = info;
            const status = info.file.status;
            if (status) {
                if (status === 'done') {
                    fileList = fileList.slice(-1);
                    if (fileList[0].response.code === '200') {
                        this.setState({
                            current: 1
                        }, () => {
                            this.setState({
                                leadingVisible: true,
                                dataFileList: fileList[0].response.data
                            })
                            this.getManagementTable();
                        })
                    } else if (fileList[0].response.code === '500') {
                        this.setState({
                            current: 1
                        }, () => {
                            this.setState({
                                leadingVisible: true,
                                dataFileList: fileList[0].response.data
                            })
                            this.getManagementTable();
                        })
                    } else if (fileList[0].response.code === '40001') {
                        message.warning(fileList[0].response.message);
                    }
                } else if (status === 'error') {
                    message.error(`${info.file.name} 上传失败`);
                }
                //重新设置state
                this.setState({ fileList });
            }
        }
        //导入
        beforeUpload = (file) => {
            let nameArr = file.name.split('.');
            let name = nameArr[nameArr.length - 1];
            // 校验 格式
            if (['xlsx', 'xls'].indexOf(name) === -1) {
                return;
            }
            const isLt = file.size / 1024 / 1024 < 100;
            if (!isLt) {
                message.error('文件过大');
            }
            return isLt;
        }
        handleChange = (nextTargetKeys, direction, moveKeys) => {
            const { rightDataName } = this.props;
            let _obj = {};
            _obj[rightDataName] = nextTargetKeys
            this.props.setData(_obj);
        };
        handleSelectChange = (sourceSelectedKeys, targetSelectedKeys) => {
            this.setState({ selectedKeys: [...sourceSelectedKeys, ...targetSelectedKeys] });
        };
        //点击退回
        handleSendBack = () => {
            this.props.history.push('/fileManagement/documentApproval')
        }
        render() {
            const annexColumns = [{
                title: '文件名称',
                key: 'attachmentName',
                width: '200px',
                render: (text, record, index) => {
                    if (record.attachmentName) {
                        return (
                            <div className='ellipsis'>{record.attachmentName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '格式',
                key: 'attachmentFormat',
                render: (text, record, index) => {
                    if (record.attachmentFormat) {
                        return (
                            <div className='ellipsis'>{record.attachmentFormat}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '上传时间',
                key: 'upLoadTime',
                render: (text, record, index) => {
                    if (record.upLoadTime) {
                        return (
                            <div className='ellipsis'>{record.upLoadTime}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            },]

            const Columns = [{
                title: '文件编号',
                key: 'fileNo',
                width: '200px',
                render: (text, record, index) => {
                    if (record.fileNo) {
                        return (
                            <div className='ellipsis'>{record.fileNo}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '文件名称',
                key: 'fileName',
                render: (text, record, index) => {
                    if (record.fileName) {
                        return (
                            <div className='ellipsis'>{record.fileName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '版本',
                key: 'fileVersion',
                render: (text, record, index) => {
                    if (record.fileVersion) {
                        return (
                            <div className='ellipsis'>{record.fileVersion}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '到达时间',
                key: 'beginDate',
                render: (text, record, index) => {
                    if (record.beginDate) {
                        return (
                            <div className='ellipsis'>{record.beginDate}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '完成时间',
                key: 'finshedDate',
                render: (text, record, index) => {
                    if (record.finshedDate) {
                        return (
                            <div className='ellipsis'>{record.finshedDate}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '处理人',
                key: 'approveUserName',
                render: (text, record, index) => {
                    if (record.approveUserName) {
                        return (
                            <div className='ellipsis'>{record.approveUserName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '结果',
                key: 'remark',
                render: (text, record, index) => {
                    if (record.result === 1) {
                        return (
                            <div className='ellipsis'>提交</div>
                        )
                    } if (record.result === 2) {
                        return (
                            <div className='ellipsis'>待处理</div>
                        )
                    }if (record.result === 1) {
                        return (
                            <div className='ellipsis'>拒绝</div>
                        )
                    }if (record.result === 1) {
                        return (
                            <div className='ellipsis'>ok</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '意见',
                key: 'fileStatusName',
                render: (text, record, index) => {
                    if (record.fileStatusName) {
                        return (
                            <div className='ellipsis'>{record.fileStatusName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }]
            const {
                form
            } = this.props;
            const {
                leftData,
                rightData,
                selectedKeys,
                breadcrumbData,
                selectByPrimaryKeyData,
                checkColumns,
                pageListFileData,
                pageListLogInfoData
            } = this.state;
            const { getFieldDecorator } = form;
            const formItemLayout = {
                labelCol: { span: 6 },
                wrapperCol: { span: 14 },
            };
            const uploadProps = {
                action: `/item/importCheckItem`,
                onChange: this.handleChange,
                multiple: false,
                showUploadList: false,
                beforeUpload: this.beforeUpload,
                headers: {
                    menuUrl: window.location.pathname
                }
            };

            return (
                <div className="wordForm">
                    <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                    {/* 一定要加p-content-wrapper */}
                    <div className='p-content-wrapper'>
                        <Form>
                            <Tabs defaultActiveKey="1" onChange={this.callback}>
                                <TabPane tab="基本信息" key="1">
                                    <div className="process-fundamental" style={{ position: 'relative', left: "-15px" }}>
                                        <div className="details-name"><div className="dataDetails-name"><span className="dataDetails">文件编号:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.fileNo}</div><div className="details-node" ><span style={{ float: 'right' }}>文件名称:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.fileName}</div><div className="details-node"><span style={{ float: 'right' }}>文件类别:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.fileTypeName}</div></div>
                                        <div className="details-name"><div className="dataDetails-name"><span className="dataDetails">版本号:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.fileVersion}</div><div className="details-node"><span style={{ float: 'right' }}>申请类别:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.applyTypeName}</div><div className="details-node"><span style={{ float: 'right' }}>审核人:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.approverUserName}</div></div>
                                        <div className="details-name"><div className="dataDetails-name"><span className="dataDetails">申请日期:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.applyDate}</div><div className="details-node"><span style={{ float: 'right' }}>页码:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.officePageSie}</div><div className="details-node"><span style={{ float: 'right' }}>申请部门:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.applyDeptName}</div></div>
                                        <div className="details-name"><div className="dataDetails-name"><span className="dataDetails">申请人:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.applyUserName}</div><div className="details-node"><span style={{ float: 'right' }}>申请原因:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.applyReasonName}</div><div className="details-node"><span style={{ float: 'right' }}>审批人:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.auditorName}</div></div>
                                        <div className="details-name"><div className="dataDetails-name"><span className="dataDetails">编写依据:</span></div><div style={{ float: 'left', width: '80%' }}>{selectByPrimaryKeyData.fileInfo}</div></div>
                                        <div className="details-name"><div className="dataDetails-name"><span className="dataDetails">是否年审:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.isAnnualVerification === true ? "是" : "否"}</div><div className="details-node"><span style={{ float: 'right' }}>年审部门:</span></div><div style={{ float: 'left', width: '18%' }}>{selectByPrimaryKeyData.annualVerificationDeptName}</div></div>
                                    </div>
                                </TabPane>
                                {/* <TabPane tab="文件内容" key="2">
                                </TabPane> */}
                                <TabPane tab="附件" key="2">
                                    <div className="addSkip">
                                        <Table
                                            columns={annexColumns}
                                            dataSource={pageListFileData}
                                            // pagination={pagination}
                                            key="bizId"
                                        ></Table>
                                        {/* <Upload {...uploadProps} fileList={this.state.fileList}>
                                            {<a style={{ marginRight: 20 }}>
                                                上传</a>}
                                        </Upload> */}
                                    </div>
                                </TabPane>
                                <TabPane tab="流程" key="3">
                                    <div style={{ paddingLeft: 33 }}>
                                        <Table
                                            columns={checkColumns}
                                            dataSource={pageListLogInfoData}
                                            // pagination={pagination}
                                            key="bizId"
                                        ></Table>
                                        {/* <Transfer
                                            ref={(transfer) => { this.transfer = transfer }}
                                            dataSource={leftData}
                                            // titles={[`未关联${title}`, `已关联${title}`]}
                                            targetKeys={rightData}
                                            selectedKeys={selectedKeys}
                                            onChange={this.handleChange}
                                            onSelectChange={this.handleSelectChange}
                                            render={item => item.title}
                                            // disabled={disabled}
                                            listStyle={{
                                                width: 270,
                                                height: 320,
                                            }}
                                            loading={true}
                                            showSearch
                                            filterOption={this.filterOption}
                                        /> */}
                                    </div>
                                </TabPane>
                                <TabPane tab="操作记录" key="4">
                                    <Table
                                        columns={Columns}
                                        dataSource={pageListLogInfoData}
                                        // pagination={pagination}
                                        key="bizId"
                                    ></Table>
                                </TabPane>
                            </Tabs>
                            <Col style={{ marginTop: "30px" }}>
                                <Button type="primary">通过审批</Button>
                                <Button style={{ marginLeft: 20, }}>委托审批</Button>
                                <Button style={{ marginLeft: 20, }}>退回</Button>
                                <Button style={{ marginLeft: 20, }}>保存</Button>
                                <Button type="primary" style={{ marginLeft: 20, }}>部门授权</Button>
                                <Button type="primary" style={{ marginLeft: 20, }}>角色授权</Button>
                                <Button onClick={this.handleSendBack} style={{ marginLeft: 20, }}>退出</Button>
                            </Col>
                        </Form>
                    </div>
                </div>
            );
        }
    }
);
export default App