import React, { Component } from 'react'
import { Form, Input, Modal, Select, Table } from 'antd'
import './index.less'
import Api from '../../../api/index';
const { Option } = Select;
const { TextArea } = Input;
const App = Form.create({ name: 'form_in_modal' })(
    class extends Component {
        constructor(props) {
            super(props)
            this.state = {
                reviseColumns: [],
                reviseNotesData: [],
            }
        }
        componentDidMount() {
        }
        render() {
            const {
                visible, onCancel, onCreate, form, pageListLogInfoData
            } = this.props;
            const {
                reviseNotesData
            } = this.state;
            const { getFieldDecorator } = form;
            const formItemLayout = {
                labelCol: { span: 6 },
                wrapperCol: { span: 18 },
            };

            const reviseColumns = [
                {
                    title: '文件编号',
                    key: 'fileNo',
                    dataIndex: 'fileNo',
                    width: '200px',
                    render: (text, record, index) => {
                        if (record.fileNo) {
                            return (
                                <div className='ellipsis'>{record.fileNo}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '文件名称',
                    key: 'fileName',
                    dataIndex: 'fileName',
                    render: (text, record, index) => {
                        if (record.fileName) {
                            return (
                                <div className='ellipsis'>{record.fileName}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '版本',
                    key: 'fileVersion',
                    dataIndex: 'fileVersion',
                    render: (text, record, index) => {
                        if (record.fileVersion) {
                            return (
                                <div className='ellipsis'>{record.fileVersion}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '操作时间',
                    key: 'finshedDate',
                    dataIndex: 'finshedDate',
                    render: (text, record, index) => {
                        if (record.finshedDate) {
                            return (
                                <div className='ellipsis'>{record.finshedDate}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '操作人',
                    key: 'approveUserName',
                    dataIndex: 'approveUserName',
                    render: (text, record, index) => {
                        if (record.approveUserName) {
                            return (
                                <div className='ellipsis'>{record.approveUserName}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '动作',
                    key: 'result',
                    dataIndex: 'result',
                    render: (text, record, index) => {
                        if (record.result) {
                            return (
                                <div className='ellipsis'>{record.result}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '结果',
                    key: 'jieguo',
                    dataIndex: 'jieguo',
                    render: (text, record, index) => {
                        if (record.jieguo) {
                            return (
                                <div className='ellipsis'>{record.jieguo}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }, {
                    title: '意见',
                    key: 'remark',
                    dataIndex: 'remark',
                    render: (text, record, index) => {
                        if (record.remark) {
                            return (
                                <div className='ellipsis'>{record.remark}</div>
                            )
                        } else {
                            return (
                                <div>-</div>
                            )
                        }
                    },
                }
            ]

            return (
                <Modal
                    visible={visible}
                    title={'流程记录'}
                    okText="确定"
                    cancelText='取消'
                    onCancel={onCancel}
                    onOk={onCreate}
                    maskClosable={false}
                >
                    <div>
                        <Form>
                            <Table
                                // style={{ marginTop: '-12px', marginLeft: '20px' }}
                                columns={reviseColumns}
                                dataSource={pageListLogInfoData}
                                key="ccc"
                            ></Table>
                        </Form>
                    </div>
                </Modal>
            );
        }
    }
);
export default App