import React, { Component } from 'react'
import { Form, Input, Table, Row, Button, message, Col, DatePicker, Card, Select } from 'antd';
import {
    HashRouter as Router, Route, Switch, Link, withRouter,
} from 'react-router-dom';
import './index.less'
// import '../../../assets/css/common.less'
import Api from '../../api/index'
import BreadcrumbLink from '../../components/bread'
import echarts from 'echarts/lib/echarts'
import 'echarts/lib/chart/bar'
import axios from 'axios'
import { searchToParams } from '../../utils/utils.js'

const { Option } = Select;
const Search = Input.Search;
const { RangePicker } = DatePicker;
class onBanner extends Component {
    constructor(props) {
        super(props)
        const { isTest, isLook, isTestTwo } = searchToParams();
        this.state = {
            msg: "",
            isTest,
            isLook,
            lookData: [],
            data: {
                monDataList: [
                    {
                        questionA: "A、这是选项A",
                        questionB: "B、这是选项B",
                        questionC: "C、这是选项C",
                        questionD: "D、这是选项D",
                        questionE: "E、这是选项E",
                        myAnsow: "A",
                        myType: "2",
                    },
                    {
                        questionA: "A、啊啊啊",
                        questionB: "B、所发生的v",
                        questionC: "C、割让给",
                        questionD: "D、过热vg",
                        questionE: "E、热狗热狗",
                        myAnsow: "C",
                        myType: "0",
                    }
                ]
            },
        }
    }
    componentDidMount() {
        this.timeTransition(20000);//根据接口返回的时间
        this.viewMyTrain()
    }
    timeTransition = (ms) => {
        let maxtime = ms / 1000; //按秒计算
        let timer = null;
        let _this = this;
        setTimeout(function f() {
            if (maxtime >= 0) {
                let minutes = Math.floor(maxtime / 60);
                let seconds = Math.floor(maxtime % 60);
                minutes < 10 ? minutes = '0' + minutes : minutes = minutes;
                seconds < 10 ? seconds = '0' + seconds : seconds = seconds;
                let msg = "请在 <em>" + minutes + "分" + seconds + "秒</em> 内完成支付";
                _this.setState({
                    msg
                });
                --maxtime;
            } else {
                _this.setState({
                    msg: '订单已过期，请重新下单'
                });
                clearTimeout(timer);
                return;
            }
            timer = setTimeout(f, 1000);
        }, 1000);
    }
    //点击退出
    handleLoginOut = () => {
        this.props.history.push('/system/myTraining')
    }
    //点击 确认交卷
    handleConfirmation = () => {
        this.props.history.push('/system/myTraining')
    }
    viewMyTrain = () => {
        Api.viewMyTrain({
            userId: this.props.location.state.query.userId
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    lookData: res.data.list
                })
            }
        })
    }
    render() {
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 16 },
        };
        const { getFieldDecorator } = this.props.form;
        const {
            data,
            lookData,
            isTest, isLook,
        } = this.state;
        console.log(lookData, "7777777777")
        let { bool, days, hours, minutes, seconds } = this.state
        const topic =lookData.map((val, i) => {
            return <Card title={<div>第一题：这里是题目内容</div>} style={{ width: 300, marginTop: '50px' }}>
                <p>{val.questionA}</p>
                <p>{val.questionB}</p>
                <p>{val.questionC}</p>
                <p>{val.questionD}</p>
                <p>{val.questionE}</p>
                {
                    isTest === "true" ?
                        <div>我的答案：
                            <Select
                                showSearch
                                allowClear
                                style={{ width: "80px" }}
                                placeholder="请选择"
                                optionFilterProp="children"
                                onChange={this.onChange}
                                // onFocus={onFocus}
                                // onBlur={onBlur}
                                // onSearch={onSearch}
                                filterOption={(input, option) =>
                                    option.props.children.toLowerCase().indexOf(input.toLowerCase()) >= 0
                                }
                            >
                                <Option value="jack">A</Option>
                                <Option value="lucy">B</Option>
                                <Option value="Yiminghe">C</Option>
                                <Option value="Yiminghe">D</Option>
                                <Option value="Yiminghe">E</Option>
                            </Select>
                        </div>
                        :
                        null
                }
                {
                    isLook === "true" ?
                        <div>
                            <span>我的答案：{val.myAnsow}</span>
                            <span style={{ marginLeft: '30px', }}>得分：{val.myType}</span>
                        </div>
                        :
                        null
                }
            </Card>
        })

        const breadcrumbData = isTest === "true" ?
            [
                { name: '培训管理' },
                { name: '我的培训' },
                { name: '考试' }
            ]
            :
            isLook === "true" ?
                [
                    { name: '培训管理' },
                    { name: '我的培训' },
                    { name: '查看' }
                ]
                :
                null

        return (
            <div className="startTheExam">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="information">
                        {
                            isTest === "true" ?
                                <div style={{ float: "right" }}>
                                    <span>剩余时间：</span>
                                </div>
                                :
                                null
                        }
                        <div>
                            <span>考试人员：</span><span>詹小翾</span>
                        </div>
                        <div style={{ marginTop: "5px" }}>
                            <span>试卷名称：</span><span>这里是试卷名称</span>
                        </div>
                        <div style={{ marginTop: "5px" }}>
                            <span>90 分钟</span>
                            <span style={{ marginLeft: '20px' }}>总分 100 分</span>
                            <span style={{ marginLeft: '20px' }}>合格 60 分</span>
                        </div>
                    </div>
                    <div className="examTitle">
                        <div style={{ marginLeft: '5px' }}>
                            <span>单选题 (2分/题)</span>
                        </div>
                    </div>
                    {topic}
                    <div style={{ position: "relative", top: "20px" }}>
                        <Button style={{ float: 'right' }} onClick={this.handleLoginOut}>退出</Button>
                        {
                            isTest === "true" ?
                                <Button style={{ float: 'right', marginRight: '20px' }} type="primary" onClick={this.handleConfirmation}>确认交卷</Button>
                                :
                                null
                        }
                    </div>
                </div>
            </div>
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner