import React, { Component } from 'react'
import { Form, Input, Modal, Select, Table, Row, Col } from 'antd'
import './index.less'
import Api from '../../../api/index';
const { Option } = Select;
const { TextArea } = Input;
const App = Form.create({ name: 'form_in_modal' })(
    class extends Component {
        constructor(props) {
            super(props)
            this.state = {
            }
        }
        componentDidMount() {
            
        }
        render() {
            const {
                visible, onCancel, onCreate, form, isEdit,
                listDetailsOne, listDetailsTwo, listDetailsThree, reviseData
            } = this.props;
            console.log(reviseData, "9999999")
            const {
                reviseNotesData
            } = this.state;
            const { getFieldDecorator } = form;
            const formItemLayout = {
                labelCol: { span: 9 },
                wrapperCol: { span: 15 },
            };

            const _title = isEdit ? "编辑" : "新增"
            return (
                <Modal
                    visible={visible}
                    title={_title}
                    okText="确定"
                    cancelText='取消'
                    onCancel={onCancel}
                    onOk={onCreate}
                    maskClosable={false}
                >
                    <div>
                        <Form>
                            <Row>
                                <Col span={8}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="试题名称"
                                    >
                                        {getFieldDecorator('questionTitle', {
                                            initialValue: isEdit ? reviseData.questionTitle : undefined,
                                        })(
                                            <Input style={{ width: '408px' }} placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="培训类别"
                                    >
                                        {getFieldDecorator('examTypeId', {
                                            initialValue: isEdit ? reviseData.examTypeId : undefined,
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listDetailsOne}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="题库类别"
                                    >
                                        {getFieldDecorator('questionsBankTypeId', {
                                            // initialValue: isEdit ? { key: reviseData.questionsBankTypeId , label: reviseData.itemUnit } : undefined,
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listDetailsTwo}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="试题类别"
                                    >
                                        {getFieldDecorator('questionsTypeId', {
                                            // initialValue: isEdit ? { key: reviseData.questionsTypeId } : undefined,
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listDetailsThree}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={8}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="A"
                                    >
                                        {getFieldDecorator('questionA', {
                                            initialValue: isEdit ? reviseData.questionA : undefined,
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={8}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="B"
                                    >
                                        {getFieldDecorator('questionB', {
                                            initialValue: isEdit ? reviseData.questionB : undefined,
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={8}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="C"
                                    >
                                        {getFieldDecorator('questionC', {
                                            initialValue: isEdit ? reviseData.questionC : undefined,
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={8}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="D"
                                    >
                                        {getFieldDecorator('questionD', {
                                            initialValue: isEdit ? reviseData.questionD : undefined,
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={8}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="E"
                                    >
                                        {getFieldDecorator('questionE', {
                                            initialValue: isEdit ? reviseData.questionE : undefined,
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={8}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="F"
                                    >
                                        {getFieldDecorator('questionF', {
                                            initialValue: isEdit ? reviseData.questionF : undefined,
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={8}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="答案"
                                    >
                                        {getFieldDecorator('answer', {
                                            initialValue: isEdit ? reviseData.answer : undefined,
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                        </Form>
                    </div>
                </Modal>
            );
        }
    }
);
export default App