import React,{Component} from 'react';
import './index.less'
import { Modal, Row, Col, Form, Input, Checkbox, TreeSelect } from "antd";
const FormItem = Form.Item;
const { TreeNode } = TreeSelect;

class setJobModal extends Component{
    constructor(){
        super();
        this.deptName = ''
        this.checkedList = [{
            line: 1,
            childrenList: [{
                title: '查询',
                dataIndex: 'allowQuery',
            },{
                title: '删除',
                dataIndex: 'allowDelete',
            }]
        },{
            line: 2,
            childrenList: [{
                title: '阅读',
                dataIndex: 'allowRead',
            },{
                title: '编辑',
                dataIndex: 'allowEdit',
            }]
        },{
            line: 3,
            childrenList: [{
                title: '启用',
                dataIndex: 'allowEnable',
            },{
                title: '锁定',
                dataIndex: 'allowFreeze',
            }]
        }]
    }
    // 点击确定
    handleaddsetMeal = (e) => {
        e.preventDefault();
        const { form, type } = this.props;
        form.validateFields((err, values) => {
            if (!err) {
                values.type = type;
                values.deptName = this.deptName;
                this.props.handleaddsetMeal(values)
            }
        })
    }
    // 点击取消
    handleCancel = () => {
        this.props.handleCancel();
    }
    // treeSelect onChange
    onChange = (value, label, extra) => {
        this.deptName = label[0];
        console.log(value, label, extra);
    }
    // 渲染树结构
    renderTreeNodes = (data) => {
        const treeItem = data.map(item => {
            if (item.list && item.list.length) {
                return <TreeNode value={item.bizId} title={item.deptName} key={item.bizId} dataRef={item}>
                    {this.renderTreeNodes(item.list)}
                </TreeNode>
            }
            return <TreeNode value={item.bizId} title={item.deptName} key={item.bizId} />;
        })
        return treeItem
    }
    render(){
        const { setJobModalShow, form, type, jobDetail, deptData } = this.props;
        const { getFieldDecorator } = form;
        return(
            <Modal
                className="setJobModal"
                title={type === 0 ? '新增职位' : '修改职位'}
                visible={setJobModalShow}
                width={500}
                onCancel={this.handleCancel}
                onOk={this.handleaddsetMeal}
            >
                <div className="setRole-box">
                    <Form labelCol={{ span: 7 }} wrapperCol={{ span: 12 }}>
                        <FormItem 
                            label="职位名称">
                            {getFieldDecorator('jobName', {
                                initialValue: jobDetail.jobName,
                                rules: [
                                    { required: true, message: '请输入职位名称' },
                                ],
                            })(
                                <Input placeholder="请输入" />
                            )}
                        </FormItem>
                        <FormItem 
                            label="部门">
                            {getFieldDecorator('deptId', {
                                initialValue: jobDetail.deptId,
                                rules: [
                                    { required: true, message: '请选择部门' },
                                ],
                            })(
                                <TreeSelect placeholder="请选择" allowClear treeDefaultExpandAll onChange={this.onChange}>
                                    {this.renderTreeNodes(deptData)}
                                </TreeSelect>
                            )}
                        </FormItem>
                        {
                            this.checkedList.map((item,index) => {
                                return (<Row gutter={24} key={index}>
                                    {
                                        item.childrenList.map((li,idx) => {
                                            return (
                                                <Col push={3} span={10} key={idx}>
                                                    <FormItem label={li.title}>
                                                        {getFieldDecorator(li.dataIndex, {
                                                            valuePropName: 'checked',
                                                            initialValue: jobDetail[li.dataIndex] ? true : false
                                                        })(
                                                            <Checkbox>{li.title}</Checkbox>
                                                        )}
                                                    </FormItem>
                                                </Col>
                                            )
                                        })
                                    }
                                </Row>)
                            })
                        }
                        <FormItem 
                            label="排序">
                            {getFieldDecorator('sortCode', {
                                initialValue: jobDetail.sortCode,
                                rules: [
                                    { required: true, message: '请输入排序' },
                                    { pattern: /^\d{1,10}$/, message: '请输入一至十位数字' }
                                ],
                            })(
                                <Input placeholder="请输入" />
                            )}
                        </FormItem>
                    </Form>
                </div>
            </Modal>
        )
    }
}
export default Form.create()(setJobModal)