import React, { Component } from 'react'
import { withRouter } from 'react-router-dom';
import {
	Form, Input, Button, message, Icon, Checkbox, Radio
} from 'antd';
import './index.less'
import md5 from 'md5';
import Api from '../../api/index'
import logoUrl from '../../assets/images/login.png';
// import '../../utils/dingding';

class LoginForm extends Component {
	constructor(props) {
		super(props)
		this.state = {
			ddddd: [],
			...this.initState()
		}
	}

	//点击登陆
	handleSubmit = (e) => {
		console.log(e,"11111111111")
		//验证码
		const ddddd = this.state.data.map((v, i) =>
			<span
				style={{ position: 'relative', bottom: '5px', fontWeight: '700' }}
				key={i}
				className='itemStr'
				onMouseEnter={() => this.setState({ refresh: true })}
			>
				{String.fromCharCode(v > 57 && v < 84 ? v + 7 : (v < 57 ? v : v + 13))}
			</span>
		)
		const aaaaa = ddddd.map((v, i) =>
			v.props.children
		)
		const captchaa = aaaaa[0] + aaaaa[1] + aaaaa[2] + aaaaa[3]

		e.preventDefault();
		this.props.form.validateFields((err, values) => {
			console.log(values,'22222222222222')
			if (values.captcha === undefined) {
				return
			} if (values.captcha === "") {
				return
			} if (captchaa === values.captcha) {
				if (!err) {
					Api.login({
						account: values.account,
						passWord: values.passWord,
						keepLogin: this.state.keepLogin
					}, (res) => {
						if (res.code === '200') {
							window.sessionStorage.setItem("isLogin", true);
							this.props.history.push('/workBench');
						} else {
							message.warning(res.message)
						}

					})
				}
			} else {
				message.warning("请输入正确的验证码")
			}

		});
	}

	initState() {
		return {
			data: this.getRandom(109, 48, 4),
			rotate: this.getRandom(75, -75, 4),
			fz: this.getRandom(8, 20, 4),
			color: [this.getRandom(100, 255, 3), this.getRandom(100, 255, 4), this.getRandom(100, 255, 3), this.getRandom(100, 255, 3)]
		}
	}
	getRandom(max, min, num) {
		const asciiNum = ~~(Math.random() * (max - min + 1) + min)
		if (!Boolean(num)) {
			return asciiNum
		}
		const arr = []
		for (let i = 0; i < num; i++) {
			arr.push(this.getRandom(max, min))
		}
		return arr
	}

	handleLogin = (e) => {
		console.log(e, e.target.checked, '33333333333')
		this.setState({
			keepLogin: e.target.checked,
		})
	}
	render() {
		const { getFieldDecorator } = this.props.form;

		const {
		} = this.state
		return (
			<div className='p-main'>
				<div className="p-landing">
					<div className="p-login">
						<div className="p-loginPicture"><img src={logoUrl}/><span style={{ fontSize: '19px', marginLeft: '10px', color: '#FFFFFF', position: 'relative', top: '3px'}}>杭州禾诺信息技术有限公司</span></div>
						<h1>实验室质量管理系统</h1>
						<h3>欢迎回来</h3>
					</div>
					<div className="p-ashore">
						<div className="disembark">
							<h2>登陆</h2>
							<Form onSubmit={this.handleSubmit} className="login-form">

								<Form.Item>
									{getFieldDecorator('account', {
										rules: [{ required: true, message: '请输入您的用户名!' }],
									})(
										<Input
											prefix={<Icon type="user" style={{ color: 'rgba(0,0,0,.25)' }} />}
											placeholder="请输入您的用户名"
										/>
									)}
								</Form.Item>

								<Form.Item style={{ marginTop: '30px' }}>
									{getFieldDecorator('passWord', {
										rules: [{ required: true, message: '请输入您的密码!' }],
									})(
										<Input
											prefix={<Icon type="lock" style={{ color: 'rgba(0,0,0,.25)' }} />}
											type="passWord"
											placeholder="请输入您的密码"
										/>
									)}
								</Form.Item>

								<Form.Item style={{ marginTop: '30px' }}>
									{getFieldDecorator("captcha", {
										rules: [{ required: true, message: "请输入验证码!" }],
									})(
										<div>
											<Input
												style={{ width: '245px' }}
												prefix={<Icon type="safety" style={{ color: 'rgba(0,0,0,.25)' }} />}
												type="safety-certifica"
												placeholder="请输入验证码"
											/>
											<span style={{ fontSize: '8px', marginLeft: '5px', color: '#535B6C', position: 'relative', top: '-8px', float: 'right', }}>(请注意大小写!)</span>
											<div style={{ width: '65px', height: '30px', float: 'right', position: 'relative', top: '-3px' }}>
												{this.state.data.map((v, i) =>
													<span
														style={{ position: 'relative', bottom: '5px', fontWeight: '700' }}
														key={i}
														className='itemStr'
														onMouseEnter={() => this.setState({ refresh: true })}
													>
														{String.fromCharCode(v > 57 && v < 84 ? v + 7 : (v < 57 ? v : v + 13))}
													</span>
												)}
											</div>
											<span
												style={{ float: 'right', color: '#4897FF', position: 'relative', top: '-18px', right: '35px', lineHeight: '20px' }}
												className='mask'
												onClick={() => {
													this.setState({ ...this.initState(), refresh: false })
													this.canvas()
												}}
												onMouseLeave={() => { this.setState({ refresh: false }) }}
											> 看不清？点击刷新
											   </span>
										</div>
									)}
								</Form.Item>

								<Form.Item>
									{getFieldDecorator('remember', {
										valuePropName: 'checked',
										initialValue: true,
									})(
										<Button style={{ width: '400px', marginTop: "50px" }} type="primary" htmlType="submit" className="login-form-button">
											登陆
                                    </Button>
									)}
									<Checkbox onChange={this.handleLogin}>保持登陆</Checkbox>
									<a onClick={this.forgetPassword} style={{ float: 'right', color: '#999999' }} href="">
										忘记密码？
                                       </a>
								</Form.Item>
							</Form>
						</div>
					</div>
				</div >
			</div >
		)
	}
}

const WrappedNormalLoginForm = Form.create({ name: 'normal_login' })(LoginForm);

export default withRouter(WrappedNormalLoginForm)