import React, { Component } from 'react'
import { Button, Input, Table, Popconfirm, message, DatePicker, Modal, Form, Col, Row, Select } from 'antd';
import './index.less'
import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread';
// import AddFormFromWrap from './addForm';
const { RangePicker } = DatePicker;
const { Option } = Select;
const { TextArea } = Input;
const dateFormat = 'YYYY/MM/DD HH:mm:ss'
// import Table from "./table-list"; // 搜索条件
class onBanner extends Component {
    constructor(props) {
        super(props)
        this.state = {
            breadcrumbData: [
                { name: '考试管理' },
                { name: '考试安排' }
            ],
            pageSize: 10,
            page: 1,
            loading: false,
            dataList: [],
            amendmentVisible: false,
            listExamForPlanData: [], //查询所有试卷
            listQMSDeptData: [], //部门

        }
    }
    componentDidMount() {

        this.queryTableList();
        this.listExamForPlan()//查询所有试卷
        this.listQMSDept() //部门
    }
    // 查询试卷列表
    queryTableList = () => {
        const { pageSize, page } = this.state
        const data = {
            pageSize,
            pageNo: page,
        }
        Api.listExamPlanModelByQuery(data, (res) => {
            if (res.code === "200") {
                this.setState({
                    dataList: res.data.list
                },
                    message.success(res.message)
                )
            } else {
                message.waring(res.message)
            }
        })
    }
    listExamForPlan = () => {
        Api.listExamForPlan({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listExamForPlanData: res.data
                })
            }
        })
    }

    listQMSDept = () => [
        Api.listQMSDept({}, (res) => {
            if (res.code === "200") {
                this.setState({
                    listQMSDeptData: res.data
                })
            }
        })
    ]
    handleSearch = (event) => {
        console.log(event.target.value)
    }
    //新增 打开弹窗
    handleAdd = () => {
        this.setState({
            amendmentVisible: true
        })
    }

    onChange = (e, dateString) => {
        console.log(e, dateString, '555555555555');
        this.setState({
            selectStartTime: dateString
        })
    }
    onChangeTwo = (e, dateString) => {
        console.log(e, dateString, '4444444444');
        this.setState({
            selectEndTime: dateString
        })
    }

    //新增/编辑 弹窗 点击确定
    maintenanceCreate = () => {
        const { isEdit } = this.state
        // if (isEdit === true ) {
        //     this.props.form.validateFieldsAndScroll((err, values) => {
        //         console.log(values,(10).toString(), values.trainDescription, '555555555555')
        //         if (true) {
        //             var num=123123;
        //             const ddd = num.toString()
        //             const { selectStartTime, selectEndTime } = this.state
        //             Api.updateTrain({
        //                 bizId: bizId,
        //                 trainTypeId: values.trainTypeId,
        //                 trainName: values.trainName == undefined ? values.trainName : values.trainName.trim(),
        //                 trainDescription: values.trainDescription == undefined ? values.trainDescription : values.trainDescription.trim(),
        //                 examName: values.examName == undefined ? values.examName : values.examName.trim(),
        //                 examId: values.examId,
        //                 totalPoint: values.totalPoint == undefined ? values.totalPoint : values.totalPoint.trim(),
        //                 fileTypeIdName: values.fileTypeIdName,
        //                 passPoint: values.passPoint == undefined ? values.passPoint : values.passPoint.trim(),
        //                 readFen: values.readFen == undefined ? (values.readFen).toString() : (values.readFen).toString().trim(),
        //                 startDate: values.startDate._i,
        //                 endDate: values.endDate._i,
        //             }, (res) => {
        //                 if (res.code === '200') {
        //                     this.setState({
        //                         amendmentVisible: false,
        //                         isEdit: false,
        //                     },
        //                         message.success("新增成功")
        //                     )
        //                     this.pageListTrains()
        //                 }
        //             })
        //         }
        //     });

        // } else {
        this.props.form.validateFieldsAndScroll((err, values) => {
            console.log(values, '6666666666666666')
            if (true) {
                const { selectStartTime, selectEndTime } = this.state
                // const examDurationA = Number(values.examDuration)
                Api.insertExamPlanModel({
                    examId: values.examId,
                    examPlanName: values.examPlanName == undefined ? values.examPlanName : values.examPlanName.trim(),
                    examDuration: Number(values.examDuration == undefined ? values.examDuration : values.examDuration.trim()),
                    totalPoints: Number(values.totalPoints == undefined ? values.totalPoints : values.totalPoints.trim()),
                    passPoints: Number(values.passPoints == undefined ? values.passPoints : values.passPoints.trim()),
                    startDate: selectStartTime,
                    endDate: selectEndTime,
                    examInfo: values.examInfo == undefined ? values.examInfo : values.examInfo.trim(),
                    examContent: values.examContent == undefined ? values.examContent : values.examContent.trim(),
                    useDeptId: values.bizId,
                }, (res) => {
                    if (res.code === '200') {
                        this.setState({
                            amendmentVisible: false,
                            isEdit: false,
                        },
                            message.success("新增成功")
                        )
                        this.queryTableList()
                    }
                })
            }
        });
        // }
    }
    //新增/编辑 弹窗 点击取消
    maintenanceCancel = () => {
        this.setState({
            amendmentVisible: false,
            isEdit: false,
        })
    }
    //预览
    handlePreview = (record) => {
        this.props.history.push(`/testManagement/testArrange/previewTwo?examId=${record.examId}`)
    }

    //启用
    handleOn = ( record ) => {
        Api.startExamPlanModel({
            examPlanModelId: record.examPlanModelId
        }, (res) => {
            if ( res.code === "200"){
                message.success(res.message)
            }
            this.queryTableList()
        })
    }
    render() {
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 16 },
        };
        const { getFieldDecorator } = this.props.form;

        const { breadcrumbData, dataList, loading, amendmentVisible, listExamForPlanData, listQMSDeptData } = this.state;
        const columns = [
            {
                title: '试卷名称',
                dataIndex: 'examPlanName',
                key: 'examPlanName',
                render: (text, record, index) => {
                    if (record.examPlanName) {
                        return (
                            <div className='ellipsis'>{record.examPlanName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },

            }, {
                title: '使用部门',
                dataIndex: 'deptName',
                key: 'deptName',
                render: (text, record, index) => {
                    if (record.deptName) {
                        return (
                            <div className='ellipsis'>{record.deptName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },

            }, {
                title: '编制者',
                dataIndex: 'createUserName',
                key: 'createUserName',
                render: (text, record, index) => {
                    if (record.createUserName) {
                        return (
                            <div className='ellipsis'>{record.createUserName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },

            }, {
                title: '起始时间',
                dataIndex: 'startDate',
                key: 'startDate',
                render: (text, record, index) => {
                    if (record.startDate) {
                        return (
                            <div className='ellipsis'>{record.startDate}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },

            }, {
                title: '结束时间',
                dataIndex: 'endDate',
                key: 'endDate',
                render: (text, record, index) => {
                    if (record.endDate) {
                        return (
                            <div className='ellipsis'>{record.endDate}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '启用',
                dataIndex: 'status',
                key: 'fileName',
                render: (text, record, index) => {
                    if (record.isStart === false) {
                        return (
                            <div className='ellipsis'>否</div>
                        )
                    } if (record.isStart === true) {
                        return (
                            <div className='ellipsis'>启用</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '操作',
                dataIndex: 'operation',
                width: 250,
                render: (text, record) =>
                    <div>
                        <a className="operation">编辑</a>
                        <a className="operation">人员配置</a>
                        {
                            record.isStart === false ? 
                            <a className="operation" onClick={this.handleOn.bind(this, record)}>启用</a>
                            :
                            null
                        }
                        <Popconfirm title="确定要删除该数据吗">
                            <a className="operation">删除</a>
                        </Popconfirm>
                        <a onClick={this.handlePreview.bind(this, record)} className="operation">预览</a>
                    </div>

            },
        ]

        //查询所有试卷
        const listExamFor = listExamForPlanData ? listExamForPlanData.map(val => {
            return <Option key={val.examId} value={val.examId}>{val.examName}</Option>
        }) : null

        //部门
        const listQMS = listQMSDeptData ? listQMSDeptData.map(val => {
            return <Option key={val.bizId} value={val.bizId}>{val.deptName}</Option>
        }) : null
        return (
            <div className="testArrange">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    {/* 新增或搜索 */}
                    <div className="btn-search">
                        <Button onClick={this.handleAdd} type="primary">新增</Button>
                        <div className="search">
                            <span>试卷安排：</span>
                            <Input placeholder="请输入" onPressEnter={this.handleSearch.bind(this)} />
                        </div>
                    </div>
                    <div className="table-list">
                        <Table
                            columns={columns}
                            dataSource={dataList}
                            // pagination={pagination}
                            key="bizId"
                        ></Table>
                    </div>
                    {/* <AddFormFromWrap
                        visible={amendmentVisible}
                        onCancel={this.amendmentCan}
                        onCreate={this.amendmentNo}
                    >
                    </AddFormFromWrap> */}

                    <Modal
                        visible={amendmentVisible}
                        title={'试卷信息-录入'}
                        okText="确定"
                        cancelText='取消'
                        onCancel={this.maintenanceCancel}
                        onOk={this.maintenanceCreate}
                        maskClosable={false}
                    >
                        <Form style={{ position: 'relative', top: -3 }}>
                            <Row>
                                <Col span={12}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="试卷"
                                    >
                                        {getFieldDecorator('examId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listExamFor}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={12}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="考试时长"
                                    >
                                        {getFieldDecorator('examDuration', {
                                            rules: [
                                                {
                                                    required: true,
                                                    message: `请输入考试时长!`,
                                                    pattern: new RegExp(/^[0-9]*$/),
                                                    message: '只能输入数字'
                                                },
                                            ],
                                        })(
                                            <span>
                                                <Input placeholder="请输入" autoComplete='off' />
                                            </span>
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row style={{ position: 'relative', top: '-10px' }}>
                                <Col span={12}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="考试名称"
                                    >
                                        {getFieldDecorator('examPlanName', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={12}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="使用部门"
                                    >
                                        {getFieldDecorator('bizId', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                {listQMS}
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row style={{ position: 'relative', top: '-10px' }}>
                                <Col span={12}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="总分"
                                    >
                                        {getFieldDecorator('totalPoints', {
                                            rules: [
                                                {
                                                    required: true,
                                                    message: `请输入总分!`,
                                                    pattern: new RegExp(/^[0-9]*$/),
                                                    message: '只能输入数字'
                                                },
                                            ],
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={12}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="及格分数"
                                    >
                                        {getFieldDecorator('passPoints', {
                                            rules: [
                                                {
                                                    required: true,
                                                    message: `请输入及格分数!`,
                                                    pattern: new RegExp(/^[0-9]*$/),
                                                    message: '只能输入数字'
                                                },
                                            ],
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row style={{ position: 'relative', top: '-10px' }}>
                                <Col span={12}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="开始日期"
                                    >
                                        {getFieldDecorator('startDate', {
                                        })(
                                            < DatePicker format={dateFormat} onChange={this.onChange} onClick={() => this.props.GetStates(this.state.dataTime)} />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={12}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="结束日期"
                                    >
                                        {getFieldDecorator('endDate', {
                                        })(
                                            <DatePicker format={dateFormat} onChange={this.onChangeTwo} onClick={() => this.props.GetStates(this.state.dataTime)} />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row style={{ position: 'relative', top: '-10px' }}>
                                <Col span={18}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="考试说明"
                                    >
                                        {getFieldDecorator('examInfo', {
                                        })(
                                            <TextArea placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row style={{ position: 'relative', top: '-10px' }}>
                                <Col span={18}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="考试内容"
                                    >
                                        {getFieldDecorator('examContent', {
                                        })(
                                            <TextArea placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>

                        </Form>
                    </Modal>

                </div>
            </div>
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner