import React, { Component } from 'react'
import './index.less'
import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread';
import { Input, Form, Row, Col, Radio, Table, Button } from 'antd';
import AddModal from "./addModal";
import api from '../../../api';

const FormItem = Form.Item

class addPaper extends Component {
    constructor(props) {
        super(props)
        this.state = {
            type: 0, // 0、新增 1、修改
            addModalShow: false, // 新增或修改弹框的显隐
            questionsType: [],
            paperDetail: {
                checked: true
            },
            breadcrumbData: [
                { name: '考试管理' },
                { name: '考试安排' },
                { name: '新增试卷' }
            ],
            dataSource: [{
                name: '这里是文件名称这里是文件名',
                type: '单选题',
                user: '陈乐乐',
                time: '2019-12-10'
            },{
                name: '这里是文件名称这里是文件名',
                type: '单选题',
                user: '陈乐乐',
                time: '2019-12-10'
            }]
        }
        this.columns=[{
            title: '试卷名称',
            dataIndex: 'name'
        },{
            title: '试题类型',
            dataIndex: 'type'
        },{
            title: '编写人',
            dataIndex: 'user'
        },{
            title: '编写时间',
            dataIndex: 'time'
        }]
    }
    componentDidMount() {
        this.listTypeDetails()
    }
    // 试卷类型的选中状态切换
    handleChangeRadio = () => {
        const { paperDetail } = this.state;
        this.setState({
            paperDetail: { ...paperDetail, checked: !paperDetail.checked}   
        })
    }
    listTypeDetails = () => {
        Api.listTypeDetails({
            itemsId: 5
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    questionsType: res.data.list
                })
            }
        })
    }
    // 打开弹框
    handleAddBtn = () => {
        const arr = this.state.questionsType
        // if (!this.state.paperDetail.checked) {
        //     arr.push({code: 4,name: '问答题'},{code: 5,name: '简答题'});
        // }
        this.setState({
            type: 0,
            addModalShow: true,
            questionsType: arr
        })
    }
    // 关闭弹框
    handleCancel = () => {
        this.setState({
            addModalShow: false
        })
    }
    // 确认弹框
    handleOk = () => {
        this.setState({
            addModalShow: false
        })
    }
    render() {
        const { breadcrumbData, dataSource, type, addModalShow, questionsType, paperDetail} = this.state;
        const { getFieldDecorator } = this.props.form;
        return (
            <div className="addPaper">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="conster-wrap">
                        <p className="title">试卷详情</p>
                        <Form labelCol={{ span: 5 }} wrapperCol={{ span: 16 }}>
                            <Row gutter={24}>
                                <Col span={12}>
                                    <FormItem label="试卷名称">
                                        {getFieldDecorator('examName',{
                                            initialValue: paperDetail.examDuration,
                                            rules: [{ required: true, message: '请输入试卷名称' }],
                                        })(
                                            <Input maxLength={60} placeholder="请输入" autoComplete='off' />
                                        )}
                                    </FormItem>
                                </Col>
                                <Col span={12}>
                                    <FormItem label="试卷类型">
                                        {getFieldDecorator('isPeixun',{
                                            initialValue: paperDetail.isPeixun,
                                        })(
                                            <Radio checked={paperDetail.isPeixun} onClick={this.handleChangeRadio}>勾选为培训试题（培训试题不采用简述题和问答题）</Radio>
                                        )}
                                    </FormItem>
                                </Col>
                            </Row>
                            <Row gutter={24}>
                                <Col span={12}>
                                    <FormItem label="时长">
                                        {getFieldDecorator('examDuration', {
                                            initialValue: paperDetail.examDuration,
                                            rules: [
                                                { required: true, message: '请输入时长' },
                                                { pattern:/^([1-9][0-9]*)$/, message: '时长为正整数!' }
                                            ]
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' suffix="分钟" />
                                        )}
                                    </FormItem>
                                </Col>
                                <Col span={12}>
                                    <FormItem label="总分">
                                        0
                                    </FormItem>
                                </Col>
                            </Row>
                        </Form>
                    </div>
                    <div className="dashed"></div>
                    <div className="conster-wrap">
                        <p className="title lineHeight">试题配置</p>
                        <div className="table-wrap">
                            <div className="table-title">
                                <div className="left-title">
                                    <span className="testType">单选题</span>
                                    <span>分值：2分</span>
                                    <span>题目数：2</span>
                                    <span>合集：2分</span>
                                </div>
                                <div className="right-btn">
                                    <span>修改</span>
                                    <span>删除</span>
                                </div>
                            </div>
                            <Table
                                className="tableClass"
                                dataSource={dataSource}
                                columns={this.columns}
                                pagination={false}
                            />
                        </div>
                        {/* <div className="table-wrap">
                            <div className="table-title">
                                <div className="left-title">
                                    <span className="testType">多选题</span>
                                    <span>分值：2分</span>
                                    <span>题目数：2</span>
                                    <span>合集：2分</span>
                                </div>
                                <div className="right-btn">
                                    <span>修改</span>
                                    <span>删除</span>
                                </div>
                            </div>
                            <Table
                                className="tableClass"
                                dataSource={dataSource}
                                columns={this.columns}
                                pagination={false}
                            />
                        </div> */}
                        <div className="table-wrap-blue" onClick={this.handleAddBtn}>新增试题</div>
                    </div>
                </div>
                <div className="bottom-btn">
                    <Button type="primary">提 交</Button>
                    <Button>退 出</Button>
                </div>
                <AddModal
                    type={type}
                    questionsType={questionsType}
                    addModalShow={addModalShow}
                    handleOk={this.handleOk}
                    handleCancel={this.handleCancel}
                />
            </div>
        )
    }
}
export default Form.create()(addPaper)