import React, { Component } from 'react'
import { Form, Input, Table, Row, Button, message, Col, DatePicker, Select } from 'antd';
import {
    HashRouter as Router, Route, Switch, Link, withRouter,
} from 'react-router-dom';
import './index.less'
// import '../../../assets/css/common.less'
import Api from '../../../api/index'
import BreadcrumbLink from '../../../components/bread'
import api from '../../../api';
const Option = Select
const Search = Input.Search;
const { RangePicker } = DatePicker;
class onBanner extends Component {
    constructor(props) {
        super(props)
        this.state = {
            checkColumns: [],
            amendmentVisible: false,//申请修改
            cancelVisible: false,//申请作废
            reviseVisible: false,//修改
            selectedRowKeys: [],
            planData: [],//列表
            selectTime: [],
            pageSize: 10,
            page: 1,
            breadcrumbData: [
                { name: '培训管理' },
                { name: '培训统计' }
            ],
        }
    }
    componentDidMount() {
        this.setState({
            checkColumns: [{
                title: '姓名',
                key: 'dspName',
                width: '200px',
                render: (text, record, index) => {
                    if (record.dspName) {
                        return (
                            <div className='ellipsis'>{record.dspName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '部门',
                key: 'deptName',
                render: (text, record, index) => {
                    if (record.deptName) {
                        return (
                            <div className='ellipsis'>{record.deptName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '培训名称',
                key: 'trainName',
                render: (text, record, index) => {
                    if (record.trainName) {
                        return (
                            <div className='ellipsis'>{record.trainName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '培训文件',
                key: 'fileName',
                render: (text, record, index) => {
                    if (record.fileName) {
                        return (
                            <div className='ellipsis'>{record.fileName}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '培训测试得分/总分',
                key: 'pointStr',
                render: (text, record, index) => {
                    if (record.pointStr) {
                        return (
                            <div className='ellipsis'>{record.pointStr}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '完成时间',
                key: 'finishTime',
                render: (text, record, index) => {
                    if (record.finishTime) {
                        return (
                            <div className='ellipsis'>{record.finishTime}</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }, {
                title: '是否合格',
                key: 'pass',
                render: (text, record, index) => {
                    if (record.pass === true) {
                        return (
                            <div className='ellipsis'>是</div>
                        )
                    } if (record.pass === false) {
                        return (
                            <div className='ellipsis'>否</div>
                        )
                    } else {
                        return (
                            <div>-</div>
                        )
                    }
                },
            }
            ]
        }, () => {
            this.pageListTrainUser()//初始化列表
        }
        )
    }
    //初始化 列表 数据
    pageListTrainUser() {
        const { pageSize, page } = this.state
        Api.pageListTrainUser({
            pageSize,
            pageNo: page,
        }, (res) => {
            if (res.code === "200") {
                this.setState({
                    planData: res.data.list
                })
                message.success(res.message)
            } else {
                message.warning(res.message)
            }
        })
    }
    //点击查询
    handleSearchPeople = () => {
        this.setState({
            page: 1,
        }, () => {
            this.props.form.validateFieldsAndScroll((err, values) => {
                console.log()
                if (true) {
                    const { pageSize, page } = this.state;
                    this.setState({
                        deviceLoading: true
                    })
                    Api.pageListTrainUser({
                        pageSize,
                        pageNo: page,
                        dspName: values.dspName == undefined ? values.dspName : values.dspName.trim(),
                        deptName: values.deptName == undefined ? values.deptName : values.deptName.trim(),
                        trainName: values.trainName == undefined ? values.trainName : values.trainName.trim(),
                        pass: values.pass,
                    }, (res) => {
                        if (res.code === '200') {
                            this.setState({
                                page,
                                total: res.data.total,
                                planData: res.data.list
                            })
                        }
                    })
                }
            });
        })
    }
    //点击 重置
    handleReset = () => {
        this.props.form.resetFields();
        this.pageListTrainUser()
    }
    onSelectChange = (selectedRowKeys) => {
        this.setState({
            selectedRowKeys
        });
    };
    start = () => {
        setTimeout(() => {
            this.setState({
                selectedRowKeys: [],
            });
        });
    }
    render() {
        const formItemLayout = {
            labelCol: { span: 4 },
            wrapperCol: { span: 16 },
        };
        const { getFieldDecorator } = this.props.form;
        const {
            breadcrumbData,
            planData,
            checkColumns,
            selectedRowKeys
        } = this.state;

        const rowSelection = {
            selectedRowKeys,
            onChange: this.onSelectChange,
        };

        // const pagination = total > 10 ? {
        //     total: this.state.total,
        //     pageSize: this.state.pageSize,
        //     current: this.state.page,
        //     onChange: this.handleTableChange,
        //     showQuickJumper: true,
        //     showSizeChanger,
        //     onShowSizeChange: this.onShowSizeChange,
        // } : false
        return (
            <div className="feedBcak">
                <BreadcrumbLink breadcrumbData={breadcrumbData}></BreadcrumbLink>
                {/* 一定要加p-content-wrapper */}
                <div className='p-content-wrapper'>
                    <div className="p-search">
                        <Form style={{ position: 'relative', top: -3 }}>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="输入查询"
                                    >
                                        {getFieldDecorator('dspName', {
                                        })(
                                            <Input placeholder="请输入用户名或账号" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="部门"
                                    >
                                        {getFieldDecorator('deptName', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="培训名称"
                                    >
                                        {getFieldDecorator('trainName', {
                                        })(
                                            <Input placeholder="请输入" autoComplete='off' />
                                        )}
                                    </Form.Item>
                                </Col>
                            </Row>
                            <Row>
                                <Col span={6}>
                                    <Form.Item
                                        {...formItemLayout}
                                        label="培训结果"
                                    >
                                        {getFieldDecorator('pass', {
                                        })(
                                            <Select
                                                allowClear
                                                placeholder="请选择"
                                                onChange={this.queryEleList}
                                                getPopupContainer={triggerNode => triggerNode.parentNode}
                                            >
                                                <Option value={true}>合格</Option>
                                                <Option value={false}>不合格</Option>
                                            </Select>
                                        )}
                                    </Form.Item>
                                </Col>
                                <Col span={6} offset={0}>
                                    <Button onClick={this.handleReset} style={{ float: 'right' }}>重置</Button>
                                    <Button type="primary" htmlType="submit" onClick={this.handleSearchPeople} style={{ marginRight: 20, float: 'right' }}>查询</Button>
                                </Col>
                            </Row>
                        </Form>
                    </div>
                    <div><Button onClick={this.start} style={{ float: 'right' }}>导出</Button></div>
                    <div style={{ marginTop: '52px' }}>
                        <Table
                            rowSelection={rowSelection}
                            columns={checkColumns}
                            dataSource={planData}
                            // pagination={pagination}
                            key="sadas"
                        ></Table>
                    </div>

                </div>
            </div>
        )
    }
}
const Banner = Form.create()(onBanner);
export default Banner